import i18n from '@/i18n'
import store from '@/store'
import http from './http.js'
import { sortBySortIndex } from './sort.js'

class Necessary {
  constructor (url, data = null, options = {}, cb) {
    this.url = url
    this.data = data
    this.options = { globals: true, login: false, ...options }
    this.cb = cb
    this.source = null
    this.timestamp = -1
  }

  post (force = false) {
    if (force || !this.source) {
      if (!this.source) {
        this.source = http.createSource()
      }
      this.options.cancelToken = this.source.token
      http.post(this.url, this.data, this.options).then(data => {
        this.source = null
        if (this.timestamp === -1 || !this.timestamp || this.timestamp !== data.updateTime) {
          this.timestamp = data.updateTime
          this.cb(data)
        }
      }, err => {
        if (!err.abort) {
          setTimeout(() => this.post(true), 2000)
        } else {
          this.source = null
        }
      })
    }
  }

  abort () {
    if (this.source) {
      this.source.cancel('abort')
      this.source = null
    }
  }
}

const config = new Necessary('/api/common/config', null, {}, cfg => {
  store.commit('config', {
    manageApiServerUrl: cfg.manageApiServerUrl,
    picLoopTime: cfg.picLoopTime,
    sharePic: cfg.defaultPics[2] && cfg.defaultPics[2].url || '',
    maxEschedulerCount: cfg.maxEschedulerCount || 3,
    orderTimeoutLong: cfg.orderTimeoutLong || 15,
    orderTimeList: cfg.orderTimeList || [],
    afterOrderTimeList: cfg.afterOrderTimeList || []
  })
})

const country = new Necessary('/api/common/country/list', null, {}, data => {
  store.commit('countryList', data.countryList)
})

function transformMail (mail, type) {
  return {
    type,
    id: mail.msgId,
    isFresh: mail.isRead === 0,
    origin: mail
  }
}

const userMails = new Necessary('/api/common/user/msg/list', {
  msgType: 1
}, { login: true, key: 'userMails' }, data => {
  store.commit('userMails', data.data.sort(sortBySortIndex).map(mail => transformMail(mail, 1)))
})

const parkMails = new Necessary('/api/common/user/msg/list', {
  msgType: 2
}, { login: true, key: 'parkMails' }, data => {
  store.commit('parkMails', data.data.sort(sortBySortIndex).map(mail => transformMail(mail, 2)))
})

export function initMails (user = true) {
  if (store.getters.isLogined) {
    parkMails.post()
    if (user) {
      userMails.post()
    }
  }
}

export function resetMails () {
  userMails.abort()
  if (userMails.timestamp !== -1) {
    userMails.timestamp = -1
    store.commit(userMails.options.key, [])
  }

  parkMails.abort()
  if (parkMails.timestamp !== -1) {
    parkMails.timestamp = -1
    store.commit(parkMails.options.key, [])
  }

  initMails()
}

export function language () {
  http.post('/api/app/user/lang/change', {
    language: i18n.locale
  }, {
    globals: true,
    login: true
  })
}

export function constant () {
  config.post()
  country.post()
}

export function reset () {
  store.commit('countryList', [])
  country.post()
  language()
  resetMails()
}
