<template>
  <div class="l-page">
    <v-nav :title="$t('common.mail.title')"/>
    <div
      v-if="hasMail"
      class="l-page__content"
    >
      <v-resource
        v-if="hasImage"
        class="o-mail-image"
        :info="mail.origin.msgPicInfo"
      />
      <div class="has-padding--block">
        <div class="is-content--dark" v-html="mail.origin.msgContent"></div>
        <div
          v-if="hasLink"
          class="is-content--blue u-separate--gap"
          @click="linkTo"
        >
          {{ mail.origin.linkInfo.text }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState(['parkMails']),
    mail () {
      let id = Number(this.$route.params.id)
      return id ? this.parkMails.find(mail => mail.id === id) : null
    },
    hasMail () {
      return !!this.mail
    },
    hasImage () {
      return this.hasMail && !!this.mail.origin.msgPicInfo
    },
    hasLink () {
      return this.hasMail && this.mail.origin.linkInfo ? true : false
    }
  },
  methods: {
    linkTo () {
      this.$features.linkTo(this.mail.origin.linkInfo)
    }
  }
}
</script>

<style lang="scss" scoped>
.o-mail-image {
  width: 100%;
  height: 498px;
}
</style>
