<template>
  <div class="c-intro u-wrapper">
    <div class="c-intro__content is-content--dark u-font" :class="{ 'is-show-more': showMore }">
      <div v-html="text"></div>
    </div>
    <div v-if="hasMoreIntro" class="is-sub u-inline-block">
      <span @click="showAllIntro = !showAllIntro">{{ introTip }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      default: ''
    },
    auto: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      introWrapperHeight: 0,
      introHeight: 0,
      showAllIntro: false
    }
  },
  computed: {
    showMore () {
      return !this.auto || !this.hasMoreIntro && this.introWrapperHeight > 0 || this.showAllIntro
    },
    introTip () {
      return this.showAllIntro ? this.$t('explore.detail.sless') : this.$t('explore.detail.smore')
    },
    hasMoreIntro () {
      return this.introWrapperHeight > 0 && this.introHeight > 0 && this.introWrapperHeight < this.introHeight
    }
  },
  mounted () {
    if (!this.auto) {
      return
    }
    this.introWrapperHeight = this.$el.children[0].offsetHeight
    this.introHeight = this.$el.children[0].scrollHeight
  }
}
</script>

<style lang="scss" scoped>
.c-intro {
  &__content {
    @include ellipse-multi(3);

    &.is-show-more {
      -webkit-line-clamp: unset;
    }
  }
}
</style>
