import cfg from 'config'
import axios from 'axios'
import i18n from '@/i18n'
import store from '@/store'
import { log } from '@/utils/log.js'
import { isExpired } from './auth.js'
import { Toast } from 'vant'

axios.defaults.baseURL = cfg.baseURL
axios.defaults.timeout = cfg.timeout

function check (url, options) {
  let {
    auth = true, login = false
  } = options

  if (auth && isExpired()) {
    log(`${ url } -> Auth required, but not`)
    return false
  }

  if (login && !store.getters.isLogined) {
    log(`${ url } -> Login required, but current state is not logined`)
    return false
  }

  return true
}

function getHeader (type, options) {
  let {
    auth = true, token = null
  } = options

  let header = {
    'content-type': type || 'application/json'
  }

  if (auth || token) {
    header['optoken'] = token || store.state.authInfo.token
  }

  return header
}

function getCommonData (options) {
  let {
    login = false
  } = options

  let commonData = {
    pId: 1,
    lang: i18n.locale
  }

  if (login) {
    commonData['userIdType'] = store.state.authInfo.userIdType
    commonData['userId'] = store.state.authInfo.userId
  }

  return commonData
}

function addCommonData (formData, options) {
  let {
    login = false
  } = options

  formData.append('pId', 1)
  formData.append('lang', i18n.locale)

  if (login) {
    formData.append('userIdType', store.state.authInfo.userIdType)
    formData.append('userId', store.state.authInfo.userId)
  }

  return formData
}

function createHttpReject (res) {
  let msg = res.data ? res.data.error || res.data.message : ''
  return {
    system: true,
    code: -9999,
    msg,
    statusCode: res.statusCode
  }
}

function createSystemReject (msg, abort = false) {
  return {
    system: true,
    abort,
    code: -999,
    msg
  }
}

let source = axios.CancelToken.source()
let toastInst = null
export function clear () {
  if (source) {
    source.cancel('abort')
  }
  if (toastInst) {
    toastInst.clear()
    toastInst = null
  }
  source = axios.CancelToken.source()
}

function createPost (url, data, options, flags) {
  return new Promise((resolve, reject) => {
    let {
      loading = false, server = false, globals = false,
      cancelToken = null
    } = flags

    options.cancelToken = cancelToken || !globals && source.token || null

    if (loading === true) {
      toastInst = Toast.loading()
    }

    axios.post(url, data, options).then(res => {
      if (loading === true && toastInst) {
        toastInst.clear()
        toastInst = null
      }
      if (res.status === 200) {
        if (res.data.code === 0) {
          resolve(res.data.data)
        } else {
          log(`${ url } -> ${ res.data.code } -> ${ res.data.msg }`)
          if (server && res.data.msg) {
            Toast(res.data.msg)
          }
          reject(res.data)
        }
      } else {
        log(`${ url } -> status ${ res.statusCode }`)
        reject(createHttpReject(res))
      }
    }, ({ message }) => {
      // code -> null | ECONNABORTED
      log(`${ url } -> ${ message }`)
      let isAbort = /abort/.test(message)
      if (!isAbort && loading === true && toastInst) {
        toastInst.clear()
        toastInst = null
      }
      reject(createSystemReject(message, isAbort))
    })
  })
}

// loading 显示loading图标
// server 显示服务器异常原因（必须有返回值）
// auth 需授权后才能调用
const http = {
  post (url, data = {}, options = {}) {
    if (!check(url, options)) {
      return Promise.reject({
        system: true,
        code: -10000
      })
    }

    return createPost(url, {
      ...getCommonData(options),
      ...data
    }, {
      headers: getHeader('application/json', options)
    }, options)
  },
  postForm (url, formData, options) {
    if (!check(url, options)) {
      return Promise.reject({
        system: true,
        code: -10000
      })
    }

    return createPost(url, addCommonData(formData, options), {
      headers: getHeader('application/json', options)
    }, options)
  },
  upload (url, data, options = {}) {
    if (!check(url, options)) {
      return Promise.reject({
        system: true,
        code: -10000
      })
    }

    let formData = new FormData()
    if (data.type === 'blob') {
      formData.append('picFile', data.file, `${ Date.now() }.jpeg`)
    } else {
      formData.append('picFile', data.file)
    }

    return createPost(url, addCommonData(formData, options), {
      headers: getHeader('application/json', options)
    }, options)
  },
  createSource () {
    return axios.CancelToken.source()
  }
}

export default http
