<template>
  <list-page
    :title="$t('settings.account.pwd.title')"
    :tip="$t('settings.account.pwd.tip')"
    :button="$t('btn.next')"
    :disable="disable"
    @click="toNext"
  >
    <field
      class="c-vlist-item--nearer"
      v-model="cpassword"
      type="password"
      :title="$t('settings.account.pwd.password')"
      :placeholder="$t('settings.account.pwd.password')"
      maxlength="16"
      :error="cerror"
      @input="cerror = ''"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="npassword"
      type="password"
      :title="$t('sign.reset.npassword')"
      :placeholder="$t('sign.reset.npassword')"
      maxlength="16"
      :error="nerror"
      @input="nerror = ''"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="rpassword"
      type="password"
      :title="$t('sign.info.rpassword')"
      :placeholder="$t('sign.info.rpassword')"
      maxlength="16"
      :error="rerror"
      @input="rerror = ''"
    />
  </list-page>
</template>

<script>
import { checkPassword } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    ListPage,
    Field
  },
  data () {
    return {
      cpassword: '',
      cerror: '',
      npassword: '',
      nerror: '',
      rpassword: '',
      rerror: ''
    }
  },
  computed: {
    disable () {
      return !this.cpassword || !this.npassword || !this.rpassword
    }
  },
  methods: {
    toNext () {
      let cResult = checkPassword(this.cpassword)
      this.cerror = cResult.msg

      let nResult = checkPassword(this.npassword)
      this.nerror = nResult.msg

      let rResult = checkPassword(this.rpassword)
      this.rerror = rResult.msg

      if (nResult.pass && rResult.pass) {
        if (this.npassword !== this.rpassword) {
          this.rerror = this.$t('patt.pwd.diff')
        } else {
          if (cResult.pass) {
            this.$http.post('/api/app/user/password/change', {
              oldPassword: this.$crypto.sha256(this.cpassword),
              newPassword: this.npassword
            }, {
              login: true,
              loading: true,
              server: true
            }).then(() => {
              this.$router.replace({
                name: 'change-password-result'
              })
            })
          }
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
