<template>
  <div class="c-ticket">
    <div class="l-flex--row" @click="toDetail(ticket)">
      <div class="is-title l-flex__fill">{{ ticket.name }}</div>
      <img class="o-icon l-flex__none" src="@/assets/icon-arrow--right.png"/>
    </div>
    <div v-if="hasRequired" class="is-content--dark u-font u-separate--nearest">{{ requiredRemark }}</div>
    <div v-if="hasValid" class="is-content u-font--bold u-separate--nearest">{{ valid }}</div>
    <div class="u-separate--nearer l-flex--row is-top">
      <div class="c-ticket__price l-flex__fill">{{ currency }} {{ priceVal }}</div>
      <div class="o-stepper l-flex__none">
        <i
          class="o-stepper__icon o-stepper__minus"
          :class="{ 'is-disable': isMin }"
          @click="reduce"
        ></i>
        <span class="o-stepper__count">{{ ticket.count }}</span>
        <i
          class="o-stepper__icon o-stepper__add"
          :class="{ 'is-disable': isMax }"
          @click="add"
        ></i>
      </div>
    </div>
  </div>
</template>

<script>
import { transformTicketValidTime } from '@/utils/format.js'

export default {
  props: {
    ticket: {
      type: Object,
      default () {
        return {}
      }
    },
    free: {
      type: Number,
      default: 0
    }
  },
  computed: {
    priceInfo () {
      return this.ticket.priceList[0]
    },
    currency () {
      return this.priceInfo.unit
    },
    price () {
      return this.priceInfo.realPrice
    },
    priceVal () {
      return this.price.toFixed(2)
    },
    hasDiscount () {
      return this.priceInfo.price !== this.price
    },
    discountVal () {
      if (this.hasDiscount) {
        return this.priceInfo.price.toFixed(2)
      }
      return ''
    },
    hasDiscountInfo () {
      if (this.hasDiscount && this.priceInfo.discountType === 1) {
        return true
      }
      return false
    },
    discount () {
      if (this.hasDiscountInfo) {
        return `${ (this.priceInfo.discount * 100 / this.priceInfo.price).toFixed(0) }% OFF`
      }
      return ''
    },
    requiredRemark () {
      return this.ticket.requiredRemark ? this.ticket.requiredRemark.filter(item => !!item).join(' • ') : ''
    },
    hasRequired () {
      return this.requiredRemark !== ''
    },
    valid () {
      return transformTicketValidTime(this.ticket)
    },
    hasValid () {
      return this.valid !== ''
    },
    minBuyCount () {
      return this.ticket.minBuyCount
    },
    maxBuyCount () {
      return this.ticket.maxBuyCount
    },
    isMin () {
      return this.ticket.count === 0
    },
    isMax () {
      return this.free === 0 || this.isMin && this.free < this.minBuyCount || this.ticket.count >= this.maxBuyCount
    }
  },
  methods: {
    add () {
      if (this.isMax) {
        return
      }
      this.ticket.count += this.isMin ? Math.max(1, this.minBuyCount) : 1
    },
    reduce () {
      if (this.isMin) {
        return
      }
      this.ticket.count -= this.ticket.count === this.minBuyCount ? this.ticket.count : 1
    },
    toDetail () {
      this.$router.push({
        name: 'ticket-detail',
        params: {
          id: this.ticket.ticketId
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.c-ticket {
  padding: 32px 40px 40px 0;

  &__price {
    font-size: 36px;
    color: $color-orange;
    line-height: 40px;
    @include u-font;
  }
}

.o-stepper {
  display: inline-flex;
  align-items: center;

  &__count {
    box-sizing: border-box;
    padding: 0 10px;
    min-width: 56px;
    text-align: center;
    @include is-content($color--dark);
    @include u-font--bold;
  }

  &__icon {
    display: inline-block;
    width: 38px;
    height: 38px;
    @include bg-img-attrs;
  }

  &__minus {
    background-image: url(~@/assets/icon-minus.png);

    &.is-disable {
      background-image: url(~@/assets/icon-minus--disable.png);
    }
  }

  &__add {
    background-image: url(~@/assets/icon-add.png);

    &.is-disable {
      background-image: url(~@/assets/icon-add--disable.png);
    }
  }
}
</style>
