<template>
  <list-page :title="$t('settings.title')">
    <cell
      :title="$t('settings.account.title')"
      arrow
      @click="toAccountSettings"
    />
    <cell
      :title="$t('settings.language')"
      :value="lang"
      arrow
      @click="toLanguage"
    />
    <cell
      :title="$t('settings.notification')"
      toggle
      :checked="notification"
      @change="switchNotification"
    />
    <cell
      :title="$t('settings.about.title')"
      arrow
      @click="toAbout"
    />
    <cell
      :title="$t('common.feedback.title')"
      arrow
      @click="toFeedback"
    />
    <cell
      :title="$t('settings.clear')"
      @click="dialogMsg.show = true"
    />
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="$t('settings.clear')"
      :message="$t('settings.clearTip')"
      :confirm-button-text="$t('btn.confirm')"
      :cancel-button-text="$t('btn.cancel')"
      show-cancel-button
      @confirm="clearCache"
    />
  </list-page>
</template>

<script>
import DialogMixin from '@/components/mixins/dialog.js'
import ListPage from '@/components/list/ListPage.vue'
import Cell from '@/components/list/Cell.vue'
import { langs } from '@/i18n/index.js'

export default {
  mixins: [ DialogMixin ],
  components: {
    ListPage,
    Cell
  },
  data () {
    return {
      notification: this.$store.getters.userInfo.receiveNotify === 0 ? false : true
    }
  },
  computed: {
    lang () {
      let langKey = this.$i18n.locale
      let lang = langs.find(lang => lang.key === langKey)
      return lang ? lang.text : ''
    }
  },
  methods: {
    toAccountSettings () {
      this.$router.push({
        name: 'account-settings'
      })
    },
    toLanguage () {
      this.$router.push({
        name: 'language'
      })
    },
    switchNotification (checked) {
      this.notification = checked
      let receiveNotify = this.notification ? 1 : 0
      this.$http.post('/api/app/user/notify/change', { receiveNotify }, {
        globals: true,
        login: true
      })
      this.$store.dispatch('userInfo', { receiveNotify })
    },
    toAbout () {
      this.$router.push({
        name: 'about'
      })
    },
    toFeedback () {
      this.$router.push({
        name: 'feedback'
      })
    },
    clearCache () {}
  }
}
</script>

<style lang="scss" scoped>
</style>
