<template>
  <div class="c-nav" :class="{ 'is-fixed': fixed }">
    <div
      class="c-nav__content"
      :class="headerClass"
      :style="{ 'opacity': opacity }"
    >
      <div class="c-nav__left">
        <div class="c-nav__arrow" @click.stop="back">
          <van-icon
            :class="{ 'u-color': normal }"
            :name="icon"
          />
        </div>
      </div>
      <div class="l-page__title c-nav__title is-ellipsis">{{ title }}</div>
      <div class="c-nav__right">
        <slot/>
      </div>
    </div>
    <div v-if="fixed" class="l-page__header c-nav__content" :style="{ 'opacity': 1 - opacity }">
      <div class="c-nav__left">
        <div class="c-nav__arrow has-bg" @click.stop="back">
          <van-icon :name="icon"/>
        </div>
      </div>
      <div v-if="alone" class="l-page__title c-nav__title is-ellipsis u-color--white">{{ title }}</div>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant'

export default {
  components: {
    [ Icon.name ]: Icon
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    normal: {
      type: [Boolean, String],
      default: false
    },
    fixed: {
      type: [Boolean, String],
      default: false
    },
    scroll: {
      type: Number,
      default: 0
    },
    icon: {
      type: String,
      default: 'down'
    },
    custom: {
      type: [Boolean, String],
      default: false
    },
    alone: {
      type: [Boolean, String],
      default: false
    }
  },
  data () {
    return {
      height: 0
    }
  },
  computed: {
    headerClass () {
      return this.normal ? 'l-page__header' : 'l-page__header--primary'
    },
    opacity () {
      if (this.fixed) {
        return this.height === 0 ? 0 : Math.min(1, this.scroll / this.height)
      }
      return 1
    }
  },
  methods: {
    back () {
      if (this.custom) {
        this.$emit('back')
      } else {
        this.$features.back()
      }
    }
  },
  mounted () {
    if (this.fixed) {
      this.$nextTick(() => {
        this.height = this.$el.children[0].offsetHeight
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.c-nav {
  z-index: 9;

  &.is-fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    .c-nav__content {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      transition: opacity .3s;
    }
  }

  &__left {
    position: relative;
    width: 20%;
  }

  &__right {
    width: 20%;
    font-size: 0;
    text-align: right;
  }

  &__title {
    max-width: 60%;
    text-align: center;
  }

  &__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    width: 72px;
    height: 72px;
    font-size: 42px;
    transform-origin: 50% 50%;
    transform: rotate(90deg) translateX(-50%);

    &.has-bg {
      color: $color-white;
      border-radius: 50%;
      background-color: rgba($color-black, .3);
    }
  }
}
</style>
