<template>
  <div class="c-required-block l-collection" :class="{ 'is-open': isOpen, 'is-alone': alone }">
    <div class="c-required-block__header l-collection__item l-flex--row" @click="toggle = !toggle">
      <span
        v-if="!alone"
        class="c-required-block__cursor"
        :class="{ 'is-valid': valid }"
      >
        {{ info.index }}
      </span>
      <div class="is-tip--dark l-flex__fill">{{ info.name }}</div>
      <van-icon
        class="c-required-block__icon c-required-block__toggle l-flex__none"
        name="arrow-down"
      />
    </div>
    <div v-if="isOpen">
      <div
        v-if="info.isBasic"
        class="c-required-block__content"
      >
        <required-item
          v-for="item in info.form[0].list"
          :key="item.id"
          :info="item"
          v-on="$listeners"
        />
      </div>
      <template v-else>
        <div
          v-if="info.hasUserData"
          class="l-collection__item has-hairline--t l-flex--row"
          @click="openCalendar"
        >
          <div class="is-tip--dark u-font l-flex__fill">{{ $t('ticket.buy.confirm.date') }}</div>
          <div class="is-tip--blue l-flex__none">{{ date }}</div>
          <van-icon
            class="c-required-block__icon l-flex__none"
            name="arrow"
          />
        </div>
        <div
          v-if="hasChoose"
          class="c-required-block__time has-hairline--t"
        >
          <div class="is-tip--dark u-font">{{ $t('ticket.buy.confirm.time') }}</div>
          <div class="l-hlist u-separate--near">
            <div class="l-hlist__wrapper">
              <div
                v-for="(time, index) in timeList"
                :key="index" class="c-hlist-item--near o-option"
                :class="{ 'is-selected': time === info.userData.time }"
                @click="onTimeChange(time)"
              >
                {{ time }}
              </div>
            </div>
          </div>
        </div>
        <template v-if="info.hasForm">
          <div class="c-required-block__list">
            <toggle
              v-for="block in info.form"
              :key="block.id"
              class="c-required-block__item has-hairline--t"
              :title="block.name"
            >
              <div class="c-required-block__content u-separate--nearer">
                <required-item
                  v-for="item in block.list"
                  :key="item.id"
                  :info="item"
                  v-on="$listeners"
                />
              </div>
            </toggle>
          </div>
        </template>
      </template>
    </div>
    <van-popup
      v-model="show"
      position="bottom"
      round
      @closed="clear"
    >
      <calendar
        v-if="dateInfo"
        :ranges="dateInfo.ranges"
        :day="dateInfo.choose"
        @change="onCalendarChange"
      />
    </van-popup>
  </div>
</template>

<script>
import { dateFormat } from '@/utils/format.js'
import { Icon, Popup } from 'vant'
import RequiredItem from './RequiredItem.vue'
import Calendar from '@/components/Calendar.vue'
import Toggle from '@/components/Toggle.vue'

export default {
  components: {
    [ Icon.name ]: Icon,
    [ Popup.name ]: Popup,
    RequiredItem,
    Calendar,
    Toggle
  },
  props: {
    info: {
      type: Object,
      default () {
        return null
      }
    },
    alone: {
      type: [Boolean, String],
      default: false
    }
  },
  data () {
    return {
      toggle: false,
      show: false,
      dateInfo: null
    }
  },
  computed: {
    isOpen () {
      return this.alone || this.toggle
    },
    hasChoose () {
      return this.info && this.info.hasUserData && this.info.userData.date !== '' ? true : false
    },
    date () {
      return this.hasChoose ? this.info.userData.date.replace(/-/g, '.') : this.$t('ticket.buy.confirm.choose')
    },
    isFixedDate () {
      return this.hasChoose && this.info.userData.from.date ? this.info.userData.from.date.length > 0 : false
    },
    timeList () {
      if (this.hasChoose) {
        if (this.isFixedDate) {
          let dateTime = new Date(this.info.userData.date.replace(/-/g, '/')).getTime()
          let dateList = this.info.userData.from.date
          let index = dateList.findIndex(item => item.startTime <= dateTime && item.endTime >= dateTime)
          if (index >= 0) {
            let list = this.info.userData.from.date[index].timeList
            if (dateFormat(Date.now(), 'yyyy-MM-dd') === this.info.userData.date) {
              let now = new Date()
              now = now.getHours() * 60 + now.getMinutes()
              return list.filter(time => this.transformTimeToMinutes(time) > now)
            }
            return list
          }
        }
        return this.info.userData.from.commonTimeList || []
      }
      return []
    },
    valid () {
      if (this.info.hasUserData && this.info.userData.code !== 1) {
        return false
      }

      if (this.info.hasForm) {
        let form = this.info.form
        for (let i = 0, j = form.length; i < j; i++) {
          let list = form[i].list
          for (let n = 0, m = list.length; n < m; n++) {
            let item = list[n]
            if (item.code !== 1) {
              return false
            }

            if (item.hasExtInfo && item.extInfo.code !== 1) {
              return false
            }
          }
        }
      }

      return true
    }
  },
  methods: {
    transformTimeToMinutes (time) {
      let arr = time.split(':')
      if (arr.length === 2) {
        return Number(arr[0]) * 60 + Number(arr[1])
      }
      return -1
    },
    openCalendar () {
      this.dateInfo = {
        ranges: this.info.userData.from.date || [],
        choose: this.info.userData.date
      }
      this.show = true
    },
    checkValid () {
      this.info.userData.code = this.info.userData.date === '' ? -100 : this.info.userData.time === '' ? -101 : 1
    },
    onCalendarChange (date) {
      this.info.userData.date = date
      let time = this.info.userData.time
      if (time && this.timeList.findIndex(item => item === time) === -1) {
        this.info.userData.time = ''
      }
      this.show = false
      this.checkValid()
    },
    clear () {
      this.dateInfo = null
    },
    onTimeChange (time) {
      this.info.userData.time = time
      this.checkValid()
    }
  }
}
</script>

<style lang="scss" scoped>
.c-required-block {
  &.is-open {
    .c-required-block__toggle {
      transform: rotate(-180deg);
    }
  }

  &.is-alone {
    padding: 40px 40px 0 40px;

    .c-required-block__header {
      padding: 0;
    }

    .c-required-block__cursor, .c-required-block__toggle {
      display: none;
    }

    .c-required-block__item {
      padding-right: 0;
    }
  }

  &__content {
    padding: 0 0 24px;
  }

  &__cursor {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    margin-right: 16px;
    font-size: 20px;
    color: $color-white;
    line-height: 36px;
    border-radius: 50%;
    background-color: $color;

    &.is-valid {
      background-color: $color-blue;
    }
  }

  &__icon {
    margin-left: 10px;
    font-size: 32px;
    color: $color-blue;
  }

  &__list {
    margin: 0 40px 40px 0;
    border-radius: 16px;
    border: 2px solid $color-separate;

    .c-required-block__item.is-toggle {
      padding-bottom: 0;
    }
  }

  &__toggle {
    transition: transform .3s;
    color: $color;
  }

  &__time {
    padding: 30px 40px 40px 0;

    .l-hlist__wrapper {
      padding: 0;
    }
  }
}
</style>
