<template>
  <div class="c-card has-shadow" @click="toDetail">
    <div class="c-card__img">
      <v-resource class="is-cover" :info="entity.picInfo"/>
      <div
        v-if="needWait"
        class="o-tag is-hanging is-orange"
      >
        <span class="t1">{{ entity.pflowInfo.entityWaitTime }}</span>
        <span class="s1">{{ $t('explore.detail.wait') }}</span>
      </div>
      <div
        v-if="isSuspend"
        class="o-tag is-hanging is-disable"
      >
        <span>{{ $t('explore.detail.suspend') }}</span>
      </div>
    </div>
    <div class="c-card__content">
      <div class="is-title">{{ entity.name }}</div>
      <div class="u-separate--nearest">
        <row
          v-if="entity.location"
          class="c-vlist-item--nearest"
          icon="location"
          dark
        >
          {{ entity.location }}
        </row>
        <row
          v-if="hasOpeningInfo"
          class="c-vlist-item--nearest"
          icon="time"
        >
          <opening
            :entity="entity"
            :status="status"
            :now="now"
          />
        </row>
        <div
          v-if="remark"
          class="c-vlist-item--nearest is-tip u-font is-ellipsis"
        >
          {{ remark }}
        </div>
      </div>
    </div>
    <v-footer :entity="entity"/>
  </div>
</template>

<script>
import Row from '@/components/RowIcon.vue'
import Opening from './ActivityOpening.vue'
import VFooter from './ActivityFooter.vue'

export default {
  components: {
    Row,
    Opening,
    VFooter
  },
  props: {
    entity: {
      type: Object,
      default: null
    },
    now: {
      type: Number,
      default () {
        return Date.now()
      }
    }
  },
  computed: {
    isShowsType () {
      return this.entity.typeId === 4
    },
    hasShows () {
      return this.entity.activityList !== void 0
    },
    hasPFlow () {
      return this.entity.pflowStatus === 1
    },
    status () {
      return this.$features.getStatus(this.entity)
    },
    isSuspend () {
      return this.status.isSuspend
    },
    needWait () {
      return this.hasPFlow && this.status.isOpeningNow && !this.isSuspend && this.entity.pflowInfo.entityWaitTime > 0
    },
    hasOpeningInfo () {
      return this.hasPFlow || this.entity.activityList || this.entity.operatorInfo
    },
    remark () {
      // 表演类型
      if (this.isShowsType) {
        if (this.hasShows && this.entity.activityList.length > 0) {
          return this.entity.activityList[0].remark
        }
        return ''
      }
      // 有表演关联且close时不显示remark
      if (this.hasShows) {
        if (this.entity.activityList.length > 0 && !this.status.isClosed) {
          return this.entity.activityList[0].remark
        }
        return ''
      }
      if (this.entity.menu) {
        let info = []
        if (this.entity.menu.menuType) {
          info.push(this.entity.menu.menuType)
        }
        if (this.entity.menu.serviceType) {
          info = info.concat(this.entity.menu.serviceType)
        }
        if (this.entity.menu.price && this.entity.menu.price.content) {
          info.push(this.entity.menu.price.content)
        }
        if (info.length > 0) {
          return info.join(' • ')
        }
        return ''
      }
      let conditionList = this.entity.conditionList ? this.entity.conditionList.filter(condition => condition !== '') : []
      if (conditionList.length > 0) {
        return conditionList.join(' • ')
      }
      return ''
    }
  },
  methods: {
    toDetail () {
      this.$features.entity(this.entity.id)
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
