<template>
  <div v-if="has" class="o-indicator">
    <div v-if="hasBeforeMore" class="o-indicator__dot is-more"></div>
    <div v-for="(dot, index) in dotTotal" :key="index" class="o-indicator__dot" :class="{ 'is-selected': index === cursor }"></div>
    <div v-if="hasAfterMore" class="o-indicator__dot is-more"></div>
  </div>
</template>

<script>
export default {
  props: {
    count: {
      type: Number,
      default: 0
    },
    main: {
      type: Number,
      default: 5
    },
    current: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      $index: 0,
      cursor: 0,
      hasBeforeMore: false,
      hasAfterMore: false
    }
  },
  computed: {
    has () {
      return this.count > 1
    },
    mainDotCount () {
      return Math.max(3, this.main)
    },
    isOver () {
      return this.count >= this.mainDotCount + 2
    },
    dotTotal () {
      return this.isOver ? this.mainDotCount : this.count
    }
  },
  methods: {
    to (index, force = false) {
      if (this.isOver) {
        if (!force && this.$index + 1 === index) {
          if (index >= this.count - 2) {
            this.cursor = this.dotTotal - (this.count - index)
            this.hasBeforeMore = true
            this.hasAfterMore = false
          } else if (this.cursor < this.dotTotal - 1) {
            this.cursor += 1
          } else {
            this.hasBeforeMore = true
          }
        } else if (!force && this.$index - 1 === index) {
          if (index < 2) {
            this.cursor = index
            this.hasBeforeMore = false
            this.hasAfterMore = true
          } else if (this.cursor > 0) {
            this.cursor -= 1
          } else {
            this.hasAfterMore = true
          }
        } else {
          if (index < this.dotTotal) {
            this.cursor = index
            this.hasBeforeMore = false
            this.hasAfterMore = true
          } else if (index + this.dotTotal < this.count) {
            this.cursor = 0
            this.hasBeforeMore = true
            this.hasAfterMore = true
          } else {
            this.cursor = this.dotTotal - (this.count - index)
            this.hasBeforeMore = true
            this.hasAfterMore = false
          }
        }
      } else {
        this.cursor = index
        this.hasBeforeMore = false
        this.hasAfterMore = false
      }
      this.$index = index
    }
  },
  watch: {
    current (nValue) {
      this.to(nValue)
    },
    count () {
      this.to(this.current, true)
    }
  },
  mounted () {
    this.to(this.current, true)
  }
}
</script>

<style lang="scss" scoped>
.o-indicator {
  display: flex;
  justify-content: center;
  align-items: center;

  &__dot {
    flex: none;
    width: 16px;
    height: 16px;
    background-color: $color-blue;
    border-radius: 100%;
    opacity: .3;
    transition: opacity .1s;

    & + & {
      margin-left: 12px;
    }

    &.is-selected {
      opacity: 1;
    }

    &.is-more {
      transform: scale(.5);
    }
  }
}
</style>
