<template>
  <list-page
    :title="$t('settings.account.profile.title')"
  >
    <template #nav>
      <span
        class="is-content--blue"
        :class="{ 'u-color': hasError }"
        @click="save"
      >
        {{ $t('btn.save') }}
      </span>
    </template>
    <i
      class="o-avatar"
      :style="avatarStyle"
      @click="toEdit"
    >
      <i
        v-if="loginByEmail"
        class="o-edit"
      >
        <van-icon name="photograph"/>
      </i>
    </i>
    <div class="c-vlist-item--nearer u-separate--far l-flex--row is-top">
      <field
        class="l-flex__fill"
        v-model="firstName"
        :title="$t('sign.signup.firstName')"
        :placeholder="$t('sign.signup.firstName')"
        :error="firstNameError"
        @input="firstNameError = ''"
        @blur="changeFirstName"
      />
      <field
        class="l-flex__fill u-h-separate"
        v-model="lastName"
        :title="$t('sign.signup.lastName')"
        :placeholder="$t('sign.signup.lastName')"
        :error="lastNameError"
        @input="lastNameError = ''"
        @blur="changeLastName"
      />
    </div>
    <field
      v-if="!loginByEmail"
      class="c-vlist-item--nearer"
      v-model="nick"
      :title="$t('settings.account.profile.nick')"
      :placeholder="$t('settings.account.profile.nick')"
      maxlength="12"
      :error="nickError"
      readonly
      @input="nickError = ''"
      @blur="changeNickName"
    />
    <field
      class="c-vlist-item--nearer"
      :value="birthDayStr"
      :title="$t('sign.signup.birth')"
      :placeholder="$t('sign.signup.birthPlaceholder')"
      readonly
      clickable
      @click="onClickBirth"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="country2city"
      :title="$t('sign.signup.country')"
      :placeholder="$t('sign.signup.countryPlaceholder')"
      readonly
      clickable
      @click="onClickCountry"
    />
    <van-popup
      v-if="showBirthPicker"
      v-model="showBirth"
      position="bottom"
      @closed="onHideBirth"
    >
      <van-datetime-picker
        type="date"
        :value="currentDate"
        :min-date="minDate"
        :max-date="maxDate"
        :confirm-button-text="$t('btn.done')"
        :cancel-button-text="$t('btn.cancel')"
        @confirm="onConfirmBirth"
        @cancel="showBirth = false"
      />
    </van-popup>
    <van-popup
      v-if="showCountryPicker"
      v-model="showCountry"
      position="bottom"
      @closed="onHideCountry"
    >
      <van-picker
        :columns="columns"
        :show-toolbar="true"
        :confirm-button-text="$t('btn.done')"
        :cancel-button-text="$t('btn.cancel')"
        @confirm="onConfirmCountry"
        @cancel="showCountry = false"
      />
    </van-popup>
    <input ref="fileUpload" type="file" accept="image/*" style="display: none;" @change="handleFile"/>
  </list-page>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { checkName, checkNickName } from '@/utils/patt.js'
import { dateFormat } from '@/utils/format.js'
import { Icon, Popup, DatetimePicker, Picker } from 'vant'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    [ Icon.name ]: Icon,
    [ Popup.name ]: Popup,
    [ DatetimePicker.name ]: DatetimePicker,
    [ Picker.name ]: Picker,
    ListPage,
    Field
  },
  data () {
    let today = new Date()

    return {
      os: '',
      avatar: null,
      firstName: '',
      firstNameError: '',
      lastName: '',
      lastNameError: '',
      nick: '',
      nickError: '',
      minDate: new Date(today.getFullYear() - 100, 0, 1),
      maxDate: today,
      birthDay: null,
      showBirth: false,
      showBirthPicker: false,
      areaId: null,
      country: null,
      columns: null,
      showCountry: false,
      showCountryPicker: false
    }
  },
  computed: {
    ...mapState(['countryList']),
    ...mapGetters(['baseUrl', 'loginByEmail', 'avatarUrl', 'nickName', 'userInfo']),
    isAndroid () {
      return this.os === 'android'
    },
    isIos () {
      return this.os === 'ios'
    },
    avatarStyle () {
      if (this.avatar && this.avatar.path) {
        return `backgroundImage: url("${ this.avatar.path }")`
      }

      return this.avatarUrl ? { backgroundImage: `url("${ this.avatarUrl }")` } : null
    },
    birth () {
      if (this.birthDay) {
        return new Date(this.birthDay.replace(/-/g, '/'))
      }

      if (this.userInfo.birthDay) {
        return new Date(this.userInfo.birthDay.replace(/-/g, '/'))
      }

      return null
    },
    birthDayStr () {
      return this.birth ? dateFormat(this.birth, this.$t('format.birth')) : ''
    },
    currentDate () {
      if (this.birthDay) {
        return new Date(this.userInfo.birthDay.replace(/-/g, '/'))
      }
      return this.birth || new Date()
    },
    country2city () {
      if (this.country) {
        return this.country
      }

      let id = this.areaId || this.userInfo.areaId
      if (id) {
        let index = this.countryList.findIndex(country => country.id === id)
        return index >= 0 ? this.countryList[index].name : ''
      }

      return ''
    },
    hasError () {
      return this.firstNameError || this.lastNameError || this.nickError
    }
  },
  methods: {
    upload (file, type, path = '') {
      this.avatar = { file, type, path }
      if (type === 'file') {
        let reader = new FileReader()
        reader.onload = e => {
          this.avatar.path = e.target.result
        }
        reader.readAsDataURL(file)
      }
    },
    handleFile (e) {
      let target = e.target || e.srcElement
      let file = target.files[0]
      if (!file) {
        return
      }
      this.$refs.fileUpload.value = null
      this.upload(file, 'file')
    },
    toEdit () {
      if (this.loginByEmail) {
        if (this.isAndroid) {
          if (this.$refs.fileUpload) {
            this.$refs.fileUpload.click()
          }
        } else if (this.isIos) {
          this.$native.pickimage().then(result => {
            let file = result && result.image
            if (file) {
              let bytes = window.atob(file)
              let array = []
              for (let i = 0; i < bytes.length; i++) {
                array.push(bytes.charCodeAt(i))
              }
              this.upload(new Blob([new Uint8Array(array)], { type: 'image/jpeg' }), 'blob', `data:image/jpeg;base64,${ file }`)
            }
          })
        }
      }
    },
    onClickBirth () {
      this.showBirthPicker = true
      this.$nextTick(() => this.showBirth = true)
    },
    onHideBirth () {
      this.showBirthPicker = false
    },
    onConfirmBirth (date) {
      this.showBirth = false
      let birthDay = dateFormat(date, 'yyyy-MM-dd')
      if (this.birthDay !== birthDay) {
        this.birthDay = birthDay
      }
    },
    onClickCountry () {
      if (this.countryList.length > 0) {
        if (this.$countryIndex === void 0) {
          this.$countryIndex = Math.max(this.countryList.findIndex(country => country.id === this.areaId), 0)
        }
        this.columns = [
          {
            values: this.countryList.map(country => country.name),
            defaultIndex: this.$countryIndex || 0
          }
        ]
        this.showCountryPicker = true
        this.$nextTick(() => this.showCountry = true)
      }
    },
    onHideCountry () {
      this.showCountryPicker = false
    },
    onConfirmCountry (values, indexs) {
      let country = this.countryList[indexs[0]]
      if (this.areaId !== country.id) {
        this.areaId = country.id
        this.country = country.name
        this.$countryIndex = indexs[0]
      }
      this.showCountry = false
    },
    changeFirstName () {
      this.firstNameError = checkName(this.firstName).msg
    },
    changeLastName () {
      this.lastNameError = checkName(this.lastName).msg
    },
    changeNickName () {
      this.nickError = checkNickName(this.nick).msg
    },
    save () {
      if (!this.hasError) {
        let change = false
        let formData = new FormData()
        if (this.avatar) {
          if (this.avatar.type === 'blob') {
            formData.append('picFile', this.avatar.file, `${ Date.now() }.jpeg`)
          } else {
            formData.append('picFile', this.avatar.file)
          }
          change = true
        }

        if (this.firstName !== this.userInfo.firstName) {
          change = true
        }
        formData.append('firstName', this.firstName)

        if (this.lastName !== this.userInfo.lastName) {
          change = true
        }
        formData.append('lastName', this.lastName)

        // if (this.nick !== this.nickName) {
        //   change = true
        // }
        // formData.append('nickName', this.nick)

        if (this.birthDay && this.birthDay !== this.userInfo.birthDay) {
          change = true
        }
        formData.append('birthDay', this.birthDay || this.userInfo.birthDay || '')

        if (this.areaId && this.areaId !== this.userInfo.areaId) {
          change = true
        }
        formData.append('areaId', this.areaId || this.userInfo.areaId || '')

        if (change) {
          this.$http.postForm('/api/app/user/info/change', formData, {
            login: true,
            loading: true,
            server: true
          }).then(data => {
            this.$store.dispatch('userInfo', data)
            this.$features.back()
          })
        } else {
          this.$features.back()
        }
      }
    }
  },
  created () {
    if (this.loginByEmail) {
      this.$native.os().then(os => {
        this.os = os
      })
    }
    this.firstName = this.userInfo.firstName || ''
    this.lastName = this.userInfo.lastName || ''
    this.nick = this.nickName
    this.birthDay = this.userInfo.birthDay || ''
    this.areaId = this.userInfo.areaId || 0
  }
}
</script>

<style lang="scss" scoped>
.o-avatar {
  display: inline-block;
  position: relative;
  width: 216px;
  height: 216px;
  border-radius: 50%;
  background: url(~@/assets/avatar.png) 0 0 / 100% 100% no-repeat;
}

.o-edit {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  bottom: 0;
  width: 80px;
  height: 80px;
  font-size: 42px;
  color: $color-white;
  border-radius: 50%;
  overflow: hidden;
  background-color: $color-blue;
}
</style>
