<template>
  <list-page :title="$t('settings.language')">
    <cell
      v-for="lang in langs"
      :key="lang.key"
      :title="lang.text"
      @click="change(lang.key)"
    >
      <img
        v-if="lang.is"
        class="o-hook"
        src="@/assets/icon-hook.png"
      />
    </cell>
  </list-page>
</template>

<script>
import { langs, changeLang } from '@/i18n/index.js'
import { reset } from '@/utils/common.js'
import ListPage from '@/components/list/ListPage.vue'
import Cell from '@/components/list/Cell.vue'

export default {
  components: {
    ListPage,
    Cell
  },
  computed: {
    langs () {
      let langKey = this.$i18n.locale
      return langs.map(({ key, text }) => {
        return {
          key, text,
          is: key === langKey
        }
      })
    }
  },
  methods: {
    change (langKey) {
      if (changeLang(langKey)) {
        reset()
      }
      this.$features.back()
    }
  }
}
</script>

<style lang="scss" scoped>
.o-hook {
  width: 40px;
  height: 40px;
}
</style>
