import Vue from 'vue'
import VueI18n from 'vue-i18n'
import en from './lang/en_US.js'
import sc from './lang/zh_CN.js'
import tc from './lang/zh_HK.js'

Vue.use(VueI18n)

export const langs = [
  {
    key: 'en',
    text: 'English'
  },
  {
    key: 'tc',
    text: '繁體中文（香港）'
  },
  {
    key: 'sc',
    text: '简体中文'
  }
]

let defaultKey = window.localStorage.getItem('locale')
function getDefaultKey () {
  if (defaultKey) {
    let defaultLang = langs.find(lang => lang.key === defaultKey)
    if (!defaultLang) {
      defaultKey = null
      window.localStorage.removeItem('locale')
    }
  }
  return 'en' || defaultKey || 'none'
}

const i18n = new VueI18n({
  locale: getDefaultKey(),
  messages: {
    en, tc, sc
  }
})

export default i18n

export function changeLang (langKey) {
  if (langKey !== i18n.locale) {
    let langLocale = langs.find(lang => lang.key === langKey)
    if (langLocale) {
      i18n.locale = langKey
      window.localStorage.setItem('locale', langKey)
      return true
    }
  }
  return false
}
