<template>
  <div class="l-page">
    <v-nav :title="title"/>
    <template v-if="list">
      <div
        ref="content"
        class="l-page__content has-padding--around"
      >
        <div
          v-if="hasHeader"
          class="c-vlist-item--far"
        >
          <div
            v-if="list.title"
            class="c-vlist-item--nearer is-title--block"
          >
            {{ list.title }}
          </div>
          <div
            v-if="list.content"
            class="c-vlist-item--nearer is-content--dark u-font"
          >
            {{ list.content }}
          </div>
        </div>
        <div
          v-for="(ticket, index) in ticketList"
          :key="index"
          class="c-vlist-item--far c-card has-shadow"
          @click="toDetail(ticket)"
        >
          <v-resource class="c-card__img" :info="ticket.picInfo"/>
          <div class="c-card__content">
            <div class="is-title">{{ ticket.name }}</div>
            <div class="is-tip--dark u-font u-separate--nearest">{{ ticket.content }}</div>
            <div class="is-title-orange u-font u-separate--nearer">{{ ticket.price }}</div>
          </div>
        </div>
      </div>
      <div class="c-fixed-footer has-hairline--tb">
        <div class="o-button is-trigger is-auto" @click="toBuy">{{ btnMsg }}</div>
      </div>
    </template>
  </div>
</template>

<script>
import LocaleMixin from '@/components/mixins/locale.js'

export default {
  mixins: [ LocaleMixin ],
  data () {
    return {
      route: 'ticket-list',
      list: null
    }
  },
  computed: {
    title () {
      return this.list && this.list.name || ''
    },
    hasHeader () {
      return this.list && (this.list.title || this.list.content)
    },
    ticketList () {
      return this.list && this.list.ticketList || []
    },
    btnMsg () {
      if (this.list) {
        switch (this.list.id) {
          case 4:
            return this.$t('ticket.detail.oft')
          case 5:
            return this.$t('ticket.detail.c2a')
          default:
            return this.$t('btn.buyticket')
        }
      }
      return ''
    }
  },
  methods: {
    getList (typeId) {
      if (this.list && this.list.id !== typeId) {
        this.list = null
      }
      this.$http.post('/api/common/ticket/type/detail', {
        typeId
      }, {
        login: true,
        server: true
      }).then(data => {
        if (!this.list || this.list.updateTime !== data.updateTime) {
          this.list = data
        }
      })
    },
    toDetail (ticket) {
      this.$features.ticket(ticket.ticketId)
    },
    toBuy () {
      this.$features.toBuyTicket(this.list.id)
    },
    onLocaleUpdated () {
      this.$scrollTop = 0
      this.list = null
    }
  },
  created () {
    this.$scrollTop = 0
  },
  activated () {
    let type = Number(this.$route.params.type)
    if (this.list && this.list.id !== type) {
      this.$scrollTop = 0
    }

    if (this.$scrollTop) {
      let dom = this.$refs.content
      if (dom) {
        dom.scrollTop = this.$scrollTop
      }
      this.$scrollTop = 0
    }

    this.getList(type)
  },
  deactivated () {
    let dom = this.$refs.content
    if (dom) {
      this.$scrollTop = dom.scrollTop
    }
  },
  beforeRouteUpdate (to, from, next) {
    let type = Number(to.params.type)
    if (!this.list || this.list.id !== type) {
      this.getList(type)
    }
    next()
  }
}
</script>

<style lang="scss" scoped>
</style>
