<template>
  <div class="l-page">
    <v-nav normal>
      <slot name="nav"/>
    </v-nav>
    <div class="l-page__content c-list-page l-flex--column">
      <div
        class="c-list-page__header l-flex__none"
        :class="{ 'is-short': isShort }"
      >
        <div class="c-list-page__title">{{ title }}</div>
        <div
          class="c-list-page__tip"
        ><slot name="tip">{{ tip }}</slot></div>
      </div>
      <div class="l-flex__stretch">
        <slot/>
        <v-button
          v-if="hasBtn"
          class="u-separate--farther"
          :text="button"
          :disable="disable"
          v-on="$listeners"
        />
        <slot name="extra"/>
      </div>
      <slot name="footer" class="l-flex__none"/>
    </div>
    <slot name="page"/>
  </div>
</template>

<script>
import { Icon } from 'vant'
import VButton from '@/components/VButton.vue'

export default {
  name: 'ListPage',
  components: {
    [ Icon.name ]: Icon,
    VButton
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    },
    button: {
      type: String,
      default: ''
    },
    disable: {
      type: [Boolean, String],
      default: false
    },
    short: {
      type: [Boolean, String],
      default: false
    }
  },
  computed: {
    hasTip () {
      return this.tip !== ''
    },
    hasBtn () {
      return this.button !== ''
    },
    isShort () {
      return this.short || this.hasTip
    }
  }
}
</script>

<style lang="scss" scoped>
.c-list-page {
  padding: 53px 75px 60px;

  &__header {
    margin-bottom: 88px;

    &.is-short {
      margin-bottom: 48px;
    }
  }

  &__title {
    font-size: 60px;
    color: $color--dark;
    line-height: 72px;
  }

  &__tip {
    margin-top: 40px;
    font-size: 24px;
    color: $color--dark;
    line-height: 36px;
    @include u-font;

    &:empty {
      margin-top: 0;
    }
  }
}
</style>
