<template>
  <div class="u-inline-block">
    <template v-if="url">
      <video
        v-if="isVideo"
        ref="video"
        class="is-cover"
        :src="url"
        preload
        poster
        controls
        v-on="$listeners"
      ></video>
      <img v-else class="is-cover" :src="url"/>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: {
    info: {
      type: Object,
      default: null
    },
    icon: {
      type: [Boolean, String],
      default: false
    },
    custom: {
      type: [Boolean, String],
      default: false
    }
  },
  computed: {
    ...mapGetters(['baseUrl']),
    url () {
      if (this.info) {
        if (this.custom) {
          return this.info.url
        } else if (this.icon) {
          if (this.info.iconCos) {
            return this.info.iconCos
          }
          if (this.info.icon && this.baseUrl) {
            return `${ this.baseUrl }${ this.info.icon }`
          }
        } else {
          if (this.info.cosUrl) {
            return this.info.cosUrl
          }
          if (this.info.url && this.baseUrl) {
            return `${ this.baseUrl }${ this.info.url }`
          }
        }
      }
      return null
    },
    isVideo () {
      return this.info && this.info.resourceType === 2
    }
  },
  watch: {
    $route () {
      if (this.isVideo) {
        this.$refs.video && this.$refs.video.pause()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
