<template>
  <div class="l-page">
    <v-nav :title="$t('explore.menu.title')"/>
    <div class="l-page__content" :class="{ 'l-flex--column': isEmpty }">
      <div v-if="isEmpty" class="c-tip">
        <img class="c-tip__img" src="@/assets/img-haibao.png"/>
        <div class="c-tip__tip">{{ $t('explore.menu.empty') }}</div>
      </div>
      <toggle v-for="(item, index) in list" :key="index" class="c-menu has-hairline--b" :title="item.name" :note="`${ item.menu.length }${ $t('explore.menu.count') }`">
        <div v-for="food in item.menu" :key="food.id" class="c-food has-hairline--b">
          <v-resource class="c-food__img" :info="food.picInfo"/>
          <div class="c-food__name is-tip--dark u-font--bold">{{ food.name }}</div>
        </div>
      </toggle>
    </div>
  </div>
</template>

<script>
import Toggle from '@/components/Toggle.vue'

export default {
  components: {
    Toggle
  },
  data () {
    return {
      list: null
    }
  },
  computed: {
    isEmpty () {
      return this.list !== null && this.list.length === 0
    }
  },
  created () {
    if (this.$route.params.id) {
      this.$http.post('/api/common/restaurant/menu/list', {
        entityId: Number(this.$route.params.id)
      }, {
        server: true
      }).then(data => {
        this.list = data.data.filter(item => item.menuList && item.menuList.length > 0).map(item => {
          return {
            name: item.sortName,
            menu: item.menuList
          }
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.c-food {
  display: flex;
  align-items: flex-start;
  padding: 32px 0;

  &:last-child {
    padding-bottom: 0;

    &::after {
      display: none;
    }
  }

  &__img {
    flex: none;
    width: 228px;
    height: 150px;
    border-radius: 16px;
    overflow: hidden;
  }

  &__name {
    flex: 1;
    padding: 8px 0 8px 32px;
  }
}
</style>
