<template>
  <div>
    <div
      v-if="hasTheme || isRestaurant"
      class="c-theme l-flex--row is-top has-padding--around has-hairline--b"
    >
      <row
        class="c-theme__content l-flex__auto"
        icon="menu"
        large
      >
        <div class="is-content--dark">{{ theme }}</div>
      </row>
      <div
        v-if="isRestaurant"
        class="c-theme__button l-flex__none"
      >
        <div class="o-button is-smaller is-hollow is-auto" @click="toMenu">{{ $t('explore.detail.dining.menu') }}</div>
      </div>
    </div>
    <div
      v-if="hasPriceInfo"
      class="has-padding--around has-hairline--b"
    >
      <row
        v-if="price.content"
        class="c-vlist-item--nearer"
        icon="price"
        large
      >
        <div class="is-content--dark u-flat">{{ price.content }}</div>
      </row>
      <div
        v-if="price.desc"
        class="c-vlist-item--nearer is-tip--dark u-font"
      >
        {{ price.desc }}
      </div>
    </div>
    <div
      v-if="hasMinCharge"
      class="has-padding--around has-hairline--b"
    >
      <div class="is-tip--dark">{{ $t('explore.detail.dining.charge') }}</div>
      <div class="is-content--dark u-font u-separate--nearer">{{ menu.minCharge }}</div>
    </div>
    <div
      v-if="hasPayType"
      class="has-padding--around has-hairline--b"
    >
      <div class="is-tip--dark">{{ $t('explore.detail.dining.pay') }}</div>
      <div class="is-content--dark u-font u-separate--nearer">{{ payType }}</div>
    </div>
    <div
      v-if="hasVipCardInfo"
      class="has-padding--around has-hairline--b"
    >
      <div class="is-tip--dark">{{ $t('explore.detail.dining.vip') }}</div>
      <div v-if="hasDiscountInfo" class="u-font">
        <div v-for="(item, index) in discountInfo" :key="index" class="l-inline-flex u-h-separate-reverse">
          <div class="o-icon--small is-circular" :class="{ 'is-silver': item.level === 1, 'is-gold': item.level === 2, 'is-platinum': item.level === 3 }"></div>
          <div class="is-content--dark u-h-separate--list">{{ item.name }} {{ item.discount }}</div>
        </div>
      </div>
      <div
        v-if="hasRemark"
        class="is-content--dark u-font u-separate--near"
      >
        {{ menu.vipcard.remark }}
      </div>
    </div>
    <div v-if="hasRecommend" class="has-padding--v has-hairline--t">
      <div class="is-tip--dark has-padding--h">{{ $t('explore.detail.dining.recommend') }}</div>
      <div class="l-hlist u-separate--near">
        <div class="l-hlist__wrapper">
          <div v-for="(item, index) in recommend" :key="index" class="c-hlist-item o-exhibit">
            <v-resource class="o-exhibit__resource" :info="item.picInfo"/>
            <div class="o-exhibit__name">{{ item.name }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Row from '@/components/RowIcon.vue'

export default {
  components: {
    Row
  },
  props: {
    entity: {
      type: Object,
      default () {
        return null
      }
    }
  },
  computed: {
    ...mapGetters(['baseUrl']),
    menu () {
      return this.entity.menu
    },
    theme () {
      let info = []
      if (this.menu.menuType) {
        info.push(this.menu.menuType)
      }
      if (this.menu.serviceType) {
        info = info.concat(this.menu.serviceType)
      }
      return info.join(' • ')
    },
    hasTheme () {
      return this.theme !== ''
    },
    isRestaurant () {
      return this.menu.restaurantType === 1
    },
    price () {
      return this.menu.price
    },
    hasPriceInfo () {
      return this.price && (this.price.content || this.price.desc)
    },
    hasMinCharge () {
      return this.menu.minCharge !== void 0 && this.menu.minCharge !== ''
    },
    payType () {
      let info = this.menu.payType && this.menu.payType.filter(type => type !== '') || []
      return info.join(', ')
    },
    hasPayType () {
      return this.payType !== ''
    },
    hasVipCard () {
      return this.menu.vipcard !== void 0
    },
    discountInfo () {
      return this.hasVipCard ? this.menu.vipcard.discountInfo : []
    },
    hasDiscountInfo () {
      return this.discountInfo.length > 0
    },
    hasRemark () {
      return this.hasVipCard && this.menu.vipcard.remark !== void 0 && this.menu.vipcard.remark !== ''
    },
    hasVipCardInfo () {
      return this.hasDiscountInfo || this.hasRemark
    },
    recommend () {
      return this.menu.recommendMenuList && this.menu.recommendMenuList.filter(item => {
        return item.name && item.picInfo
      }) || []
    },
    hasRecommend () {
      return this.recommend.length > 0
    }
  },
  methods: {
    toMenu () {
      this.$router.push({
        name: 'menu',
        params: {
          id: this.entity.id
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.c-theme {
  &__button {
    display: flex;
    align-items: center;
    width: 160px;
    height: 40px;
    margin-left: 10px;
  }
}

.is-silver {
  display: inline-block;
  background-color: #999999;
}

.is-gold {
  display: inline-block;
  background-color: #D8AE04;
}

.is-platinum {
  display: inline-block;
  background-color: #5C6E7C;
}
</style>
