<template>
  <div
    class="o-toggle-button"
    :class="{ 'is-disable': disable, 'is-smaller': small }"
    @click="onClick"
  >{{ text }}</div>
</template>

<script>
export default {
  props: {
    disable: {
      type: [Boolean, String],
      default: false
    },
    small: {
      type: [Boolean, String],
      default: false
    },
    text: {
      type: String,
      default: ''
    }
  },
  methods: {
    onClick () {
      if (!this.disable) {
        this.$emit('click')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.o-toggle-button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90px;
  font-size: 28px;
  color: $color-white;
  line-height: 36px;
  border-radius: 10px;
  background-color: $color-blue;

  &.is-disable {
    background-color: #99DDF3;
  }

  &.is-smaller {
    height: 72px;
    font-size: 26px;
  }
}
</style>
