<template>
  <div class="l-page">
    <v-nav/>
    <template v-if="url">
      <iframe class="l-flex__fill" :src="url"></iframe>
    </template>
  </div>
</template>

<script>
export default {
  computed: {
    url () {
      let url = this.$route.query.url
      return url ? decodeURIComponent(url) : ''
    }
  }
}
</script>

