<template>
  <div class="l-page c-preorder">
    <v-nav :title="$t('ticket.buy.confirm.title')"/>
    <div class="l-page__content">
      <template v-if="hasPriceInfo">
        <order :order="order"/>
        <div
          v-if="requiredInfo.length > 0"
          class="has-separate--t"
        >
          <div
            v-if="!alone"
            class="is-mini--dark u-font has-padding--large has-hairline--separate"
          >
            {{ $t('ticket.buy.confirm.tip') }}
          </div>
          <required-block
            v-for="(info, index) in requiredInfo"
            :key="index"
            class="has-hairline--separate"
            :info="info"
            :alone="alone"
            @link="toLink"
          />
        </div>
        <div class="c-block--small has-separate--t">
          <field
            v-model="coupon"
            maxlength="24"
            :title="$t('ticket.buy.confirm.coupon')"
            :placeholder="$t('ticket.buy.confirm.hcoupon')"
            :tip="$t('ticket.buy.confirm.tcoupon')"
            :readonly="couponChecking || couponChecked"
            :error="couponError"
          >
            <template #msg v-if="couponChecking || couponInfo">
              <template v-if="couponChecking">
                <van-loading class="o-coupon-loading"/>
                <span class="u-h-separate--nearest">{{ $t('ticket.buy.confirm.loading') }}</span>
              </template>
              <div
                v-else
                class="o-valid-coupon"
              >
                {{ $t('ticket.buy.confirm.ucoupon') }}{{ couponInfo }}
              </div>
            </template>
            <div
              class="is-tip--blue u-font--bold u-h-separate--near"
              :class="{ 'u-color': !coupon }"
              @click="toCheckOrClear"
            >
              {{ couponButton }}
            </div>
          </field>
        </div>
        <div class="has-padding--around has-separate--t">
          <van-checkbox
            class="o-checkbox"
            v-model="checked"
            checked-color="#00A9E0"
          >
            {{ $t('ticket.buy.confirm.agree') }}<span class="u-color--blue">{{ $t('ticket.buy.confirm.agree1') }}</span>
          </van-checkbox>
        </div>
      </template>
    </div>
    <div
      v-if="order"
      class="c-fixed-footer c-footer has-hairline--tb l-flex--row is-top"
    >
      <div class="l-flex__fill u-font--bold">
        <div class="c-footer__price">{{ order.total }}</div>
        <div class="is-mini l-flex--row">
          {{ count }}{{ $t('ticket.buy.count') }}
          <template v-if="order.subtotal">
            <span class="o-sp is-noraml"></span><span class="u-line-through">{{ order.subtotal }}</span>
          </template>
        </div>
      </div>
      <v-button
        class="c-footer__button l-flex__none"
        :text="$t('ticket.buy.confirm.pay')"
        :disable="disable"
        small
        @click="toPreConfirm"
      />
    </div>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="dialogMsg.title"
      :message="dialogMsg.content"
      :confirm-button-text="$t('btn.cont')"
      :cancel-button-text="$t('btn.cancel')"
      show-cancel-button
      @confirm="toPay"
    />
    <transition name="slide-wrapper">
      <router-view style="z-index: 9;" :content="linkContent"/>
    </transition>
  </div>
</template>

<script>
import { sortByIndex } from '@/utils/sort.js'
import DialogMixin from '@/components/mixins/dialog.js'
import { Loading, Checkbox } from 'vant'
import VButton from '@/components/VButton.vue'
import Order from '@/components/ticket/Order.vue'
import Field from '@/components/list/Field.vue'
import RequiredBlock from '@/components/ticket/RequiredBlock.vue'

export default {
  mixins: [ DialogMixin ],
  components: {
    [ Loading.name ]: Loading,
    [ Checkbox.name ]: Checkbox,
    VButton,
    Order,
    Field,
    RequiredBlock
  },
  props: {
    tickets: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      priceInfo: null,
      coupon: '',
      couponChecking: false,
      couponChecked: false,
      couponError: '',
      couponInfo: '',
      checked: false,
      requiredInfo: [],
      linkContent: ''
    }
  },
  computed: {
    count () {
      return this.tickets.reduce((total, ticket) => total + ticket.count, 0)
    },
    hasPriceInfo () {
      return this.priceInfo !== void 0 && this.priceInfo !== null
    },
    currency () {
      return this.hasPriceInfo && this.priceInfo.unit || ''
    },
    order () {
      if (this.hasPriceInfo) {
        let data = {
          list: this.tickets,
          total: `${ this.currency } ${ this.priceInfo.realPrice.toFixed(2) }`
        }
        if (this.priceInfo.couponInfo) {
          data.subtotal = `${ this.currency } ${ this.priceInfo.price.toFixed(2) }`
          data.discount = `- ${ this.currency } ${ this.priceInfo.discount.toFixed(2) }`
          data.total = `${ this.currency } ${ this.priceInfo.realPrice.toFixed(2) }`
        } else {
          data.total = `${ this.currency } ${ this.priceInfo.price.toFixed(2) }`
        }
        return data
      }
      return null
    },
    ticketIds () {
      return this.tickets.map(ticket => {
        return {
          ticketId: ticket.ticketId,
          count: ticket.count
        }
      })
    },
    couponButton () {
      return this.couponChecked ? this.$t('ticket.buy.confirm.ccoupon') : this.$t('ticket.buy.confirm.ucoupon')
    },
    disable () {
      return this.couponChecking || this.requiredInfo.findIndex(item => !this.checkValid(item)) > -1
    },
    alone () {
      return this.requiredInfo.length === 1 && this.requiredInfo[0].isBasic
    }
  },
  methods: {
    showErr (item, target) {
      let i18nCode = `ticket.buy.confirm.errMsg.${ item.code }`
      switch (item.code) {
        case -1:
        case -2:
        case -6:
          this.$toast(this.$t(i18nCode, {
            name: item.from.name
          }))
          break
        case -4:
          this.$toast(this.$t(i18nCode, {
            name: item.from.name,
            count: item.from.minLen
          }))
          break
        case -5:
          this.$toast(this.$t(i18nCode, {
            name: item.from.name,
            count: item.from.maxLen
          }))
          break
        case -100:
        case -101:
          this.$toast(this.$t(i18nCode, {
            name: target.name
          }))
          break
        default:
          this.$toast(this.$t(i18nCode))
          break
      }
    },
    setDefaultData (item) {
      let data = {
        from: item,
        value: ''
      }

      switch (item.valueType) {
        case 'input':
          item.minLen = item.minLen || 0
          item.maxLen = item.maxLen || 255
          if (item.id === 'phone') {
            item.isPhone = true
            data.areaCode = ''
          }
          break
        default:
          break
      }

      data.required = item.required === false ? false : true
      data.code = data.required ? item.valueType === 'input' ? -1 : -2 : 1

      if (item.extInfo) {
        data.hasExtInfo = true
        data.extInfo = this.setDefaultData(item.extInfo)
      } else {
        data.hasExtInfo = false
      }

      return data
    },
    transformInfo (info) {
      return info.list.map((item, index) => {
        return this.setDefaultData(item, index)
      })
    },
    getFormList (info) {
      if (info.id === 1) {
        return [{
          list: this.transformInfo(info)
        }]
      } else if (info.count !== void 0) {
        let arr = []
        for (let i = 0; i < info.count; i++) {
          arr.push({
            name: `${ info.subName } ${ i + 1 }`,
            list: this.transformInfo(info)
          })
        }
        return arr
      }
      return []
    },
    checkValid (data) {
      if (data.hasUserData && data.userData.code !== 1) {
        // this.showErr(data.userData, data)
        return false
      }

      if (data.hasForm) {
        let form = data.form
        for (let i = 0, j = form.length; i < j; i++) {
          let list = form[i].list
          for (let n = 0, m = list.length; n < m; n++) {
            let item = list[n]
            if (item.code !== 1) {
              // this.showErr(item)
              return false
            }

            if (item.hasExtInfo && item.extInfo.code !== 1) {
              // this.showErr(item.extInfo)
              return false
            }
          }
        }
      }

      return true
    },
    transformRequired (info, index = 0) {
      let { id, ticketId, name } = info
      let data = {
        id, ticketId, name,
        index: index + 1,
        isBasic: info.id === 1
      }

      // 基本资料不处理userData
      if (!data.isBasic && info.userData) {
        data.hasUserData = true
        data.userData = {
          from: info.userData,
          date: '', time: '', code: -100
        }
      } else {
        data.hasUserData = false
      }

      if (info.list && info.list.length > 0) {
        info.list.sort(sortByIndex)
        data.form = this.getFormList(info)
        data.hasForm = data.form.length > 0
      } else {
        data.hasForm = false
      }

      return data
    },
    preorder (coupon) {
      let data = this.getOrderInfo()
      if (coupon) {
        data.couponType = 1
        data.couponCode = coupon
      }
      return this.$http.post('/api/common/ticket/preorder', data, {
        login: true,
        loading: !coupon,
        server: !coupon
      })
    },
    getOrderInfo () {
      return {
        currency: this.tickets[0].currency,
        ticketIds: this.ticketIds
      }
    },
    toCheckOrClear () {
      if (!this.coupon || this.couponChecking) {
        return
      }

      if (this.couponChecked) {
        this.coupon = ''
        this.couponChecked = false
        this.couponError = ''
        this.couponInfo = ''
        this.priceInfo = {
          ...this.$priceInfo
        }
        return
      }

      this.couponChecking = true

      this.preorder(this.coupon).then(data => {
        let { priceInfo, couponInfo } = data
        if (couponInfo) {
          if (couponInfo.status === 1) {
            this.couponInfo = couponInfo.name
            priceInfo.couponInfo = couponInfo
            this.priceInfo = priceInfo
          } else {
            this.couponError = this.$t('ticket.buy.confirm.ecoupon')
          }
          return true
        }
        return false
      }, err => {
        if (!err.system) {
          this.couponError = err.msg
          return true
        }
        return false
      }).then(result => {
        this.couponChecked = result
        this.couponChecking = false
      })
    },
    getItemValue (item) {
      if (item.from.valueType === 'input' && item.from.isPhone) {
        return `${ item.areaCode }${ item.value }`
      }
      return item.value
    },
    transformItemToValue (item) {
      var data = {
        id: item.from.id,
        value: this.getItemValue(item)
      }

      if (item.hasExtInfo) {
        data.extValue = this.getItemValue(item.extInfo)
      }

      return data
    },
    normalize (id, userData, list) {
      return {
        id,
        value: {
          ...userData,
          list
        }
      }
    },
    transformRequiredToValue (item) {
      let { id, ticketId, hasUserData, hasForm } = item

      let userData = hasUserData ? {
        date: item.userData.date,
        time: item.userData.time
      } : null

      let data = {
        required: (hasForm ? item.form.map(block => block.list.map(this.transformItemToValue)) : [ null ]).map(val => this.normalize(id, userData, val || []))
      }

      if (ticketId) {
        data.ticketId = ticketId
      }

      return data
    },
    normalizeTicket (item, arr) {
      let { ticketId, count: ticketCount } = item

      let info = {
        ticketId, ticketCount
      }

      let requiredInfo = arr.find(item => item.ticketId === ticketId)
      if (requiredInfo) {
        info.required = requiredInfo.required
      }

      return info
    },
    toPreConfirm () {
      if (!this.checked) {
        this.$toast(this.$t('ticket.buy.confirm.agreement'))
        return
      }

      let data = {
        currency: this.priceInfo.id
      }

      if (this.requiredInfo[0] && this.requiredInfo[0].isBasic) {
        let basic = this.requiredInfo[0]
        data.required = this.normalize(basic.id, null, basic.form[0].list.map(this.transformItemToValue))
      }

      let tickets = this.requiredInfo.filter(item => !item.isBasic)
      let requiredInfos = tickets.map(this.transformRequiredToValue)
      data.tickets = this.tickets.map(ticket => this.normalizeTicket(ticket, requiredInfos))

      if (this.priceInfo.couponInfo) {
        data.couponCode = this.coupon
      }

      this.$requiredInfo = data
      this.$log(this.$requiredInfo)

      let times = tickets.filter(item => item.hasUserData)
      if (times.length > 1) {
        let first = times[0].userData.date

        if (times.findIndex(item => item.userData.date !== first) > -1) {
          this.dialogMsg.title = ''
          this.dialogMsg.content = this.$t('ticket.buy.confirm.across')
          this.dialogMsg.show = true
          return
        }

        times = times.map(item => {
          let date = item.userData.date.replace(/-/g, '/')
          let time = item.userData.time.split('-')
          return {
            name: item.name,
            startTime: new Date(`${ date } ${ time[0] }`).getTime(),
            endTime: new Date(`${ date } ${ time.length > 1 ? time[1] : time[0] }`).getTime(),
            time: item.userData.time
          }
        }).sort(this.sortByStartTime)

        let arr = []
        let cursor = 0
        for (let i = 1, j = times.length; i < j; i++) {
          if (this.isNear(times[i - 1], times[i])) {
            while (cursor <= i) {
              arr.push(`${ times[cursor].name }: ${ times[cursor].time }`)
              cursor += 1
            }
          } else {
            cursor = i
          }
        }

        if (arr.length > 0) {
          this.dialogMsg.title = this.$t('ticket.buy.confirm.conflict')
          this.dialogMsg.content = arr.join('\n')
          this.dialogMsg.show = true
          return
        }
      }

      this.toPay()
    },
    toPay () {
      this.$http.post('/api/common/user/ticket/order', this.$requiredInfo, {
        login: true,
        loading: true,
        server: true
      }).then(({ orderId }) => {
        this.$router.replace({
          name: 'payment',
          params: {
            id: orderId
          }
        })
      })
    },
    sortRequired (a, b) {
      return a.id === 1 ? -1 : b.id === 1 ? 1 : a.sortIndex - b.sortIndex
    },
    sortByStartTime (a, b) {
      return a.startTime - b.startTime
    },
    isNear (a, b) {
      if (Math.abs(a.startTime - b.endTime) <= 3600000) {
        return true
      }

      if (Math.abs(a.endTime - b.startTime) <= 3600000) {
        return true
      }

      return false
    },
    toLink (linkContent) {
      this.linkContent = linkContent
      this.$router.push({
        name: 'ticket-pre-order-link'
      })
    }
  },
  created () {
    if (this.tickets && this.tickets.length > 0) {
      this.preorder().then(data => {
        this.$priceInfo = data.priceInfo
        this.priceInfo = {
          ...data.priceInfo
        }

        if (data.required && data.required.length > 0) {
          this.requiredInfo = data.required.sort(this.sortRequired).map(this.transformRequired)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.c-block--small {
  padding: 32px 40px 24px;
}

.c-footer {
  &__price {
    font-size: 42px;
    color: $color-orange;
    line-height: 50px;
  }

  &__button {
    min-width: 300px;
  }
}

.o-coupon-loading {
  display: inline-flex;
  align-items: center;
  width: 24px;
  height: 24px;
  color: $color;
}

.o-valid-coupon {
  color: #00C5A0;
}

.c-preorder {
  /deep/ .van-dialog.c-dialog {
    left: 95px;
    right: 95px;
    width: auto;
    transform: translate(0, -50%);

    .van-dialog__header {
      padding-bottom: 24px;
      font-size: 30px;
      color: $color-black;
      line-height: 44px;
      @include u-font;
    }

    .van-dialog__message {
      max-height: 400px;
      color: $color-black;
      overflow-y: auto;
    }
  }
}
</style>
