<template>
  <div class="l-page">
    <v-nav :title="$t('ai.title')"/>
    <div class="l-page__content l-flex--column">
      <router-link
        tag="div"
        class="o-photograph"
        :to="{ name: 'photograph' }"
      >
        <van-icon name="photograph"/>
        <div class="o-help" :class="{ 'is-hide': hide }">{{ $t('ai.help') }}</div>
      </router-link>
      <van-tabs
        class="l-flex__fill c-nav-tabs c-nav-tabs--options"
        type='none'
        v-model="tabIndex"
        :ellipsis="false"
        swipeable
        animated
        @change="switchTab"
      >
        <van-tab
          v-for="(tab, index) in tabs"
          :key="tab.id"
        >
          <template #title>
            <div
              class="o-option"
              :class="{ 'is-selected': index === tabIndex }"
            >
              {{ tab.name }}
            </div>
          </template>
          <div
            :ref="tab.tabId"
            class="c-list--2c has-padding--around"
          >
            <div
              v-if="tab.loading && tab.total === 0"
              class="is-cover u-align-center"
            >
              <van-loading type="spinner"/>
            </div>
            <div
              v-if="!tab.loading && tab.total === 0 && tab.timestamp !== 0"
              class="is-tip--dark"
            >
              {{ $t('common.empty') }}
            </div>
            <template v-if="tab.total > 0">
              <animal
                v-for="(item, index) in tab.list"
                :key="index"
                class="c-list--2c__item"
                :entity="item"
                large
              />
            </template>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import LocaleMixin from '@/components/mixins/locale.js'
import { Tabs, Tab, Icon, Loading } from 'vant'
import Animal from '@/components/map/Animal.vue'

export default {
  mixins: [ LocaleMixin ],
  components: {
    [ Tabs.name ]: Tabs,
    [ Tab.name ]: Tab,
    [ Icon.name ]: Icon,
    [ Loading.name ]: Loading,
    Animal
  },
  data () {
    return {
      hide: false,
      tabIndex: -1,
      tabs: []
    }
  },
  methods: {
    switchTab (index) {
      this.tabIndex = index
      let tab = this.tabs[this.tabIndex]
      if (tab) {
        this.getList()
      }
    },
    isDiff (nVal) {
      let length = this.tabs.length
      let nLength = nVal.length

      if (length === 0 && nLength > 0 || length !== nLength) {
        return true
      }

      for (let i = 0; i < length; i++) {
        if (this.tabs[i].id !== nVal[i].id) {
          return true
        }
      }

      return false
    },
    getTabs () {
      this.$http.post('/api/common/dic/values', {
        typeId: 'animalType'
      }).then(data => {
        if (this.isDiff(data)) {
          this.tabs = data.map(type => {
            return {
              tabId: `tab_${ type.id }`,
              timestamp: 0,
              loading: false,
              total: 0,
              list: [],
              ...type
            }
          })
          this.switchTab(Math.max(0, Math.min(this.tabIndex, this.tabs.length - 1)))
        }
      })
    },
    getList () {
      let tab = this.tabs[this.tabIndex]
      if (!tab.loading) {
        tab.loading = true
        this.$http.post('/api/common/entity/list/animal', {
          typeId: tab.id
        }).then(data => {
          if (tab.timestamp !== data.updateTime) {
            tab.timestamp = data.updateTime
            tab.list = data.data
            tab.total = data.data.length
          }
        }).finally(() => tab.loading = false)
      }
    },
    onLocaleUpdated () {
      this.tabs = []
      this.tabIndex = 0
    }
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  },
  activated () {
    this.hide = false
    this.$timer = setTimeout(() => this.hide = true, 3000)

    if (this.tabs.length > 0) {
      this.tabs.forEach(tab => {
        if (tab.scrollTop) {
          let dom = this.$refs[tab.tabId]
          if (dom) {
            dom[0].parentNode.parentNode.scrollTop = tab.scrollTop
          }
          tab.scrollTop = 0
        }
      })
    }

    this.getTabs()
    this.switchTab(this.tabIndex)
  },
  deactivated () {
    this.tabs.forEach(tab => {
      let dom = this.$refs[tab.tabId]
      if (dom) {
        tab.scrollTop = dom[0].parentNode.parentNode.scrollTop
      }
    })
    clearTimeout(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.o-photograph {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 106px;
  height: 106px;
  color: $color-blue;
  background-color: $color-white;
  z-index: 99;
}

.o-help {
  position: absolute;
  left: 50%;
  bottom: 10px;
  padding: 24px 32px;
  font-size: 28px;
  color: $color-white;
  line-height: 34px;
  white-space: nowrap;
  transform: translate(-32px, 100%);
  border-radius: 16px;
  background-color: rgba($color-black, .7);
  pointer-events: none;

  &.is-hide {
    display: none;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 32px;
    transform: translate(-50%, -100%);
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-bottom: 14px solid rgba($color-black, .7);
  }
}

.c-nav-tabs--options {
  /deep/ .van-tabs__wrap {
    display: flex;
    align-items: center;
    height: 106px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0 , .1);
  }

  /deep/ .van-tab {
    padding: 0;
    margin-left: 16px;

    &:first-child {
      margin-left: 126px;
    }

    &:last-child {
      padding-right: 40px;
    }
  }
}
</style>
