<template>
  <list-page
    :title="$t('common.verify.code.title')"
  >
    <template #tip>
      <div>
        {{ $t('common.verify.code.tip') }}<span class="u-font--bold">{{ email }}</span>
      </div>
    </template>
    <div class="c-code" @click="showKeyboard = true">
      <div
        v-for="(msg, index) in codeMsg"
        :key="index"
        class="c-hlist-item--near c-code__item"
        :class="{ 'is-selected': index === codeIndex }"
      >
        {{ msg }}
      </div>
    </div>
    <van-number-keyboard
      :show="showKeyboard"
      @input="onInput"
      @delete="onDelete"
      @blur="showKeyboard = false"
    />
    <div class="is-sub u-pre-wrap u-align-center u-separate--farther">
      {{ $t('common.verify.code.retry') }}
      <span
        :class="{ 'u-color': isSended, 'u-color--blue': !isSended }"
        @click="resend"
      >
        {{ $t('common.verify.code.send') }}{{ countMsg }}
      </span>
    </div>
  </list-page>
</template>

<script>
import { save } from '@/utils/auth.js'
import { NumberKeyboard } from 'vant'
import ListPage from '@/components/list/ListPage.vue'

export default {
  components: {
    [ NumberKeyboard.name ]: NumberKeyboard,
    ListPage
  },
  data () {
    return {
      showKeyboard: false,
      type: '',
      email: '',
      activeExpire: 0,
      code: '',
      count: 0
    }
  },
  computed: {
    isSended () {
      return this.count > 0
    },
    countMsg () {
      if (this.count > 0) {
        let minutes = Math.floor(this.count / 60)
        if (minutes < 10) {
          minutes = `0${ minutes }`
        }
        let seconds = this.count % 60
        if (seconds < 10) {
          seconds = `0${ seconds }`
        }
        return `(${ minutes }:${ seconds })`
      }
      return ''
    },
    codeIndex () {
      return this.showKeyboard ? this.code.length : -1
    },
    codeMsg () {
      let arr = []
      for (let i = 0; i < 4; i++) {
        arr[i] = i === this.codeIndex ? '' : this.code[i] || '-'
      }
      return arr
    }
  },
  methods: {
    resend () {
      if (this.email && this.count === 0) {
        let url
        switch (this.type) {
          case 'sign':
            url = '/api/app/user/email/register/reSend'
            break
          case 'forget':
            url = '/api/app/user/email/resetPwd/reSend'
            break
          case 'bind':
            url = '/api/app/user/email/bind/reSend'
            break
          default:
            return
        }
        this.code = ''
        this.count = -1
        this.$http.post(url, {
          email: this.email
        }, {
          login: this.type === 'bind',
          server: true
        }).then(({ activeExpire }) => {
          this.activeExpire = activeExpire
          this.toCount()
        }, () => this.count = 0)
      }
    },
    onInput (key) {
      if (this.showKeyboard && this.code.length < 4) {
        this.code += key
        if (this.code.length === 4) {
          this.showKeyboard = false
          this.toVerify()
        }
      }
    },
    onDelete () {
      if (this.showKeyboard) {
        this.code = this.code.slice(0, this.code.length - 1)
      }
    },
    toCount () {
      this.count = Math.floor(Math.max(this.activeExpire - Date.now(), 0) / 1000)
      if (this.count > 0) {
        this.$timer = setTimeout(this.toCount, 1000)
      }
    },
    toSign () {
      this.$http.post('/api/app/user/email/active', {
        email: this.email,
        code: this.code
      }, {
        loading: true,
        server: true
      }).then(data => {
        save(3, data)
        this.$router.replace({
          name: 'verify-phone',
          query: {
            skip: 1
          }
        })
      }, () => this.code = '')
    },
    toForget () {
      this.$http.post('/api/app/user/email/resetPwd/active', {
        email: this.email,
        code: this.code
      }, {
        loading: true,
        server: true
      }).then(data => {
        this.$router.replace({
          name: 'forget-reset',
          query: {
            userIdType: data.userIdType,
            userId: data.userId,
            email: encodeURIComponent(data.email),
            token: data.token
          }
        })
      }, () => this.code = '')
    },
    toBind () {
      this.$http.post('/api/app/user/email/bind/active', {
        email: this.email,
        code: this.code
      }, {
        login: true,
        loading: true,
        server: true
      }).then(() => {
        this.$router.replace({
          name: 'add-email-done'
        })
      }, () => this.code = '')
    },
    toVerify () {
      if (this.email) {
        switch (this.type) {
          case 'sign':
            this.toSign()
            break
          case 'forget':
            this.toForget()
            break
          case 'bind':
            this.toBind()
            break
          default:
            break
        }
      }
    }
  },
  created () {
    this.$timer = -1
    let { type, email = '', expire = 0 } = this.$route.query
    this.type = type
    this.email = decodeURIComponent(email)
    this.activeExpire = Number(expire)
    this.toCount()
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.c-code {
  display: flex;
  justify-content: center;
  height: 138px;

  &__item {
    flex: 1;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 138px;
    font-size: 72px;
    color: $color--dark;
    border-radius: 8px;
    border: 2px solid $color-separate;

    &.is-selected {
      border: 4px solid $color-blue;
    }
  }
}
</style>
