<template>
  <div
    class="o-field"
    :class="{ 'is-error': isError, 'is-focus': focus }"
  >
    <div class="o-field__title">{{ titleMsg }}</div>
    <div
      class="o-field__content l-flex--row"
      @click.stop="onClick"
    >
      <input
        class="o-field__input l-flex__fill"
        :type="inputType"
        :placeholder="placeholder"
        :value="value"
        :maxlength="maxlength"
        :readonly="readonly"
        @input="onInput"
        @focus="onFocus"
        @blur="onBlur"
      />
      <van-icon
        v-if="showPlain"
        class="o-field__icon is-eye u-h-separate--near"
        :class="{ 'u-color--blue': plain }"
        name="eye-o"
        @click.stop="plain = !plain"
      />
      <van-icon
        v-if="clickable"
        class="o-field__icon u-h-separate--near"
        name="arrow-down"
      />
      <slot/>
    </div>
    <div class="o-field__tip has-hairline--t l-flex--row">
      <slot name="msg">{{ msg }}</slot>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant'

export default {
  components: {
    [ Icon.name ]: Icon
  },
  props: {
    type: {
      type: String,
      default: 'text'
    },
    value: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    },
    error: {
      type: String,
      default: ''
    },
    maxlength: {
      type: [String, Number],
      default: 32
    },
    readonly: {
      type: [Boolean, String],
      default: false
    },
    clickable: {
      type: [Boolean, String],
      default: false
    }
  },
  data () {
    return {
      focus: false,
      plain: false
    }
  },
  computed: {
    titleMsg () {
      return this.value ? this.title : ''
    },
    isError () {
      return !!this.error
    },
    msg () {
      return this.error || this.tip
    },
    isPassword () {
      return this.type === 'password'
    },
    inputType () {
      return this.plain ? 'text' : this.type
    },
    showPlain () {
      return this.isPassword && !!this.value
    }
  },
  methods: {
    onInput (e) {
      this.$emit('input', e.target.value)
    },
    onClick () {
      if (this.clickable) {
        this.$emit('click')
      }
    },
    onFocus () {
      this.focus = true
    },
    onBlur () {
      this.focus = false
      this.$emit('blur')
    }
  }
}
</script>

<style lang="scss" scoped>
.o-field {
  &.is-error {
    .o-field__title, .o-field__tip {
      color: $color--error;
    }

    .o-field__tip::after {
      border-color: $color--error;
    }
  }

  &.is-focus {
    .o-field__title {
      color: $color-blue;
    }

    .o-field__tip::after {
      border-top-width: 4px;
      border-color: $color-blue;
    }
  }

  &__title {
    height: 40px;
    font-size: 24px;
    color: $color--dark;
    line-height: 40px;
  }

  &__content {
    height: 40px;
  }

  &__input {
    height: 40px;
    font-size: 32px;
    color: $color--dark;
    line-height: 40px;
    border: none;
    outline: none;
    @include u-font--bold;

    &::-webkit-input-placeholder {
      color: $color;
    }
  }

  &__tip {
    margin-top: 12px;
    min-height: 40px;
    font-size: 24px;
    color: $color;
    line-height: 40px;
    @include u-font;
  }

  &__icon {
    font-size: 32px;
    color: $color;

    &.is-eye {
      font-size: 48px;
    }
  }
}

[readonly="readonly"] {
  pointer-events: none;
}
</style>
