<template>
  <div class="l-page">
    <v-nav :title="$t('common.mail.title')"/>
    <div class="l-flex--row has-shadow--short u-background--blue">
      <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 0 }" @click="tabIndex = 0">{{ $t('common.mail.account') }}</div>
      <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 1 }" @click="tabIndex = 1">{{ $t('common.mail.park') }}</div>
    </div>
    <div class="l-hscroll l-flex__fill">
      <div class="l-hscroll__wrapper" :class="{ 'is-second': tabIndex === 1 }">
        <div class="l-hscroll__item u-scroll-y">
          <div
            v-for="(mail, index) in userMails"
            :key="index"
            class="c-mail has-padding--around has-hairline--b"
            :class="{ 'is-fresh': mail.isFresh }"
            @click="readMail(mail)"
          >
            <div class="is-tip--dark u-font u-pre-wrap">{{ mail.origin.msgContent }}</div>
            <div class="u-separate--nearer">{{ transformDate(mail.origin.addTime) }}</div>
          </div>
        </div>
        <div class="l-hscroll__item u-scroll-y">
          <div
            v-for="(mail, index) in parkMails"
            :key="index"
            class="c-mail has-padding--around has-hairline--b"
            :class="{ 'is-fresh': mail.isFresh }"
            @click="readMail(mail)"
          >
            <div class="is-tip--dark u-font">{{ mail.origin.msgTitle }}</div>
            <div class="u-separate--nearer">{{ transformDate(mail.origin.addTime) }}</div>
          </div>
        </div>
      </div>
    </div>
    <transition name="slide-wrapper">
      <router-view style="z-index: 9;"/>
    </transition>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { initMails } from '@/utils/common.js'
import { dateFormat } from '@/utils/format.js'
import StepMixin from '@/components/mixins/step.js'

export default {
  mixins: [ StepMixin ],
  data () {
    return {
      tabIndex: 0,
      route: 'mail'
    }
  },
  computed: {
    ...mapState(['userMails', 'parkMails']),
    hasUserMails () {
      return this.userMails.length > 0
    },
    hasParkMails () {
      return this.parkMails.length > 0
    }
  },
  methods: {
    readMail (mail) {
      this.$features.readMail(mail)
    },
    transformDate (time) {
      let diff = Math.ceil((Date.now() - time) / 60000)
      if (diff >= 60) {
        diff = Math.floor(diff / 60)
        if (diff >= 24) {
          return dateFormat(time, this.$t('common.mail.time'))
        }
        return this.$t('common.mail.hour', { hour: diff })
      } else {
        return this.$t('common.mail.minute', { minute: diff })
      }
    }
  },
  created () {
    initMails()
  }
}
</script>

<style lang="scss" scoped>
.c-mail {
  @include is-mini;

  &.is-fresh {
    color: $color-blue;
    background-color: rgba($color-blue, .1);
  }
}
</style>
