<template>
  <div class="l-page">
    <div class="l-page__header--primary">
      <div class="l-page__title">{{ $t('tabbar.park') }}</div>
    </div>
    <div class="l-flex--row has-shadow--short u-background--blue">
      <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 0 }" @click="switchTab(0)">{{ $t('park.timing') }}</div>
      <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 1 }" @click="switchTab(1)">{{ $t('park.ticket') }}</div>
    </div>
    <div class="l-hscroll l-flex__fill">
      <div class="l-hscroll__wrapper" :class="{ 'is-second': tabIndex === 1 }">
        <div class="l-hscroll__item l-flex--column">
          <template v-if="hasOpening">

            <div class="has-padding--v has-shadow--short l-flex__none">
              <div class="is-tip has-padding--h">
                <span :class="{ 'u-color--dark': startOpeningDate === openingYm, 'u-font--bold': startOpeningDate !== openingYm }">{{ startOpeningDate }}</span>
                <span
                  v-if="startOpeningDate !== endOpeningDate"
                  class="u-h-separate"
                  :class="{ 'u-color--dark': endOpeningDate === openingYm, 'u-font--bold': endOpeningDate !== openingYm }"
                >
                  {{ endOpeningDate }}
                </span>
              </div>
              <div class="l-hlist u-separate--far">
                <div class="l-hlist__wrapper">
                  <div
                    v-for="(item, index) in parkInfo.days"
                    :key="index" class="c-hlist-item--far o-tag"
                    :class="{ 'is-primary': index === parkInfo.index }"
                    @click="changeDay(index)"
                  >
                    <div class="t2">{{ item.day }}</div>
                    <div class="s2 u-font--bold">{{ item.week }}</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="l-flex__fill u-scroll-y">
              <template v-if="selectedDay.opening">
                <div class="has-padding--around has-separate--t has-hairline--b">
                  <div class="is-tip--dark">{{ selectedDay.opening.date }}</div>
                  <div class="u-separate">
                    <div class="is-title">{{ $t('park.parko') }}</div>
                    <div class="is-content--dark u-font u-separate--nearest">{{ selectedDay.opening.opening }}</div>
                  </div>
                  <div class="u-separate--near">
                    <div class="is-title">{{ $t('park.parkho') }}</div>
                    <div class="is-content--dark u-font u-separate--nearest">{{ selectedDay.opening.hCloseTime }}</div>
                  </div>
                  <div class="u-separate--near">
                    <div class="is-title">{{ $t('park.parkingo') }}</div>
                    <div class="is-content--dark u-font u-separate--nearest">{{ selectedDay.opening.parkingOpening }}</div>
                  </div>
                </div>
                <toggle
                  v-if="selectedDay.opening.waterfront.length > 0"
                  class="has-hairline--b" :title="$t('park.waterfront')"
                  far
                >
                  <div class="u-separate">
                    <div
                      v-for="(item, index) in selectedDay.opening.waterfront"
                      :key="index"
                      class="c-vlist-item--near"
                    >
                      <div class="is-title">{{ item.name }}</div>
                      <div class="is-content--dark u-font u-separate--nearest">{{ item.value }}</div>
                    </div>
                  </div>
                </toggle>
                <toggle
                  v-if="selectedDay.opening.peakpark.length > 0"
                  class="has-hairline--b"
                  :title="$t('park.peakpark')"
                  far
                >
                  <div class="u-separate">
                    <div v-for="(item, index) in selectedDay.opening.peakpark" :key="index" class="c-vlist-item--near">
                      <div class="is-title">{{ item.name }}</div>
                      <div class="is-content--dark u-font u-separate--nearest">{{ item.value }}</div>
                    </div>
                  </div>
                </toggle>
                <toggle
                  v-if="selectedDay.opening.maintenanceList.length > 0"
                  class="has-hairline--b"
                  :title="$t('park.maintenance')"
                  far
                >
                  <div class="u-separate">
                    <div v-for="(item, index) in selectedDay.opening.maintenanceList" :key="index" class="c-vlist-item--near">
                      <div class="is-title">{{ item.name }}</div>
                      <div class="is-content--dark u-font u-separate--nearest" :class="{ 'u-color--error': item.isClosed }">{{ item.value }}</div>
                    </div>
                  </div>
                </toggle>
              </template>
              <template v-else>
                <div
                  v-if="selectedDay.loading"
                  class="has-padding--around has-separate--t u-text-center"
                >
                  <van-loading type="spinner"/>
                </div>
                <div
                  v-else
                  class="is-tip--dark has-padding--around has-separate--t"
                >
                  {{ $t('common.empty') }}
                </div>
              </template>
            </div>
          </template>
        </div>
        <div class="l-hscroll__item l-flex--column">
          <template v-if="vip.list.length === 0">
            <div
              v-if="vip.loading"
              class="has-padding--around u-text-center"
            >
              <van-loading type="spinner"/>
            </div>
            <div
              v-else
              class="is-tip--dark has-padding--around"
            >
              {{ $t('common.empty') }}
            </div>
          </template>
          <template v-else>
            <div class="l-hlist has-padding--v has-shadow--short l-flex__none">
              <div class="l-hlist__wrapper">
                <div
                  v-for="(item, index) in vip.list"
                  :key="index"
                  class="c-hlist-item--near o-option"
                  :class="{ 'is-selected': index === vip.index }"
                  @click="changeVip(index)"
                >
                  {{ item.name }}
                </div>
              </div>
            </div>
            <div class="l-flex__fill u-scroll-y">
              <div class="c-calendar has-separate--t">
                <div class="l-flex--row has-padding--around has-hairline--b">
                  <i
                    class="o-icon o-toggle-date is-rotate l-flex__none"
                    :class="{ 'is-valid': canToPresent }"
                    @click="toPresentMonth"
                  ></i>
                  <div class="is-title u-text-center l-flex__fill">{{ date }}</div>
                  <i
                    class="o-icon o-toggle-date is-valid l-flex__none"
                    @click="toNextMonth"
                  ></i>
                </div>
                <div class="c-vlist-item--farther l-flex--row is-centered u-separate--nearest">
                  <div class="c-calendar__block">
                    <div class="c-calendar__day">{{ $t('calendar.sun') }}</div>
                    <div class="c-calendar__day">{{ $t('calendar.mon') }}</div>
                    <div class="c-calendar__day">{{ $t('calendar.tues') }}</div>
                    <div class="c-calendar__day">{{ $t('calendar.wed') }}</div>
                    <div class="c-calendar__day">{{ $t('calendar.thur') }}</div>
                    <div class="c-calendar__day">{{ $t('calendar.fri') }}</div>
                    <div class="c-calendar__day">{{ $t('calendar.sat') }}</div>
                  </div>
                </div>
                <div
                  v-for="(week, weekIndex) in vip.weeks"
                  :key="weekIndex"
                  class="c-vlist-item--farther l-flex--row is-centered"
                >
                  <div
                    v-for="(block, blockIndex) in week"
                    :key="blockIndex"
                    class="c-calendar__block"
                    :class="{ 'is-disable': block.state === -1, 'is-defaults': block.state === 1, 'is-special': block.state === 99 }"
                  >
                    <div
                      v-for="(day, index) in block.day"
                      :key="index"
                      class="c-calendar__day"
                      :class="{ 'is-holiday': day.holiday }"
                    >
                      {{ day.day }}
                    </div>
                  </div>
                </div>
              </div>
              <div class="has-separate--t">
                <div class="l-flex--row is-space-between has-padding--around has-hairline--b">
                  <div class="o-day">
                    <i class="o-day__icon is-blue"></i>
                    <span class="o-day__text">{{ $t('park.v1') }}</span>
                  </div>
                  <div class="o-day">
                    <i class="o-day__icon is-gray"></i>
                    <span class="o-day__text">{{ $t('park.v2') }}</span>
                  </div>
                  <div class="o-day">
                    <i class="o-day__icon is-orange"></i>
                    <span class="o-day__text">{{ $t('park.v3') }}</span>
                  </div>
                </div>
                <div class="has-padding--around">
                  <div class="is-tip--dark">{{ $t('park.remark') }}</div>
                  <div class="is-content u-separate--nearer">{{ $t('park.tip') }}</div>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { dateFormat, transformTime } from '@/utils/format.js'
import LocaleMixin from '@/components/mixins/locale.js'
import { Loading } from 'vant'
import Toggle from '@/components/Toggle.vue'

export default {
  mixins: [ LocaleMixin ],
  components: {
    [ Loading.name ]: Loading,
    Toggle
  },
  data () {
    return {
      tabIndex: 0,
      parkInfo: {
        index: 0,
        start: null,
        days: []
      },
      vip: {
        loading: false,
        date: null,
        index: 0,
        list: [],
        holiday: [],
        weeks: []
      }
    }
  },
  computed: {
    hasOpening () {
      return this.parkInfo.days.length > 0
    },
    startOpeningDate () {
      return this.hasOpening ? this.parkInfo.days[0].ym : ''
    },
    endOpeningDate () {
      return this.hasOpening ? this.parkInfo.days[this.parkInfo.days.length - 1].ym : ''
    },
    openingYm () {
      return this.hasOpening ? this.parkInfo.days[this.parkInfo.index].ym : ''
    },
    selectedDay () {
      return this.hasOpening ? this.parkInfo.days[this.parkInfo.index] : null
    },
    date () {
      return this.vip.date ? this.$t('calendar.tip', {
        year: this.vip.date.year,
        month: this.$t(`calendar.month.${ this.vip.date.month }`)
      }) : ''
    },
    canToPresent () {
      if (this.vip.date) {
        let today = new Date()
        return today.getFullYear() < this.vip.date.year || today.getFullYear() === this.vip.date.year && today.getMonth() < this.vip.date.month
      }
      return false
    }
  },
  methods: {
    switchTab (index) {
      this.tabIndex = index
      switch (this.tabIndex) {
        case 0:
          this.getParkInfo()
          break
        case 1:
          this.getCardInfo()
          break
        default:
          break
      }
    },
    transformMaintenance (item) {
      let startTime = item.timeList[0].startTime
      let endTime = item.timeList[0].endTime
      let isClosed = false
      let startDate = new Date(startTime)
      let endDate = new Date(endTime)
      if (startDate.getHours() === 0 && startDate.getMinutes() === 0 && endDate.getHours() === 23 && endDate.getMinutes() === 59) {
        isClosed = true
      }
      return {
        name: item.name,
        isClosed,
        value: isClosed ? this.$t('park.closed') : `${ transformTime(startTime) } - ${ transformTime(endTime) }`
      }
    },
    transformDay (time) {
      let date = new Date(time)
      let year = date.getFullYear()
      let month = date.getMonth()
      let day = date.getDate()
      let week = this.$t(`calendar.week.${ date.getDay() }`)
      return {
        year: year,
        month: month,
        day: day,
        week: week,
        openDate: dateFormat(time, 'yyyy-MM-dd'),
        ym: this.$t('time.ym', {
          year,
          month: this.$t(`calendar.month.${ date.getMonth() }`)
        }),
        loading: false,
        timestamp: -2,
        opening: null
      }
    },
    transformOpening (item) {
      if (!item) {
        return null
      }
      let date = new Date(item.parkOpenTime)
      return {
        date: this.$t('time.full', {
          year: date.getFullYear(),
          month: date.getMonth() + 1,
          day: date.getDate(),
          week: this.$t(`calendar.week.${ date.getDay() }`)
        }),
        opening: `${ transformTime(item.parkOpenTime) } - ${ transformTime(item.parkCloseTime) }`,
        hCloseTime: transformTime(item.summitCloseTime),
        parkingOpening: `${ transformTime(item.parkingOpenTime) } - ${ transformTime(item.parkingCloseTime) }`,
        waterfront: item.zoneOperatingHourList.waterfront.filter(obj => obj.timeList.length > 0).map(obj => {
          return {
            name: obj.name,
            value: `${ transformTime(obj.timeList[0].openTime) } - ${ transformTime(obj.timeList[0].closeTime) }`
          }
        }),
        peakpark: item.zoneOperatingHourList.peakpark.filter(obj => obj.timeList.length > 0).map(obj => {
          return {
            name: obj.name,
            value: `${ transformTime(obj.timeList[0].openTime) } - ${ transformTime(obj.timeList[0].closeTime) }`
          }
        }),
        maintenanceList: item.maintenanceList.filter(obj => obj.timeList.length > 0).map(obj => {
          return this.transformMaintenance(obj)
        })
      }
    },
    getOpeningInfo () {
      let day = this.selectedDay
      if (day.loading) {
        return
      }
      day.loading = true
      this.$http.post('/api/common/park/detail', {
        openDate: day.openDate
      }).then(data => {
        if (day.timestamp !== data.updateTime) {
          day.opening = this.transformOpening(data.parkOperatingHourList[0])
          day.timestamp = day.opening ? data.updateTime : -1
        }
      }, err => {
        if (!err.abort) {
          day.timestamp = 0
        }
      }).finally(() => day.loading = false)
    },
    changeDay (index) {
      this.parkInfo.index = index
      this.getOpeningInfo()
    },
    getParkInfo () {
      let today = Date.now()
      if (!this.parkInfo.start || this.parkInfo.start !== dateFormat(today, 'yyyy-MM-dd')) {
        let onday = 3600 * 24 * 1000
        let days = []
        for (let i = 0; i < 7; i++) {
          days.push(this.transformDay(today + onday * i))
        }
        this.parkInfo.days = days

        let current = this.selectedDay ? this.selectedDay.openDate : null
        this.parkInfo.index = Math.max(current ? days.findIndex(day => day.openDate === current) : 0, 0)
      }
      this.changeDay(this.parkInfo.index)
    },
    changeVip (index) {
      if (this.vip.index !== index) {
        this.vip.index = index
        this.calculate()
      }
    },
    _createItem (date) {
      let item = {
        day: date.getDate()
      }

      let msg = dateFormat(date, 'yyyy-MM-dd')
      let vip = this.vip.list[this.vip.index]

      if ((vip.specialDayList || []).findIndex(item => item.date === msg) > -1) {
        item.state = 99
        item.holiday = false
      } else {
        item.holiday = this.vip.holiday.findIndex(item => item.date === msg) > -1
        let day = date.getDay()
        switch (true) {
          case vip.excludeWeekend === 1 && (day === 0 || day === 6):
          case (vip.excludeDayList || []).findIndex(item => item.date === msg) > -1:
          case vip.excludePublicHoliday === 1 && item.holiday:
            item.state = -1
            break
          default:
            item.state = 1
            break
        }
      }

      return item
    },
    _createEmptyItem () {
      return {
        day: '',
        state: 0,
        holiday: false
      }
    },
    _getPreentMonthDays (year, month) {
      let dateArr = []

      for (let i = new Date(year, month, 1).getDay(); i > 0; i--) {
        dateArr.push(this._createEmptyItem())
      }

      return dateArr
    },
    _currentMonthDays (year, month) {
      let dateArr = []
      let lastDay = new Date(year, month + 1, 0).getDate()

      for (let i = 1; i <= lastDay; i++) {
        dateArr.push(this._createItem(new Date(year, month, i)))
      }

      return dateArr
    },
    calculate () {
      if (!this.vip.date) {
        let date = new Date()
        this.vip.date = {
          year: date.getFullYear(),
          month: date.getMonth()
        }
      }

      let { year, month } = this.vip.date
      let dates = this._getPreentMonthDays(year, month).concat(this._currentMonthDays(year, month))
      let weeks = []

      let row
      for (let i = 0; i < dates.length; i++) {
        if (i % 7 === 0) {
          row = weeks.length
          weeks[row] = []
        }
        weeks[row][i % 7] = dates[i]
      }

      let length = weeks[row].length
      while (length < 7) {
        weeks[row].push(this._createEmptyItem())
        length += 1
      }

      this.vip.weeks = weeks.map(week => {
        let state = null
        let arr = []
        let temp
        week.forEach(day => {
          if (state === null || state !== day.state) {
            state = day.state
            arr.push(temp = {
              day: [],
              state
            })
          }
          temp.day.push(day)
        })
        return arr
      })
    },
    _offsetMonth (offset) {
      let { year, month } = this.vip.date
      if (offset < 0) {
        if (month === 0) {
          year -= 1
          month = 11
        } else {
          month -= 1
        }
      } else if (offset > 0) {
        if (month === 11) {
          year += 1
          month = 0
        } else {
          month += 1
        }
      }

      this.vip.date = { year, month }

      if (offset <= 0 && !this.canToPresent) {
        let today = new Date()
        if (today.getFullYear() > year || today.getFullYear() === year && today.getMonth() > month) {
          this.vip.date = null
        }
      }

      this.calculate()
    },
    toPresentMonth () {
      if (this.canToPresent) {
        this._offsetMonth(-1)
      }
    },
    toNextMonth () {
      this._offsetMonth(1)
    },
    getCardInfo () {
      if (this.$vipFresh && !this.vip.loading) {
        this.vip.loading = true
        this.$http.post('/api/common/vipcard/detail', null).then(data => {
          this.$vipFresh = false
          this.vip.list = (data.card || []).sort((a, b) => a.level - b.level)
          this.vip.index = Math.max(0, Math.min(this.vip.index, this.vip.list.length - 1))
          this.vip.holiday = data.publicHolidayList || []
          if (this.vip.list.length === 0) {
            this.vip.date = null
          } else {
            if (this.vip.date) {
              this._offsetMonth(0)
            } else {
              this.calculate()
            }
          }
        }).finally(() => this.vip.loading = false)
      }
    },
    onLocaleUpdated () {
      this.parkInfo.start = null
      this.vip.list = []
      this.vip.holiday = []
      this.vip.weeks = []
    }
  },
  created () {
    this.$vipFresh = false
  },
  activated () {
    this.$vipFresh = true
    this.switchTab(this.tabIndex)
  }
}
</script>

<style lang="scss" scoped>
.o-toggle-date {
  background: url(~@/assets/icon-arrow--right.png) 0 0 / 100% 100% no-repeat;

  &.is-rotate {
    transform: rotate(180deg);
  }

  &.is-valid {
    background-image: url(~@/assets/icon-arrow--right--blue.png);
  }
}

.c-calendar {
  padding: 0 0 40px;

  &__block {
    flex: none;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 32px;
    @include u-font--bold;
    @include is-content($color);

    & + & {
      margin-left: 32px;
    }

    &.is-defaults {
      color: $color-blue;
      background-color: rgba($color-blue, .1);
    }

    &.is-disable {
      color: $color;
      background-color: rgba($color-separate, .5);
    }

    &.is-special {
      color: $color-white;
      background-color: $color-orange;
    }
  }

  &__day {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    &.is-holiday {
      color: $color--error;
    }

    & + & {
      margin-left: 32px;
    }
  }
}

.o-day {
  display: inline-flex;
  align-items: center;

  &__icon {
    flex: none;
    display: inline-block;
    width: 28px;
    height: 28px;
    border-radius: 50%;

    &.is-blue {
      background-color: $color-blue;
    }

    &.is-gray {
      background-color: $color-separate;
    }

    &.is-orange {
      background-color: $color-orange;
    }
  }

  &__text {
    margin-left: 12px;
    font-size: 28px;
    color: $color--dark;
    line-height: 32px;
    @include u-font;
  }
}
</style>
