import Vue from 'vue'
import VueRouter from 'vue-router'

import TabBar from '@/views/tabbar/TabBar.vue'
import Home from '@/views/tabbar/Home.vue'
import MapView from '@/views/tabbar/Map.vue'
import Park from '@/views/tabbar/Park.vue'
import Mine from '@/views/tabbar/Mine.vue'

import Filter from '@/views/map/Filter.vue'
import Entity from '@/views/map/Entity.vue'
import Menu from '@/views/map/Menu.vue'

import Web from '@/views/common/Web.vue'
import Weather from '@/views/common/Weather.vue'
import Feedback from '@/views/common/Feedback.vue'
import OrderDetail from '@/views/common/OrderDetail.vue'
import Mail from '@/views/common/mail/Mail.vue'
import MailDetail from '@/views/common/mail/MailDetail.vue'
import Payment from '@/views/common/payment/Payment.vue'
import PaymentResult from '@/views/common/payment/PaymentResult.vue'

import VerifyPhone from '@/views/common/verify/VerifyPhone.vue'
import VerifySms from '@/views/common/verify/VerifySms.vue'
import VerifyPhoneDone from '@/views/common/verify/VerifyPhoneDone.vue'
import VerifyCode from '@/views/common/verify/VerifyCode.vue'

import Settings from '@/views/settings/Settings.vue'

import SettingsAccount from '@/views/settings/account/Account.vue'
import SettingsAccountEmail from '@/views/settings/account/email/Email.vue'
import SettingsAccountEmailDone from '@/views/settings/account/email/EmailDone.vue'
import SettingsAccountPwd from '@/views/settings/account/change-password/ChangePassword.vue'
import SettingsAccountPwdResult from '@/views/settings/account/change-password/Result.vue'
import SettingsAccountProfile from '@/views/settings/account/profile/Profile.vue'

import SettingsLanguage from '@/views/settings/Language.vue'

import SettingsAbout from '@/views/settings/about/About.vue'
import SettingsAboutParkInfo from '@/views/settings/about/ParkInfo.vue'
import SettingsAboutRule from '@/views/settings/about/Rule.vue'
import SettingsAboutGeneral from '@/views/settings/about/General.vue'

import AnimalDiscovery from '@/views/ai/AnimalDiscovery.vue'
import Photograph from '@/views/ai/Photograph.vue'

import TicketDetail from '@/views/ticket/TicketDetail.vue'
import TicketList from '@/views/ticket/TicketList.vue'
import TicketBuy from '@/views/ticket/buy/TicketBuy.vue'
import TicketBuyPreOrder from '@/views/ticket/buy/TicketPreOrder.vue'
import TicketBuyPreOrderLink from '@/views/ticket/buy/TicketPreOrderLink.vue'
import TicketBind from '@/views/ticket/bind/TicketBind.vue'
import TicketCollect from '@/views/ticket/collect/TicketCollect.vue'

import EScheduler from '@/views/e-scheduler/EScheduler.vue'

import Sign from '@/views/sign/Sign.vue'
import Login from '@/views/sign/Login.vue'
import Forget from '@/views/sign/Forget.vue'
import ResetPassword from '@/views/sign/ResetPassword.vue'
import SignUp from '@/views/sign/SignUp.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: TabBar,
    children: [
      {
        path: '',
        name: 'home',
        component: Home,
        meta: { cache: true, tabbar: true, depth: 1 }
      },
      {
        path: 'map',
        name: 'map',
        component: MapView,
        meta: { cache: true, tabbar: true, depth: 2 }
      },
      {
        path: 'park',
        name: 'park',
        component: Park,
        meta: { cache: true, tabbar: true, depth: 3 }
      },
      {
        path: 'mine',
        name: 'mine',
        component: Mine,
        meta: { cache: true, tabbar: true, depth: 4 }
      }
    ]
  },
  {
    path: '/map/filter',
    name: 'filter',
    component: Filter
  },
  {
    path: '/web',
    name: 'web',
    component: Web
  },
  {
    path: '/feedback',
    name: 'feedback',
    component: Feedback
  },
  {
    path: '/weather',
    name: 'weather',
    component: Weather
  },
  {
    path: '/entity/:id',
    name: 'entity',
    component: Entity,
    children: [
      {
        path: 'menu',
        name: 'menu',
        component: Menu
      }
    ]
  },
  {
    path: '/settings',
    name: 'settings',
    component: Settings,
    meta: { login: true }
  },
  {
    path: '/settings/account',
    name: 'account-settings',
    component: SettingsAccount,
    meta: { login: true }
  },
  {
    path: '/settings/account/email',
    name: 'add-email',
    component: SettingsAccountEmail,
    meta: { login: true }
  },
  {
    path: '/settings/account/email/done',
    name: 'add-email-done',
    component: SettingsAccountEmailDone,
    meta: { login: true }
  },
  {
    path: '/settings/account/password',
    name: 'change-password',
    component: SettingsAccountPwd,
    meta: { login: true }
  },
  {
    path: '/settings/account/password/result',
    name: 'change-password-result',
    component: SettingsAccountPwdResult,
    meta: { login: true }
  },
  {
    path: '/settings/account/profile',
    name: 'profile',
    component: SettingsAccountProfile,
    meta: { login: true }
  },
  {
    path: '/settings/language',
    name: 'language',
    component: SettingsLanguage,
    meta: { login: true }
  },
  {
    path: '/settings/about',
    name: 'about',
    component: SettingsAbout,
    meta: { login: true }
  },
  {
    path: '/settings/about/park',
    name: 'about-park',
    component: SettingsAboutParkInfo,
    meta: { login: true }
  },
  {
    path: '/settings/about/rule',
    name: 'about-rule',
    component: SettingsAboutRule,
    meta: { login: true }
  },
  {
    path: '/settings/about/general',
    name: 'about-general',
    component: SettingsAboutGeneral,
    meta: { login: true }
  },
  {
    path: '/order/:id',
    name: 'order-detail',
    component: OrderDetail,
    meta: { login: true }
  },
  {
    path: '/mail',
    name: 'mail',
    component: Mail,
    meta: { login: true },
    children: [
      {
        path: ':id',
        name: 'mail-detail',
        component: MailDetail,
        meta: { login: true }
      }
    ]
  },
  {
    path: '/payment/:id',
    name: 'payment',
    component: Payment
  },
  {
    path: '/payment/result/:id',
    name: 'payment-result',
    component: PaymentResult
  },
  {
    path: '/verify/phone',
    name: 'verify-phone',
    component: VerifyPhone,
    meta: { login: true },
    children: [
      {
        path: 'sms',
        name: 'verify-phone-sms',
        component: VerifySms,
        meta: { login: true }
      },
      {
        path: 'done',
        name: 'verify-phone-done',
        component: VerifyPhoneDone,
        meta: { login: true, back: -2 }
      }
    ]
  },
  {
    path: '/verify/code',
    name: 'verify-code',
    component: VerifyCode
  },
  {
    path: '/sign',
    name: 'sign',
    component: Sign,
    meta: { login: false }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: { login: false }
  },
  {
    path: '/forget',
    name: 'forget',
    component: Forget,
    meta: { login: false }
  },
  {
    path: '/forget/reset',
    name: 'forget-reset',
    component: ResetPassword,
    meta: { login: false }
  },
  {
    path: '/signup',
    name: 'signup',
    component: SignUp,
    meta: { login: false }
  },
  {
    path: '/ai',
    name: 'ai',
    component: AnimalDiscovery,
    meta: { cache: true }
  },
  {
    path: '/ai/photograph',
    name: 'photograph',
    component: Photograph
  },
  {
    path: '/ticket/type/:type',
    name: 'ticket-list',
    component: TicketList,
    meta: { cache: true, login: true }
  },
  {
    path: '/ticket/:id',
    name: 'ticket',
    component: TicketDetail,
    meta: { login: true }
  },
  {
    path: '/bind/ticket',
    name: 'ticket-bind',
    component: TicketBind,
    meta: { login: true }
  },
  {
    path: '/buy/ticket',
    name: 'ticket-buy',
    component: TicketBuy,
    meta: { cache: true, login: true },
    children: [
      {
        path: 'detail/:id',
        name: 'ticket-detail',
        component: TicketDetail,
        meta: { cache: true, login: true }
      },
      {
        path: 'order',
        name: 'ticket-pre-order',
        component: TicketBuyPreOrder,
        meta: { cache: true, login: true },
        children: [
          {
            path: 'ext',
            name: 'ticket-pre-order-link',
            component: TicketBuyPreOrderLink,
            meta: { cache: true, login: true }
          }
        ]
      }
    ]
  },
  {
    path: '/collect/ticket/:id',
    name: 'ticket-collect',
    component: TicketCollect,
    meta: { login: true }
  },
  {
    path: '/escheduler',
    name: 'escheduler',
    component: EScheduler,
    meta: { login: true }
  },
  {
    path: '*',
    redirect: { name: 'home' }
  }
]

const router = new VueRouter({
  routes
})

export default router
