<template>
  <list-page
    :title="$t('settings.account.email.title')"
    :tip="$t('settings.account.email.tip')"
    :button="$t('btn.next')"
    :disable="disable"
    @click="toNext"
  >
    <field
      v-model="email"
      :title="$t('sign.info.email')"
      :placeholder="$t('sign.info.email')"
      :error="error"
      @input="error = ''"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="npassword"
      type="password"
      :title="$t('sign.reset.npassword')"
      :placeholder="$t('sign.reset.npassword')"
      maxlength="16"
      :error="nerror"
      @input="nerror = ''"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="rpassword"
      type="password"
      :title="$t('sign.info.rpassword')"
      :placeholder="$t('sign.info.rpassword')"
      maxlength="16"
      :error="rerror"
      @input="rerror = ''"
    />
  </list-page>
</template>

<script>
import { checkEmail, checkPassword } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    ListPage,
    Field
  },
  data () {
    return {
      email: '',
      error: '',
      npassword: '',
      nerror: '',
      rpassword: '',
      rerror: ''
    }
  },
  computed: {
    disable () {
      return !this.email || !this.npassword || !this.rpassword
    }
  },
  methods: {
    toNext () {
      let eResult = checkEmail(this.email)
      this.error = eResult.msg

      let nResult = checkPassword(this.npassword)
      this.nerror = nResult.msg

      let rResult = checkPassword(this.rpassword)
      this.rerror = rResult.msg

      if (eResult.pass && nResult.pass && rResult.pass) {
        if (this.npassword !== this.rpassword) {
          this.rerror = this.$t('patt.pwd.diff')
        } else {
          this.$http.post('/api/app/user/email/bind', {
            email: this.email,
            password: this.npassword
          }, {
            login: true,
            loading: true,
            server: true
          }).then(({ activeExpire }) => this.$router.replace({
            name: 'verify-code',
            query: {
              type: 'bind',
              email: encodeURIComponent(this.email),
              expire: activeExpire
            }
          }))
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
