<template>
  <div class="l-page">
    <div class="l-page__content">
      <transition :name="direction">
        <keep-alive>
          <router-view/>
        </keep-alive>
      </transition>
    </div>
    <div class="l-page__footer c-tabbar has-hairline--t">
      <router-link
        tag="div"
        v-for="(item, index) in items"
        :key="index"
        class="c-tabbar__item l-flex--column is-centered u-flex-nonw"
        :to="{ name: item.url }"
        replace
        exact
      >
        <i
          class="c-tabbar__icon"
          :class="[item.iconClass, { 'has-dot': item.dot && hasParkMails }]"
        ></i>
        <span class="c-tabbar__text">{{ item.label }}</span>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      direction: 'slide-r2l',
      items: [
        {
          url: 'home',
          label: this.$t('tabbar.home'),
          iconClass: 'is-home'
        },
        {
          url: 'map',
          label: this.$t('tabbar.map'),
          iconClass: 'is-map'
        },
        {
          url: 'park',
          label: this.$t('tabbar.park'),
          iconClass: 'is-info'
        },
        {
          url: 'mine',
          label: this.$t('tabbar.mine'),
          iconClass: 'is-mine',
          dot: true
        }
      ]
    }
  },
  computed: {
    ...mapGetters(['hasParkMails'])
  },
  beforeRouteUpdate (to, from, next) {
    this.direction = to.meta.depth < from.meta.depth ? 'slide-l2r' : 'slide-r2l'
    next()
  }
}
</script>

<style lang="scss" scoped>
.c-tabbar {
  &__item {
    height: 90px;
    width: 33%;
    color: $color;
    &.router-link-exact-active {
      color: $color-blue;

      .is-home {
        background-image: url(~@/assets/tabbar-home-sl.png)
      }

      .is-map {
        background-image: url(~@/assets/tabbar-map-sl.png)
      }

      .is-info {
        background-image: url(~@/assets/tabbar-info-sl.png)
      }

      .is-mine {
        background-image: url(~@/assets/tabbar-mine-sl.png)
      }
    }
  }

  &__icon {
    display: inline-block;
    position: relative;
    width: 48px;
    height: 48px;
    @include bg-img-attrs;

    &.is-home {
      background-image: url(~@/assets/tabbar-home.png)
    }

    &.is-map {
      background-image: url(~@/assets/tabbar-map.png)
    }

    &.is-info {
      background-image: url(~@/assets/tabbar-info.png)
    }

    &.is-mine {
      background-image: url(~@/assets/tabbar-mine.png)
    }
  }

  &__text {
    font-size: 20px;
    line-height: 24px;
  }
}
</style>
