<template>
  <div v-if="has" class="c-relation has-padding--v">
    <div v-if="animal.length > 0" class="c-vlist-item--farther has-padding--h">
      <div class="is-tip--dark">{{ $t('explore.detail.close2animal') }}</div>
      <div class="u-separate--near">
        <v-animal v-for="(item, index) in animal" :key="index" class="c-vlist-item--far" :entity="item"/>
      </div>
    </div>
    <div v-if="product.length > 0" class="c-vlist-item--farther">
      <div class="is-tip--dark has-padding--h">{{ $t('explore.detail.product') }}</div>
      <div class="l-hlist has-extra u-separate--near">
        <div class="l-hlist__wrapper">
          <div v-for="(item, index) in product" :key="index" class="c-hlist-item o-product has-shadow">
            <div class="u-text-center">
              <v-resource  class="o-product__img" :info="item.picInfo"/>
            </div>
            <div class="is-mini--dark u-separate--near">
              <div class="is-ellipsis">{{ item.name }}</div>
              <div class="is-ellipsis u-font u-separate--gap">{{ item.currency }}{{ item.price }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="coupon.length > 0" class="has-padding">
      <div class="is-tip--dark">{{ $t('explore.detail.coupon') }}</div>
      <div class="u-separate--near">
        <div v-for="(item, index) in coupon" :key="index" class="c-vlist-item o-coupon">
          <div class="o-coupon__content has-shadow">
            <div class="o-coupon__wrapper">
              <v-resource  class="o-coupon__img" :info="item"/>
              <div class="o-coupon__name is-tip--dark u-h-separate--near">{{ item.name }}</div>
            </div>
            <div class="o-coupon__line has-hairline--t"></div>
            <div class="is-mini--orange u-font--bold">{{ item.valid }}</div>
          </div>
          <div class="o-coupon__button">{{ item.discount }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { dateFormat } from '@/utils/format.js'
import VAnimal from './VAnimal.vue'

export default {
  components: {
    VAnimal
  },
  props: {
    entity: {
      type: Object,
      default () {
        return null
      }
    }
  },
  computed: {
    relateList () {
      return this.entity.relateList || []
    },
    animal () {
      let relation = this.relateList.find(item => item.type === 'closeToAnimal')
      if (relation) {
        return relation.list
      }
      return []
    },
    product () {
      let relation = this.relateList.find(item => item.type === 'product')
      if (relation) {
        return relation.list
      }
      return []
    },
    coupon () {
      let relation = this.relateList.find(item => item.type === 'coupon')
      if (relation) {
        return relation.list.map(this.transformCoupon)
      }
      return []
    },
    has () {
      return this.animal.length > 0 || this.product.length > 0 || this.coupon.length > 0
    }
  },
  methods: {
    transformCoupon (item) {
      return {
        id: item.id,
        name: item.name,
        url: item.picInfo.url,
        cosUrl: item.picInfo.cosUrl,
        discount: item.discount,
        valid: `${ this.$t('explore.detail.valid') } ${ dateFormat(item.validEndDate, 'yyyy.MM.dd') }`
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.c-relation {
  background-color: $color-separate--block;
}

.o-product {
  box-sizing: border-box;
  display: inline-block;
  width: 180px;
  height: 265px;
  padding: 32px 16px 0;
  border-radius: 16px;
  background-color: $color-white;

  &__img {
    width: 116px;
    height: 116px;
  }
}

.o-coupon {
  display: flex;
  position: relative;
  height: 258px;

  &__left {
    position: absolute;
    top: 0;
    left: 0;
    right: 130px;
    bottom: 0;
    width: auto;
    height: auto;
  }

  &__right {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 130px;
    height: auto;
  }

  &__content {
    flex: 1;
    padding: 24px 32px 0;
    background: url(~@/assets/coupon-left.png) 0 0 / 100% 100% no-repeat;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    height: 148px;
  }

  &__img {
    flex: none;
    width: 132px;
    height: 132px;
    border-radius: 50%;
    overflow: hidden;
  }

  &__name {
    flex: 1;
    @include ellipse-multi(3);
  }

  &__line {
    height: 1px;
    margin: 16px 0;
  }

  &__button {
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 140px;
    text-align: center;
    background: url(~@/assets/coupon-right.png) 0 0 / 100% 100% no-repeat;
    @include is-content($color-white);
  }
}
</style>
