<template>
  <div class="l-page">
    <div class="l-page__header--primary">
      <div
        v-if="miniSize"
        class="l-flex__fill l-flex--row l-row-layout"
      >
        <router-link
          tag="i"
          class="o-avatar l-flex__none"
          :style="avatar"
          :to="{ name: 'profile' }"
        />
        <div class="l-flex__fill o-nick is-ellipsis">{{ msg }}</div>
      </div>
      <div
        v-else
        class="l-page__title"
      >{{ $t('tabbar.mine') }}</div>
      <div class="l-flex__none l-flex--row">
        <router-link
          tag="i"
          class="o-nav-icon"
          :class="{ 'has-dot': hasParkMails }"
          :to="{ name: 'mail' }"
        >
          <img class="o-nav-icon" src="@/assets/icon-notification.png"/>
        </router-link>
        <router-link
          tag="i"
          class="o-nav-icon"
          :to="{ name: 'settings' }">
          <img class="o-nav-icon" src="@/assets/icon-settings.png"/>
        </router-link>
      </div>
    </div>
    <div ref="content" class="l-page__content l-flex--column">
      <div
        v-if="!miniSize"
        class="l-flex__none l-column-layout o-account"
      >
        <router-link
          tag="i"
          class="o-avatar"
          :style="avatar"
          :to="{ name: 'profile' }"
        >
          <i
            v-if="isLogined"
            class="o-edit"
          ></i>
        </router-link>
        <div class="o-nick is-ellipsis">{{ msg }}</div>
      </div>
      <div class="l-flex__fill l-flex--column">
        <div class="l-flex--row has-shadow--short u-background--blue">
          <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 0 }" @click="switchTab(0)">{{ $t('mine.journey') }}</div>
          <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 1 }" @click="switchTab(1)">{{ $t('mine.orders') }}</div>
          <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 2 }" @click="switchTab(2)">{{ $t('mine.coupon') }}</div>
        </div>
        <div class="l-hscroll l-flex__fill">
          <div class="l-hscroll__wrapper" :class="{ 'is-second': tabIndex === 1, 'is-third': tabIndex === 2 }">
            <div
              ref="ticket"
              class="l-hscroll__item"
              @scroll="scrollTicket"
            >
              <div v-if="hasTickets" class="has-padding--v">
                <indicator
                  v-if="tickets.length > 1"
                  class="c-vlist-item"
                  :count="tickets.length"
                  :current="currentTicketIndex"
                />
                <van-swipe
                  ref="swiper"
                  class="c-vlist-item"
                  :initial-swipe="ticketIndex"
                  :show-indicators="false"
                  :loop="false"
                  @change="changeTicket"
                >
                  <van-swipe-item
                    v-for="(ticket, index) in tickets"
                    :key="index"
                    :style="{ height: `${ swiperHeight }px` }"
                  >
                    <div
                      :ref="`ticket${ index }`"
                      class="has-padding--h"
                    >
                      <ticket
                        :ticket="ticket"
                        @toggle="checkTickets(true)"
                      />
                    </div>
                  </van-swipe-item>
                </van-swipe>
              </div>
              <div v-else class="c-tip is-fill u-background--white">
                <img class="c-tip__img" src="~@/assets/no-ticket.png"/>
                <div class="c-tip__tip">{{ $t('mine.noticket') }}</div>
                <div class="c-tip__footer">
                  <router-link
                    tag="div"
                    class="c-tip__button"
                    :to="{ name: 'ticket-buy' }"
                  >
                    {{ $t('btn.buyticket') }}
                  </router-link>
                  <router-link
                    tag="div"
                    class="c-tip__button"
                    :to="{ name: 'ticket-bind' }"
                  >
                    {{ $t('btn.bindticket') }}
                  </router-link>
                </div>
              </div>
            </div>
            <div
              ref="order"
              class="l-hscroll__item"
              @scroll="scrollOrder"
            >
              <div class="has-padding--large u-scroll-x u-background--white">
                <div class="c-hlist-item--near o-option" :class="{ 'is-choosed': orderType === 'all' }" @click="changeType('all')">{{ $t('mine.types.all') }}</div>
                <div class="c-hlist-item--near o-option" :class="{ 'is-choosed': orderType === 'paid' }" @click="changeType('paid')">{{ $t('mine.types.paid') }}</div>
                <div class="c-hlist-item--near o-option" :class="{ 'is-choosed': orderType === 'unpaid' }" @click="changeType('unpaid')">{{ $t('mine.types.unpaid') }}</div>
                <div class="c-hlist-item--near o-option" :class="{ 'is-choosed': orderType === 'cancelled' }" @click="changeType('cancelled')">{{ $t('mine.types.cancelled') }}</div>
              </div>
              <order
                v-for="order in orderList"
                :key="order.info.id"
                class="u-separate--nearer u-background--white"
                :order="order"
                detail
                @del="toDel"
              />
            </div>
            <div class="l-hscroll__item">
              Coupons
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { dateFormat, transformTicketValidTime } from '@/utils/format.js'
import { sortBySortIndex } from '@/utils/sort.js'
import LocaleMixin from '@/components/mixins/locale.js'
import { Swipe, SwipeItem } from 'vant'
import Indicator from '@/components/Indicator.vue'
import Order from '@/components/ticket/Order.vue'
import Ticket from '@/components/mine/Ticket.vue'

export default {
  mixins: [ LocaleMixin ],
  components: {
    [ Swipe.name ]: Swipe,
    [ SwipeItem.name ]: SwipeItem,
    Indicator,
    Order,
    Ticket
  },
  data () {
    return {
      tabIndex: 0,
      swiperHeight: 0,
      tickets: [],
      ticketIndex: 0,
      currentTicketIndex: 0,
      ticketScrollOut: false,
      orderScrollOut: false,
      orders: [],
      orderType: 'all'
    }
  },
  computed: {
    ...mapGetters(['isLogined', 'nickName', 'avatarUrl', 'hasParkMails']),
    hasTickets () {
      return this.tickets.length > 0
    },
    avatar () {
      return this.avatarUrl ? { backgroundImage: `url("${ this.avatarUrl }")` } : null
    },
    msg () {
      if (!this.isLogined) {
        return this.$t('sign.title')
      }
      return this.nickName || this.$t('common.visitor')
    },
    miniSize () {
      return this.tabIndex === 0 ? this.ticketScrollOut : this.orderScrollOut
    },
    orderList () {
      switch (this.orderType) {
        case 'paid':
          return this.orders.filter(order => order.status > 37)
        case 'unpaid':
          return this.orders.filter(order => order.status === 1)
        case 'cancelled':
          return this.orders.filter(order => order.status === 2)
        default:
          return this.orders
      }
    }
  },
  methods: {
    changeTicket (index) {
      this.currentTicketIndex = index
      this.checkTickets(true)
    },
    getTickets () {
      clearTimeout(this.$ticketTimer)
      this.$http.post('/api/common/user/ticket/list', null, {
        login: true
      }).then(data => {
        this.tickets = data.data.sort(sortBySortIndex)

        let index = Math.max(0, Math.min(this.currentTicketIndex, this.tickets.length - 1))
        if (this.currentTicketIndex !== index) {
          this.currentTicketIndex = index
          this.ticketIndex = index
        }

        this.$ticketHeight = 0
        this.checkTickets()
      }, err => {
        if (!err.abort) {
          this.$ticketTimer = setTimeout(this.getTickets, 2000)
        }
      })
    },
    checkTickets (force) {
      if (this.tickets.length === 0) {
        this.$ticketCanScroll = false
        this.ticketScrollOut = false
        this.swiperHeight = 0
      } else {
        if (!force && this.$ticketHeight) {
          if (this.$ticketScrollTop) {
            this.$refs.ticket.scrollTop = this.$ticketScrollTop
            this.$ticketScrollTop = 0
          }
        } else {
          this.$nextTick(() => {
            let swiperHeight = this.$refs[`ticket${ this.currentTicketIndex }`][0].offsetHeight
            if (swiperHeight !== this.swiperHeight) {
              this.swiperHeight = swiperHeight
              this.$nextTick(() => {
                this.$ticketHeight = this.$refs.ticket.scrollHeight
                this.$ticketCanScroll = this.$ticketHeight >= this.$contentHeight + 40
                this.ticketScrollOut = this.$ticketCanScroll && (this.$refs.ticket.scrollTop > 40)
              })
            }
          })
        }
      }
    },
    scrollTicket (e) {
      if (this.$ticketCanScroll) {
        this.ticketScrollOut = e.target.scrollTop > 40
      }
    },
    transformOrder (order) {
      let { orderId, addTime, status, spOrderList, discountPrice, coupon, totalPrice } = order

      return {
        info: {
          id: orderId,
          date: dateFormat(addTime, 'yyyy.MM.dd')
        },
        status,
        list: spOrderList[0].productList.map(ticket => {
          return {
            name: ticket.productName,
            count: ticket.productCount,
            total: ticket.price,
            date: transformTicketValidTime(ticket.extInfo)
          }
        }),
        createTime: addTime,
        subtotal: coupon ? totalPrice : null,
        discount: coupon ? coupon.discount ? `- ${ coupon.discount }` : coupon.name : '',
        total: coupon ? discountPrice : totalPrice
      }
    },
    toDel (order) {
      let orderId = order.info.id
      let index = this.orders.findIndex(order => order.info.id === orderId)
      if (index > -1) {
        this.$http.post('/api/common/user/order/delete', {
          orderId
        }, {
          globals: true,
          login: true
        })
        this.orders.splice(index, 1)
      }
    },
    getOrders () {
      clearTimeout(this.$orderTimer)
      this.$http.post('/api/common/user/order/list', null, {
        login: true
      }).then(data => {
        this.orders = data.data.map(this.transformOrder)

        this.$orderHeight = 0
        this.checkOrders()
      }, err => {
        if (!err.abort) {
          this.$orderTimer = setTimeout(this.getOrders, 2000)
        }
      })
    },
    checkOrders (force = false) {
      if (this.orderList.length < 2) {
        this.orderScrollOut = false
      } else {
        if (!force && this.$orderHeight) {
          if (this.$orderScrollTop) {
            this.$refs.order.scrollTop = this.$orderScrollTop
            this.$orderScrollTop = 0
          }
        } else {
          this.$nextTick(() => {
            this.$orderHeight = this.$refs.order.scrollHeight
            this.$orderCanScroll = this.$orderHeight >= this.$contentHeight + 40
            this.orderScrollOut = this.$orderCanScroll && (this.$refs.order.scrollTop > 40)
          })
        }
      }
    },
    scrollOrder (e) {
      if (this.$orderCanScroll) {
        this.orderScrollOut = e.target.scrollTop > 40
      }
    },
    changeType (type) {
      this.orderType = type
      this.checkOrders(true)
    },
    switchTab (index) {
      this.tabIndex = index
    },
    clearData () {
      this.tickets = []
      this.ticketIndex = 0
      this.currentTicketIndex = 0
      this.orders = []
    },
    onLocaleUpdated () {
      this.clearData()
    }
  },
  created () {
    this.$ticketTimer = -1
    this.$orderTimer = -1
  },
  mounted () {
    this.$contentHeight = this.$refs.content.offsetHeight
  },
  activated () {
    if (this.isLogined) {
      this.getTickets()
      this.getOrders()
    } else {
      this.clearData()
    }
    this.checkTickets()
    this.checkOrders()
  },
  deactivated () {
    this.$ticketScrollTop = this.$refs.ticket.scrollTop
    this.$orderScrollTop = this.$refs.order.scrollTop
    clearTimeout(this.$ticketTimer)
    clearTimeout(this.$orderTimer)
  }
}
</script>

<style lang="scss" scoped>
.l-page {
  background-color: $color-blue;
}

.l-hscroll {
  background-color: $color-separate--block;
}

.o-avatar {
  box-sizing: border-box;
  display: inline-block;
  position: relative;
  border-radius: 50%;
  border: 4px solid $color-white;
  background: url(~@/assets/avatar.png) 0 0 / 100% 100% no-repeat;
}

.o-nick {
  @include is-title($color-white);
}

.l-column-layout {
  padding: 20px 40px 30px;
  text-align: center;

  .o-avatar {
    width: 158px;
    height: 158px;
    margin-top: 20px;
  }

  .o-nick {
    margin-top: 28px;
  }
}

.l-row-layout {
  .o-avatar {
    width: 60px;
    height: 60px;
    border-width: 2px;
  }

  .o-nick {
    margin-left: 18px;
  }
}

.o-edit {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  overflow: hidden;
  background-color: $color-white;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 32px;
    height: 32px;
    transform: translate(-50%, -50%);
    background: url(~@/assets/icon-edit.png) 0 0 / 100% 100% no-repeat;
  }
}
</style>
