<template>
  <div class="l-page" @click="clean">
    <div class="l-page__header--primary">
      <div class="l-page__title">{{ $t('home.title') }}</div>
      <div class="l-flex__none l-flex--row">
        <i
          v-if="hasExtras"
          class="o-nav-icon o-extra-icon"
          @click.stop="showBlock = !showBlock">
        </i>
        <img class="o-nav-icon" src="@/assets/icon-scan.png"/>
      </div>
      <transition name="fade">
        <div v-if="showBlock" class="c-extras" @click.stop="clean">
          <div v-for="(item, index) in extras" :key="index" class="c-extras__item has-hairline--separate l-flex--row" @click="navigateTo(item)">
            <v-resource class="c-extras__img l-flex__none" :info="item.picInfo"/>
            <div class="is-tip--dark l-flex__fill">{{ item.title }}</div>
          </div>
        </div>
      </transition>
    </div>
    <div class="l-page__content l-flex--column">
      <banner
        :list="banners"
        inst
        primary
        @emit="onBannerClick"
      />
      <div
        v-if="hasNavigation"
        class="c-navigation has-shadow--short"
      >
        <div
          v-for="(item, index) in navigation"
          :key="index"
          class="c-navigation__item o-navigation"
          @click="navigateTo(item)">
          <v-resource class="o-navigation__icon" :info="item.picInfo"/>
          <div class="o-navigation__text">{{ item.title }}</div>
        </div>
      </div>
      <div
        v-if="hasPopular"
        class="c-popular-list"
      >
        <div class="is-tip--dark has-padding--h">{{ $t('home.popular') }}</div>
        <div class="u-separate">
          <div
            v-for="(item, index) in popular"
            :key="index"
            class="c-popular-list__block"
          >
            <div class="l-flex has-padding--h">
              <div class="l-flex__fill">
                <div class="is-title--block">{{ item.title }}</div>
                <div v-if="item.tags" class="o-popular-tag is-mini--blue u-font--bold u-separate--nearer">{{ item.tags }}</div>
              </div>
              <router-link
                class="l-flex__none o-map"
                :to="{ name: 'map' }"
              />
            </div>
            <div class="c-popular-list__block l-hlist has-extra">
              <div class="l-hlist__wrapper u-separate--near">
                <div
                  v-for="(entity, index) in item.entityList" :key="index"
                  class="c-hlist-item o-popular has-shadow"
                  @click="toEntity(entity)"
                >
                  <v-resource class="o-popular__img" :info="entity.picInfo"/>
                  <div class="o-popular__content is-tip--dark">{{ item.title }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-popup
      v-if="hasMail"
      class="c-pop-mail"
      :value="hasMail"
      :close-on-click-overlay="false"
    >
      <template v-if="mail.origin.msgType === 2">
        <div class="c-image-mail u-position-r">
          <v-resource
            class="c-image-mail__img"
            :info="mail.origin.titlePicInfo"
            @click.native="toOpenMail(false)"
          />
          <div class="c-image-mail__close" src="/static/icon-close-white.png" @click="toOpenMail(true)"></div>
        </div>
      </template>
      <template v-else>
        <div class="c-dialog u-background--white">
          <div class="c-dialog__header">{{ $t('modal.title') }}</div>
          <div class="c-dialog__message c-dialog__message--has-title">{{ mail.origin.msgTitle }}</div>
          <div class="c-dialog__footer">
            <div class="c-dialog__button" @click="toOpenMail(false)">{{ mail.origin.msgType === 0 ? $t('btn.known') : $t('home.detail') }}</div>
          </div>
        </div>
      </template>
    </van-popup>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="$t('modal.title')"
      :message="$t('home.collect')"
      :confirm-button-text="$t('home.login')"
      :cancel-button-text="$t('btn.cancel')"
      show-cancel-button
      @close="clearShare"
      @confirm="toReceive"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { initMails } from '@/utils/common.js'
import LocaleMixin from '@/components/mixins/locale.js'
import DialogMixin from '@/components/mixins/dialog.js'
import { Popup } from 'vant'
import Banner from '@/components/Banner'

export default {
  mixins: [ LocaleMixin, DialogMixin ],
  components: {
    [ Popup.name ]: Popup,
    Banner
  },
  data () {
    return {
      loading: false,
      showBlock: false,
      extrasFromServer: null,
      bannersFromServer: null,
      navigationFromServer: null,
      popularFromServer: null,
      share: null
    }
  },
  computed: {
    ...mapGetters(['isLogined', 'parkPopMails']),
    extras () {
      return this.extrasFromServer && this.extrasFromServer.contentList || []
    },
    hasExtras () {
      return this.extras.length > 0
    },
    banners () {
      return this.bannersFromServer && this.bannersFromServer.contentList || []
    },
    navigation () {
      return this.navigationFromServer && this.navigationFromServer.contentList || []
    },
    hasNavigation () {
      return this.navigation.length > 0
    },
    popular () {
      return this.popularFromServer ? this.popularFromServer.contentList : []
    },
    hasPopular () {
      return this.popular.length > 0
    },
    mail () {
      return this.parkPopMails[0] || null
    },
    hasMail () {
      return this.share === null && this.mail !== null
    }
  },
  methods: {
    getData () {
      if (this.loading) {
        return
      }
      this.loading = true
      this.$http.post('/api/app/home/index').then(data => {
        if (this.$timestamp !== data.updateTime) {
          this.$timestamp = data.updateTime

          this.extrasFromServer = data.moduleList.find(module => module.moduleType === 4)

          this.bannersFromServer = data.moduleList.find(module => module.moduleType === 1)

          this.navigationFromServer = data.moduleList.find(module => module.moduleType === 2)

          this.popularFromServer = data.moduleList.find(module => module.moduleType === 5)
        }
      }).finally(() => this.loading = false)
    },
    clean () {
      this.showBlock = false
    },
    navigateTo (item) {
      this.$features.linkTo(item.link)
    },
    onBannerClick (index) {
      this.navigateTo(this.banners[index])
    },
    toEntity (entity) {
      this.$features.entity(entity.entityId)
    },
    toScan () {
      this.$features.scan()
    },
    toOpenMail (skip = false) {
      this.$features.readMail(this.mail, skip)
    },
    clearShare () {
      this.share = null
    },
    toReceive () {
      this.$router.push({
        name: 'ticket-collect',
        params: {
          id: this.share
        }
      })
    },
    onLocaleUpdated () {
      this.$scrollTop = 0
      this.$timestamp = 0
      this.extrasFromServer = null
      this.bannersFromServer = null
      this.navigationFromServer = null
      this.popularFromServer = null
    }
  },
  created () {
    this.$timestamp = 0
    this.$scrollTop = 0
    this.share = this.$route.query.id || null
  },
  activated () {
    initMails(false)

    if (this.$scrollTop) {
      let dom = this.$refs.content
      if (dom) {
        dom.scrollTop = this.$scrollTop
      }
      this.$scrollTop = 0
    }

    this.getData()

    this.$nextTick(() => {
      if (this.share) {
        if (this.isLogined) {
          this.toReceive()
          this.share = null
        } else {
          this.dialogMsg.show = true
        }
      }
    })
  },
  deactivated () {
    this.clean()
    let dom = this.$refs.content
    if (dom) {
      this.$scrollTop = dom.scrollTop
    }
  }
}
</script>

<style lang="scss" scoped>
.o-extra-icon {
  background: url(~@/assets/icon-add--white.png) 0 0 / 100% 100% no-repeat;
}

.c-extras {
  position: absolute;
  top: 100%;
  right: 40px;
  width: 349px;
  border-radius: 16px;
  background-color: $color-white;

  &::after {
    content: '';
    position: absolute;
    top: -12px;
    right: 82px;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-bottom: 14px solid $color-white;
  }

  &__item {
    padding: 34px 30px;
  }

  &__img {
    display: inline-block;
    width: 40px;
    height: 40px;
    margin-right: 26px;
  }
}

.c-navigation {
  display: flex;
  flex-wrap: wrap;
  padding: 6px 12px 24px;

  &__item {
    flex: 0 0 25%;
  }
}

.o-navigation {
  box-sizing: border-box;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  height: 160px;
  padding: 26px 12px 0;
  overflow: hidden;

  &__icon {
    flex: none;
    display: inline-block;
    width: 64px;
    height: 64px;
  }

  &__text {
    align-self: stretch;
    margin-top: 12px;
    font-size: 24px;
    color: $color--dark;
    line-height: 26px;
    text-align: center;
    @include u-font--bold;
  }
}

.c-popular-list {
  padding: 48px 0 40px;

  &__block {
    & + & {
      margin-top: 56px;
    }
  }
}

.o-popular-tag {
  display: inline-block;
  padding: 2px 10px;
  border-radius: 8px;
  background-color: rgba(#00A9E0, .1);
}

.o-popular {
  display: inline-block;
  width: 310px;
  height: 292px;
  border-radius: 16px;
  overflow: hidden;

  &__img {
    width: 100%;
    height: 180px;
  }

  &__content {
    padding: 14px 24px 0;
  }
}

.o-map {
  display: inline-block;
  position: relative;
  width: 133px;

  &::before {
    content: '';
    position: absolute;
    width: 133px;
    height: 136px;
    background: url(~@/assets/bg--blue.png) 0 0 / 100% 100% no-repeat;
  }

  &::after {
    content: '';
    position: absolute;
    top: 20px;
    left: 50%;
    width: 56px;
    height: 56px;
    transform: translateX(-50%);
    background: url(~@/assets/icon-location--white.png) 0 0 / 100% 100% no-repeat;
  }
}

.c-pop-mail {
  background-color: transparent;
}

.c-image-mail {
  display: inline-flex;
  flex-direction: column;
  align-items: center;

  &__img {
    display: inline-block;
    width: 670px;
    height: 960px;
    border-radius: 16px;
    overflow: hidden;
  }

  &__close {
    display: inline-block;
    position: relative;
    width: 124px;
    height: 124px;
    margin-top: 48px;
    border-radius: 50%;
    background-color: rgba($color-white, .3);

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 80px;
      height: 80px;
      transform: translate(-50%, -50%);
      background: url(~@/assets/icon-close--white.png) 0 0 / 100% 100% no-repeat;
    }
  }
}

</style>
