<template>
  <list-page
    :title="title"
    :tip="$t('common.verify.phone.tip')"
    :button="$t('btn.confirmupper')"
    :disable="disable"
    @click="toConfirm"
  >
    <template #nav v-if="canSkip">
      <span class="is-content--blue" @click="skip">{{ $t('btn.skip') }}</span>
    </template>
    <template #page>
      <transition name="slide-wrapper">
        <router-view :verify="verify" style="z-index: 9;"/>
      </transition>
    </template>
    <phone
      v-model="phone"
      :area-code="areaCode"
      :error="error"
      @change="onAreaCodeChange"
      @input="clean"
    />
  </list-page>
</template>

<script>
import { checkPhone } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Phone from '@/components/list/Phone.vue'

export default {
  components: {
    ListPage,
    Phone
  },
  data () {
    return {
      verify: null,
      areaCode: '',
      phone: '',
      regExp: '',
      error: ''
    }
  },
  computed: {
    canSkip () {
      return this.$route.query.skip
    },
    title () {
      return this.canSkip ? this.$t('common.verify.phone.title') : this.$t('settings.account.phone.title')
    },
    disable () {
      return !this.areaCode || !this.phone
    }
  },
  methods: {
    clean () {
      this.error = ''
    },
    onAreaCodeChange ({ areaCode, regExp }) {
      this.areaCode = areaCode
      this.regExp = regExp
      this.clean()
    },
    skip () {
      this.$features.back()
    },
    toConfirm () {
      let result = checkPhone(this.areaCode, this.phone, this.regExp)
      if (result.pass) {
        this.toNext()
      } else {
        this.error = result.msg
      }
    },
    toNext () {
      let data = {
        type: 4,
        areaCode: this.areaCode,
        phone: this.phone
      }
      this.$http.post('/api/common/user/phone/smscode', data, {
        login: true,
        loading: true,
        server: true
      }).then(({ expireTime }) => {
        this.verify = {
          type: 'bind',
          data, expireTime
        }
        this.$router.push({
          name: 'verify-phone-sms'
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
