<template>
  <list-page
    :title="$t('sign.forget.title')"
    :tip="$t('sign.forget.tip')"
    :button="$t('btn.next')"
    :disable="disable"
    @click="toNext"
  >
    <field
      v-model="email"
      :title="$t('sign.info.email')"
      :placeholder="$t('sign.info.email')"
      :error="error"
      @input="error = ''"
    />
  </list-page>
</template>

<script>
import { checkEmail } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    ListPage,
    Field
  },
  data () {
    return {
      email: '',
      error: ''
    }
  },
  computed: {
    disable () {
      return !this.email
    }
  },
  methods: {
    toNext () {
      let result = checkEmail(this.email)
      this.error = result.msg
      if (result.pass) {
        this.$http.post('/api/app/user/email/resetPwd/apply', {
          email: this.email
        }, {
          loading: true
        }).then(({ activeExpire }) => {
          this.$router.replace({
            name: 'verify-code',
            query: {
              type: 'forget',
              email: encodeURIComponent(this.email),
              expire: activeExpire
            }
          })
        }, err => {
          if (!err.system) {
            this.error = err.msg
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
