<template>
  <div class="l-collection c-order" :class="{ 'has-padding-top': hasPaddingTop }" @click="toDetail">
    <div
      v-if="hasHeader"
      class="c-order__header is-mini--dark has-hairline--separate u-font"
    >
      <div class="l-flex__auto u-h-separate-reverse--list">{{ $t('common.order.date') }}{{ order.info.date }}</div>
      <div class="l-flex__none">{{ $t('common.order.no') }}{{ order.info.id }}</div>
    </div>
    <div class="l-collection__item has-hairline--separate">
      <div class="c-order__title is-title">{{ $t('common.order.name') }}</div>
      <div class="u-separate--near u-font">
        <div
          v-for="(item, index) in order.list"
          :key="index"
          class="c-vlist-item--nearer is-content--dark l-flex--row is-top"
        >
          <div class="l-flex__fill">
            <div>{{ item.name }} x {{ item.count }}</div>
            <div class="is-tip u-separate--gap">{{ item.date }}</div>
          </div>
          <div class="is-flex--none u-h-separate--gap">{{ item.total }}</div>
        </div>
      </div>
    </div>
    <div class="l-collection__item has-hairline--separate">
      <template v-if="detail">
        <div class="c-vlist-item--nearest l-flex--row is-rb">
          <div class="is-tip--dark">{{ $t('common.order.total') }} </div>
          <div class="is-title u-h-separate--nearest">{{ order.total }}</div>
        </div>
        <div
          v-if="order.subtotal"
          class="c-vlist-item--nearest is-mini l-flex--row is-rb u-font--bold u-line-through"
        >
          {{ order.subtotal }}
        </div>
      </template>
      <template v-else>
        <div
          v-if="order.subtotal"
          class="c-vlist-item--nearest l-flex--row u-font"
        >
          <div class="is-tip--dark l-flex__fill">{{ $t('common.order.subtotal') }} </div>
          <div class="is-content--dark l-flex__none">{{ order.subtotal }}</div>
        </div>
        <div
          v-if="order.discount"
          class="c-vlist-item--nearest l-flex--row u-font"
        >
          <div class="is-tip--dark l-flex__fill">{{ $t('common.order.discount') }} </div>
          <div class="is-content--dark l-flex__none">{{ order.discount }}</div>
        </div>
        <div class="c-vlist-item--nearest l-flex--row">
          <div class="is-tip--dark l-flex__fill">{{ $t('common.order.total') }} </div>
          <div class="is-title l-flex__none">{{ order.total }}</div>
        </div>
      </template>
    </div>
    <div
      v-if="detail"
      class="l-collection__item has-hairline--separate o-order-footer l-flex--row"
      @click.stop
    >
      <div class="o-order-footer__countdown l-flex__fill">{{ flag.msg }}</div>
      <div v-if="flag.canDel" class="o-order-footer__button is-hollow l-flex__none" @click="toDel">{{ $t('btn.del') }}</div>
      <div v-if="flag.isFinished" class="o-order-footer__button is-hollow l-flex__none">{{ flag.statusMsg }}</div>
      <div v-if="flag.canPay" class="o-order-footer__button is-primary l-flex__none" @click="toPay">{{ $t('btn.pay') }}</div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { zeroFill } from '@/utils/format.js'

export default {
  props: {
    status: {
      type: Number,
      default: -1
    },
    order: {
      type: Object,
      default () {
        return null
      }
    },
    detail: {
      type: [Boolean, String],
      default: false
    }
  },
  data () {
    return {
      flag: null,
      cancelled: false
    }
  },
  computed: {
    ...mapGetters(['orderTimeout']),
    hasHeader () {
      return this.order && this.order.info
    },
    hasPaddingTop () {
      return !this.hasHeader
    }
  },
  methods: {
    getStatusMsg () {
      if (!this.cancelled && this.order.status === 1 && this.orderTimeout) {
        let diff = Math.floor((this.orderTimeout + this.order.createTime - Date.now()) / 1000)
        if (diff > 0) {
          let hour = Math.floor(diff / 3600)
          let minute = Math.floor((diff - hour * 3600) / 60)
          let second = diff % 60
          let str = ` ${ zeroFill(minute, 2) }:${ zeroFill(second, 2) }`
          if (hour > 0) {
            str = `${ zeroFill(hour, 2) }:${ str }`
          }
          return `${ this.$t('common.order.unpaid') } ${ str }`
        }
        this.cancelled = true
      }

      return ''
    },
    toDetail () {
      if (!this.detail) {
        return
      }

      this.$router.push({
        name: 'order-detail',
        params: {
          id: this.order.info.id
        }
      })
    },
    toDel () {
      this.$emit('del', this.order)
    },
    toPay () {
      this.$router.push({
        name: 'payment',
        params: {
          id: this.order.info.id
        }
      })
    },
    check () {
      clearTimeout(this.$timer)
      if (this.detail) {
        let msg = this.getStatusMsg()
        let status = this.cancelled ? 2 : this.order.status
        let canPay = status === 1
        let isFinished = false
        let statusMsg = ''
        let canDel = canPay
        if (status === 2 || status === 6 || status === 36 || status >= 41) {
          statusMsg = status >= 41 ? this.$t('status.order.41') : this.$t(`status.order.${ status }`)
          isFinished = true
          canDel = status !== 36
        }
        this.flag = {
          msg, canPay, isFinished, statusMsg, canDel
        }
        if (status === 1) {
          this.$timer = setTimeout(() => {
            this.check()
          }, 1000)
        }
      } else {
        this.flag = null
      }
    }
  },
  watch: {
    order () {
      this.cancelled = false
      this.check()
    }
  },
  created () {
    this.$timer = -1
    this.check()
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.c-order {
  &.has-padding-top {
    padding-top: 10px;
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    padding: 24px 40px 24px 0;
  }

  &__status {
    padding: 60px 40px 60px 0;
    font-size: 50px;
    color: $color--dark;
    line-height: 70px;
    text-align: center;
  }
}

.o-order-footer {
  &__countdown {
    font-size: 28px;
    color: $color--error;
    line-height: 32px;
    @include u-font;
  }

  &__button {
    box-sizing: border-box;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 146px;
    height: 64px;
    padding: 0 32px;
    font-size: 28px;
    border-radius: 32px;

    &.is-hollow {
      color: $color-blue;
      border: 2px solid $color-blue;
    }

    &.is-primary {
      color: $color-white;
      background-color: $color-blue;
    }

    & + & {
      margin-left: 12px;
    }
  }
}
</style>
