<template>
  <div class="has-padding--v has-hairline--b">
    <template v-if="toggle">
      <div class="c-vlist-item--far is-tip has-padding--h">
        <span :class="{ 'u-color--dark': startYm === selectedYm, 'u-font--bold': startYm !== selectedYm }">{{ startYm }}</span>
        <span
          v-if="startYm !== endYm"
          class="u-h-separate"
          :class="{ 'u-color--dark': endYm === selectedYm, 'u-font--bold': endYm !== selectedYm }"
        >
          {{ endYm }}
        </span>
      </div>
      <div class="l-hlist c-vlist-item--far">
        <div class="l-hlist__wrapper">
          <div
            v-for="(item, index) in operatingList"
            :key="index"
            class="c-hlist-item--far o-tag"
            :class="{ 'is-primary': index === dayIndex }"
            @click="changeDay(index)"
          >
            <div class="t2">{{ item.day }}</div>
            <div class="s2 u-font--bold">{{ item.week }}</div>
          </div>
        </div>
      </div>
    </template>
    <div class="c-vlist-item--far has-padding--h">
      <div class="is-tip--dark">{{ $t('explore.detail.opening') }}</div>
      <div class="is-content--dark u-font--bold u-flat u-separate--nearer">
        <div v-if="timeList.valid">
          <span
            v-for="(item, index) in timeList.list"
            :key="index"
            class="u-h-separate-reverse--list"
            :class="{ 'u-color--orange': item.state === 0, 'u-line-through': item.state === 2 }"
          >
            {{ item.span }}
          </span>
        </div>
        <div v-else>{{ timeList.span }}</div>
      </div>
      <row
        v-if="remark"
        class="u-separate--nearest"
        icon="time"
        dark
      >
        {{ remark }}
      </row>
    </div>
    <div
      v-if="hasEnoughOperatingInfo"
      class="o-toggle o-toggle--calendar"
       @click="toggle = !toggle"
    >
      {{ selectedMd }}<i class="o-toggle__icon" :class="{ 'is-toggle': toggle }"></i>
    </div>
  </div>
</template>

<script>
import { transformTime } from '@/utils/format.js'
import Row from '@/components/RowIcon.vue'

export default {
  components: {
    Row
  },
  props: {
    entity: {
      type: Object,
      default () {
        return null
      }
    }
  },
  data () {
    return {
      dayIndex: 0,
      toggle: false,
      now: Date.now()
    }
  },
  computed: {
    hasShows () {
      return this.entity.activityList ? this.entity.activityList.length > 0 : false
    },
    remark () {
      if (this.hasShows) {
        return this.entity.activityList[0].remark
      }
      return ''
    },
    operatingList () {
      return this.hasShows ? this.transform(this.entity.activityList[0].timeList) : []
    },
    hasOperatingInfo () {
      return this.operatingList.length > 0
    },
    hasEnoughOperatingInfo () {
      return this.operatingList.length > 1
    },
    startYm () {
      return this.hasOperatingInfo ? this.operatingList[0].ym : ''
    },
    endYm () {
      return this.hasOperatingInfo ? this.operatingList[this.operatingList.length - 1].ym : ''
    },
    selectedYm () {
      return this.hasOperatingInfo ? this.operatingList[this.dayIndex].ym : ''
    },
    selectedMd () {
      return this.hasOperatingInfo ? this.operatingList[this.dayIndex].isToday ? this.$t('calendar.today') : this.operatingList[this.dayIndex].md : ''
    },
    timeList () {
      return this.hasOperatingInfo ? this.operatingList[this.dayIndex].list : {
        valid: false,
        span: this.$t('explore.detail.noshows')
      }
    }
  },
  methods: {
    transform (list) {
      if (!list || list.length === 0) {
        return []
      }
      let arr = []
      let temp = null
      for (let i = 0, j = list.length; i < j; i++) {
        if (!temp || this.isChange(temp, list[i])) {
          temp = this.transformOpening(list[i])
          arr.push(temp)
        }
        temp.list.push(list[i])
      }
      arr.forEach(item => item.list = this.transformTimeList(item.list))
      return arr
    },
    isChange (obj, item) {
      let date = new Date(item.startTime)
      return obj.year !== date.getFullYear() || obj.month !== date.getMonth() || obj.day !== date.getDate()
    },
    checkIsToday (year, month, day) {
      let today = new Date(this.now)
      return year === today.getFullYear() && month === today.getMonth() && day === today.getDate()
    },
    transformOpening (item) {
      let date = new Date(item.startTime)
      let year = date.getFullYear()
      let month = date.getMonth()
      let day = date.getDate()
      return {
        year: year,
        month: month,
        day: day,
        week: this.$t(`calendar.week.${ date.getDay() }`),
        ym: this.$t('time.ym', {
          year,
          month: this.$t(`calendar.month.${ date.getMonth() }`)
        }),
        md: this.$t('time.md', {
          month: month + 1,
          day
        }),
        isToday: this.checkIsToday(year, month, day),
        list: []
      }
    },
    transformTimeList (list) {
      let has = list.findIndex(item => item.status !== 'cancel' && item.endTime > this.now) > -1
      return {
        valid: has,
        span: has ? '' : this.$t('explore.detail.finished'),
        list: has ? list.map(item => {
          return {
            state: item.status === 'cancel' ? 2 : item.endTime <= this.now ? 1 : 0,
            span: transformTime(item.startTime)
          }
        }) : []
      }
    },
    changeDay (index) {
      this.dayIndex = index
    }
  }
}
</script>

<style lang="scss" scoped>
.o-toggle {
  position: absolute;
  top: 40px;
  right: 40px;
}
</style>
