<template>
  <div v-if="has" class="c-fixed-footer has-hairline--tb">
    <div v-if="hasOFT" class="o-button is-trigger is-auto" @click="toBuyOFT">{{ $t('explore.detail.btn.oft') }}</div>
    <div v-if="canBooking" class="o-button is-trigger is-auto" @click="toBooking">{{ $t('explore.detail.btn.booking') }}</div>
    <div v-if="canQueue" class="o-button is-trigger is-auto" @click="toQueue">{{ $t('explore.detail.btn.queue') }}</div>
    <div v-if="canOrder" class="o-button is-trigger is-auto" @click="toOrder">{{ $t('explore.detail.btn.order') }}</div>
    <div v-if="hasPhone" class="o-button is-trigger is-auto" @click="toCall">{{ $t('explore.detail.btn.phone') }}</div>
  </div>
</template>

<script>
export default {
  props: {
    entity: {
      type: Object,
      default () {
        return null
      }
    }
  },
  computed: {
    relateList () {
      return this.entity.relateList || []
    },
    hasOFT () {
      return this.relateList.findIndex(item => item.type === 'oft') >= 0
    },
    bookingList () {
      let relation = this.relateList.find(item => item.type === 'booking')
      if (relation && relation.list) {
        return relation.list.filter(item => !!item.url)
      }
      return []
    },
    canBooking () {
      return this.bookingList.length > 0
    },
    queueList () {
      let relation = this.relateList.find(item => item.type === 'queue')
      if (relation && relation.list) {
        return relation.list.filter(item => !!item.url)
      }
      return []
    },
    canQueue () {
      return this.queueList.length > 0
    },
    orderList () {
      let relation = this.relateList.find(item => item.type === 'electronicOrder')
      if (relation && relation.list) {
        return relation.list.filter(item => !!item.url)
      }
      return []
    },
    canOrder () {
      return this.orderList.length > 0
    },
    phoneList () {
      return this.entity.phone ? this.entity.phone.filter(item => item !== '') : []
    },
    hasPhone () {
      return this.phoneList.length > 0
    },
    has () {
      return this.hasOFT || this.canBooking || this.canQueue || this.canOrder || this.hasPhone
    }
  },
  methods: {
    toBuyOFT () {
      this.$features.toBuyTicket(4)
    },
    toBooking () {
      this.$features.web(this.bookingList[0].url)
    },
    toQueue () {
      this.$features.web(this.queueList[0].url)
    },
    toOrder () {
      this.$features.web(this.orderList[0].url)
    },
    toCall () {
      this.$features.phone(this.phoneList[0])
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
