export default {
  data () {
    return {
      dialogMsg: {
        show: false,
        title: '',
        content: '',
        btn: ''
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.dialogMsg.show) {
      this.dialogMsg.show = false
      if (this.$router.logout) {
        next()
      } else {
        next(false)
      }
    } else {
      next()
    }
  }
}
