<template>
  <div class="u-font--bold u-color--dark">
    <div v-if="hasShows && info.valid && isShowsType">
      <span v-for="(item, index) in info.span" :key="index" class="u-h-separate-reverse--list" :class="{ 'u-color--orange': item.state === 0, 'u-line-through': item.state === 2 }">{{ item.span }}</span>
    </div>
    <div v-if="hasShows && info.valid && !isShowsType">
      <span class="u-color--orange">{{ info.tip }}</span>
      <span class="is-tag">•</span>
      <span class="u-font">{{ info.span }}</span>
    </div>
    <div v-if="hasShows && !info.valid || !hasShows" :class="{ 'u-color--orange': info.valid }">{{ info.span }}</div>
  </div>
</template>

<script>
import { transformTime } from '@/utils/format.js'

export default {
  props: {
    entity: {
      type: Object,
      default () {
        return null
      }
    },
    status: {
      type: Object,
      default () {
        return {
          isOpeningNow: false,
          isSuspend: false,
          isClosed: false
        }
      }
    },
    now: {
      type: Number,
      default () {
        return Date.now()
      }
    }
  },
  computed: {
    isShowsType () {
      return this.entity.typeId === 4
    },
    hasShows () {
      return this.entity.activityList !== void 0
    },
    hasPFlow () {
      return this.entity.pflowStatus === 1
    },
    isSuspend () {
      return this.status.isSuspend
    },
    isClosed () {
      return this.status.isClosed
    },
    info () {
      if (this.isSuspend) {
        return this.isShowsType ? {
          valid: false,
          span: this.$t('explore.detail.suspends')
        } : this.transformOpening()
      } else if (this.isClosed) {
        return {
          valid: false,
          span: this.isShowsType || this.hasShows ? this.$t('explore.detail.noshows') : this.$t('explore.detail.closed')
        }
      } else {
        if (this.hasShows) {
          return this.transformShows()
        } else {
          return this.transformOpening()
        }
      }
    }
  },
  methods: {
    transformShows () {
      let { name, timeList } = this.entity.activityList[0]
      if (timeList.findIndex(item => item.status !== 'cancel' && item.endTime > this.now) === -1) {
        return {
          valid: false,
          span: this.$t('explore.detail.finished')
        }
      }
      if (this.isShowsType) {
        return {
          valid: true,
          span: timeList.map(item => {
            return {
              state: item.status === 'cancel' ? 2 : item.endTime <= this.now ? 1 : 0,
              span: transformTime(item.startTime)
            }
          })
        }
      } else {
        let playing = timeList.find(item => item.status !== 'cancel' && item.startTime <= this.now && item.endTime > this.now)
        if (playing) {
          return {
            valid: true,
            tip: this.$t('explore.detail.playing'),
            span: `${ transformTime(playing.startTime) } ${ name }`
          }
        }
        let soon = timeList.find(item => item.status !== 'cancel' && item.startTime > this.now)
        if (soon) {
          return {
            valid: true,
            tip: this.$t('explore.detail.soon'),
            span: `${ transformTime(soon.startTime) } ${ name }`
          }
        }
      }
    },
    transformOpening () {
      let valid
      let span
      if (this.hasPFlow) {
        let info = this.entity.pflowInfo.operatingHourList[0]
        valid = this.isOpeningNow && !this.isSuspend
        span = `${ transformTime(info.openTime) } - ${ transformTime(info.closeTime) }`
      } else {
        // 未关联景点默认无效
        valid = false
        span = this.entity.operatorInfo
      }
      return {
        valid, span
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.is-tag {
  display: inline-block;
  margin: 0 10px;
  transform: scale(.5);
}
</style>
