<template>
  <div class="u-align-center">
    <div class="o-tip-img" :class="classType"></div>
    <div class="is-tip--dark u-font u-separate--near">
      <div>{{ msg }}</div>
      <div v-if="tip" class="u-separate--gap">{{ tip }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: 'fastrack'
    },
    msg: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    }
  },
  computed: {
    classType () {
      return `is-${ this.type }`
    }
  }
}
</script>

<style lang="scss" scoped>
.o-tip-img {
  display: inline-block;
  width: 184px;
  height: 184px;
  background-image: url(~@/assets/img-haitun.png);
  @include bg-img-attrs;

  &.is-shared {
    background-image: url(~@/assets/img-pangxie.png);
  }
}
</style>
