import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  authInfo: null,
  config: JSON.parse(window.localStorage.getItem('config')) || {
    manageApiServerUrl: 'https://sop.videoseeing.com/admin',
    sharePic: '',
    picLoopTime: 3,
    maxEschedulerCount: 3,
    orderTimeoutLong: 15,
    orderTimeList: [],
    afterOrderTimeList: []
  },
  entityTypes: {
    timestamp: 0,
    index: 0,
    list: []
  },
  countryList: [],
  eschedulerTickets: null,
  userMails: [],
  parkMails: []
}

const getters = {
  isAuth (state) {
    return state.authInfo !== null
  },
  loginFacebook (state, getters) {
    return getters.isAuth && state.authInfo.type === 1
  },
  loginByWecaht (state, getters) {
    return getters.isAuth && state.authInfo.type === 2
  },
  // 仅在微信登录时有效
  isAbnormal (state, getters) {
    return getters.loginByWecaht && state.authInfo.status === 0
  },
  isRegistered (state, getters) {
    return getters.loginByWecaht && state.authInfo.status === 2
  },
  loginByEmail (state, getters) {
    return getters.isAuth && state.authInfo.type === 3
  },
  isLogined (state, getters) {
    if (getters.isAuth && state.authInfo.type > 0) {
      if (getters.loginByWecaht) {
        return state.authInfo.status === 1
      }
      return true
    }
    return false
  },
  baseUrl (state) {
    return state.config.manageApiServerUrl
  },
  loopInterval (state) {
    return Math.max(1, state.config.picLoopTime) * 1000
  },
  loopDuration () {
    return 500
  },
  orderTimeout (state) {
    return state.config.orderTimeoutLong * 60 * 1000
  },
  userInfo (state, getters) {
    return getters.isLogined ? state.authInfo.user : {}
  },
  nickName (state, getters) {
    let { firstName = '', lastName = '' } = getters.userInfo
    return `${ firstName } ${ lastName }`
  },
  avatarUrl (state, getters) {
    if (getters.userInfo.picInfo) {
      if (getters.userInfo.picInfo.cosUrl) {
        return getters.userInfo.picInfo.cosUrl
      }
      if (getters.baseUrl && getters.userInfo.picInfo.url) {
        return `${ getters.baseUrl }${ getters.userInfo.picInfo.url }`
      }
    }
    return ''
  },
  parkPopMails (state) {
    return state.parkMails.filter(mail => mail.origin.showType === 1 && mail.isFresh)
  },
  hasParkMails (state, getters) {
    return getters.parkPopMails.length > 0
  }
}

const mutations = {
  name (state, payload) {
    state.name = payload
  },
  config (state, payload) {
    state.config = payload
    window.localStorage.setItem('config', JSON.stringify(payload))
  },
  authInfo (state, payload) {
    if (payload) {
      state.authInfo = payload
      window.localStorage.setItem('authInfo', JSON.stringify(payload))
    } else {
      state.authInfo = null
      window.localStorage.removeItem('authInfo')
    }
  },
  entityTypes (state, payload) {
    state.entityTypes = payload
  },
  countryList (state, payload) {
    state.countryList = payload
  },
  orderTickets (state, payload) {
    state.orderTickets = payload
  },
  eschedulerTickets (state, payload) {
    state.eschedulerTickets = payload
  },
  userMails (state, payload) {
    state.userMails = payload
  },
  parkMails (state, payload) {
    state.parkMails = payload
  }
}

const actions = {
  userInfo ({ state, getters, commit }, payload) {
    if (getters.isLogined) {
      commit('authInfo', Object.assign({}, state.authInfo, {
        user: Object.assign({}, state.authInfo.user, payload)
      }))
    }
  },
  saveEntityTypes ({ commit }, payload) {
    commit('entityTypes', payload)
  },
  saveOrderTickets ({ commit }, payload) {
    commit('orderTickets', payload)
  },
  saveESchedulerTickets ({ commit }, payload) {
    commit('eschedulerTickets', payload)
  }
}

let store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  modules: {}
})

export default store
