import i18n from '@/i18n/index.js'

export function zeroFill (number, targetLength = 1) {
  number += ''
  return Math.pow(10, Math.max(0, targetLength - number.length)).toString().substr(1) + number
}

function getFullYear (date) {
  return date.getFullYear()
}

function getMonth (date) {
  return date.getMonth() + 1
}

function getDate (date) {
  return date.getDate()
}

function getHours (date) {
  return date.getHours()
}

function getMinutes (date) {
  return date.getMinutes()
}

function getSeconds (date) {
  return date.getSeconds()
}

const keys = ['sun', 'mon', 'tues', 'wed', 'thur', 'fri', 'sat']
function getWeek (date) {
  return i18n.t(`calendar.${ keys[date.getDay()] }`)
}

//时间戳转换成时间
export function dateFormat (timestamp, format = 'yyyy-MM-dd hh:mm:ss') {
  let realDate = typeof timestamp === 'object' ? timestamp : new Date(parseInt(timestamp))

  let date = [
    ['y+', getFullYear],
    ['M+', getMonth],
    ['d+', getDate],
    ['h+', getHours],
    ['m+', getMinutes],
    ['s+', getSeconds],
    ['w+', getWeek]
  ]

  for (let i = 0; i < date.length; i++) {
    let fn = date[i][1]
    format = format.replace(new RegExp(date[i][0]), function (result) {
      return zeroFill(fn(realDate), result.length)
    })
  }
  return format
}

export function transformTime (time) {
  let date = new Date(time)
  let hour = date.getHours()

  let str
  switch (true) {
    case hour < 12:
      str = 'time.morning'
      break
    case hour === 12:
      str = 'time.noon'
      break
    default:
      str = 'time.afternoon'
      hour -= 12
      break
  }

  let minute = date.getMinutes()

  if (i18n.locale === 'en_US') {
    return  i18n.t(str, { time: minute > 0 ? `${ hour }:${ minute }` : hour })
  }

  if (hour < 10) {
    hour = '0' + hour
  }
  if (minute < 10) {
    minute = '0' + minute
  }

  return i18n.t(str, { time: `${ hour }:${ minute }` })
}

export function transformOpening (entity) {
  let opening = false
  let todayItem = null
  let openingHour
  if (entity.pflowStatus === 1) {
    let todayS = new Date(dateFormat(Date.now(), 'yyyy/MM/dd')).getTime()
    let todayE = todayS + 3600 * 24 * 1000
    todayItem = entity.operatingHourList.find(item => {
      return item.openTime >= todayS && item.openTime < todayE
    })
  }
  if (todayItem) {
    openingHour = `${ transformTime(todayItem.openTime) } - ${ transformTime(todayItem.closeTime) }`
    let now = Date.now()
    if (entity.pflowStatus === 1 ? entity.entityStatus === 'open' : entity.openStatus === 1) {
      opening = now >= todayItem.openTime && now < todayItem.closeTime
    }
  } else {
    openingHour = i18n.t('explore.detail.closed')
  }
  return {
    opening, openingHour
  }
}

function transformTicketTime (date) {
  let { startTime, endTime } = date
  let start = dateFormat(startTime, 'yyyy.MM.dd')
  let end = dateFormat(endTime, 'yyyy.MM.dd')
  return start === end ? start : `${ start } - ${ end }`
}

export function transformTicketValidTime (ticket) {
  if (ticket.timeList && ticket.timeList.length > 0) {
    switch (ticket.timeType) {
      case 1:
        return `${ i18n.t('ticket.validd') } ${ dateFormat(ticket.timeList[0].endTime, 'yyyy.MM.dd') }`
      case 2:
        return i18n.t('ticket.valido', {
          time: ticket.timeList.map(transformTicketTime).join(', ')
        })
      default:
        break
    }
  }
  return ''
}

export function getLength (val) {
  return typeof val === 'string' ? val.replace(/[\u4e00-\u9fa5]/g, 'aa').length : 0
}
