<template>
  <div class="c-ticket-toggle" :class="{ 'is-open': toggle }">
    <div class="c-ticket-toggle__title l-flex--row u-relative">
      <div class="c-ticket-toggle__name is-ellipsis has-shadow--text l-flex__fill">{{ title }}</div>
      <div
        v-if="msg"
        class="is-mini l-flex--none u-h-separate--near u-align-right"
        :class="{ 'u-color--orange': valid }"
      >
        <div>{{ msg }}</div>
        <div v-if="tip">{{ tip }}</div>
      </div>
      <div
        v-if="hasShareBtn"
        class="o-share-status l-flex__none u-h-separate--near"
        :class="{ 'is-normal': status < 2 }"
        @click="toShare"
      >
        {{ shareMsg }}
        <van-icon
          v-if="status === 2"
          class="o-share-status__hook"
          name="passed"
        />
      </div>
    </div>
    <div v-if="toggle" class="c-ticket-toggle__content">
      <slot/>
    </div>
    <div class="u-align-center">
      <div class="c-ticket-toggle__wrapper">
        <i class="van-icon van-icon-arrow-down c-ticket-toggle__icon" @click="toToggle"></i>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant'

export default {
  components: {
    [ Icon.name ]: Icon
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    msg: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    },
    valid: {
      type: [Boolean, String],
      default: false
    },
    checked: {
      type: [Boolean, String],
      default: false
    },
    status: {
      type: Number,
      default: -1
    }
  },
  data () {
    return {
      toggle: this.checked
    }
  },
  computed: {
    hasShareBtn () {
      return this.status > -1
    },
    shareMsg () {
      switch (this.status) {
        case 0:
        case 1:
          return this.$t('mine.send')
        case 2:
          return this.$t('mine.sended')
        default:
          return ''
      }
    }
  },
  methods: {
    toToggle () {
      this.toggle = !this.toggle
      this.$emit('toggle', this.toggle)
    },
    toShare () {
      this.$emit('share')
    }
  }
}
</script>

<style lang="scss" scoped>
.c-ticket-toggle {
  padding: 48px 40px 0;

  &.is-open {
    .c-ticket-toggle__wrapper {
      padding-top: 10px;
    }

    .c-ticket-toggle__icon {
      transform: rotate(-180deg);
    }
  }

  &__title {
    height: 40px;
    font-size: 32px;
    color: $color--dark;
    line-height: 40px;
  }

  &__content {
    padding-top: 40px;
  }

  &__wrapper {
    padding-bottom: 10px;
  }

  &__icon {
    padding: 0 30px;
    font-size: 32px;
    color: $color;
    transition: transform .3s;
  }
}

.o-share-status {
  box-sizing: border-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  @include is-tip(#00C5A0);

  &.is-normal {
    height: 64px;
    padding: 0 32px;
    color: $color-blue;
    border: 2px solid $color-blue;
    border-radius: 32px;
  }

  &__hook {
    margin-left: 12px;
    font-size: 32px;
  }
}
</style>
