<template>
  <div class="o-cell has-hairline--b" @click="onClick">
    <div class="o-cell__label">{{ title }}</div>
    <div class="o-cell__right l-flex--row">
      <div class="o-cell__value">{{ value }}</div>
      <van-switch
        v-if="toggle"
        :value="checked"
        class="is-fixed-r"
        active-color="#00A9E0"
        inactive-color="#DDDDDD"
        @change="onSwitchChange"
      />
      <img
        v-if="arrow"
        class="o-cell__icon"
        src="@/assets/icon-arrow--right.png"
      />
      <slot/>
    </div>
  </div>
</template>

<script>
import { Switch } from 'vant'

export default {
  components: {
    [ Switch.name ]: Switch
  },
  props: {
    title: {
      // 列表标题
      type: String,
      default: ''
    },
    value: {
      // 列表内容
      type: String,
      default: ''
    },
    arrow: {
      // 是否显示箭头
      type: [Boolean, String],
      default: false
    },
    toggle: {
      // 是否显示Switch
      type: [Boolean, String],
      default: false
    },
    checked: {
      // Switch是否被选中
      type: [Boolean, String],
      default: false
    }
  },
  computed: {
    hasValue () {
      return !!this.value
    },
    isVertical () {
      return this.hasValue && this.vertical
    }
  },
  methods: {
    onClick () {
      this.$emit('click')
    },
    onSwitchChange (checked) {
      this.$emit('change', checked)
    }
  }
}
</script>

<style lang="scss" scoped>
.o-cell {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  height: 120px;
  @include u-font--bold;

  &__label {
    flex: 1;
    font-size: 32px;
    color: $color--dark;
    line-height: 40px;
  }

  &__right {
    flex: none;
    position: relative;
  }

  &__value {
    font-size: 28px;
    color: $color;
    line-height: 40px;
  }

  &__icon {
    width: 32px;
    height: 32px;
  }
}
</style>
