<template>
  <list-page
    :title="$t('sign.login.title')"
    :button="$t('sign.login.btn')"
    :disable="disable"
    @click="toLogin"
  >
    <template #footer>
      <div class="is-sub u-align-center">
        {{ $t('sign.noaccount') }}
        <router-link
          class="u-color--blue"
          :to="{ name: 'signup' }"
          replace
        >
          {{ $t('sign.tosign') }}
        </router-link>
      </div>
    </template>
    <template #extra>
      <div class="is-sub--blue u-align-center u-separate--farther">
        <router-link
          :to="{ name: 'forget' }"
        >
          {{ $t('sign.login.forget') }}
        </router-link>
      </div>
    </template>
    <field
      class="c-vlist-item--nearer"
      v-model="email"
      :title="$t('sign.info.email')"
      :placeholder="$t('sign.info.email')"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="password"
      type="password"
      :title="$t('sign.info.password')"
      :placeholder="$t('sign.info.password')"
      maxlength="16"
    />
    <van-checkbox
      class="c-vlist-item--nearer o-checkbox"
      v-model="checked"
      checked-color="#00A9E0"
    >
      {{ $t('sign.login.stay') }}
    </van-checkbox>
  </list-page>
</template>

<script>
import { save } from '@/utils/auth.js'
import { Checkbox } from 'vant'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    [ Checkbox.name ]: Checkbox,
    ListPage,
    Field
  },
  data () {
    return {
      email: '',
      password: '',
      checked: false
    }
  },
  computed: {
    disable () {
      return !this.email || !this.password
    }
  },
  methods: {
    toLogin () {
      let data = {
        email: this.email,
        password: this.$crypto.sha256(this.password)
      }

      if (this.checked) {
        data.tokenExpireTime = Date.now() + 15 * 24 * 3600 * 1000
      }

      this.$http.post('/api/app/user/email/login', data, {
        loading: true,
        server: true
      }).then(data => {
        save(3, data)
        this.$features.back()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
