<template>
  <list-page
    :title="$t('sign.reset.title')"
    :tip="$t('sign.reset.tip')"
    :button="$t('sign.reset.btn')"
    :disable="disable"
    @click="toReset"
  >
    <field
      class="c-vlist-item--nearer"
      v-model="npassword"
      type="password"
      :title="$t('sign.reset.npassword')"
      :placeholder="$t('sign.reset.npassword')"
      maxlength="16"
      :error="nerror"
      @input="nerror = ''"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="rpassword"
      type="password"
      :title="$t('sign.info.rpassword')"
      :placeholder="$t('sign.info.rpassword')"
      maxlength="16"
      :error="rerror"
      @input="rerror = ''"
    />
  </list-page>
</template>

<script>
import { save } from '@/utils/auth.js'
import { checkPassword } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    ListPage,
    Field
  },
  data () {
    return {
      npassword: '',
      nerror: '',
      rpassword: '',
      rerror: ''
    }
  },
  computed: {
    disable () {
      return !this.npassword || !this.rpassword
    }
  },
  methods: {
    toReset () {
      let nResult = checkPassword(this.npassword)
      this.nerror = nResult.msg

      let rResult = checkPassword(this.rpassword)
      this.rerror = rResult.msg

      if (nResult.pass && rResult.pass) {
        if (this.npassword !== this.rpassword) {
          this.rerror = this.$t('patt.pwd.diff')
        } else {
          this.$http.post('/api/app/user/email/resetPwd/change', {
            userIdType: Number(this.$route.query.userIdType),
            userId: Number(this.$route.query.userId),
            email: decodeURIComponent(this.$route.query.email),
            password: this.npassword
          }, {
            auth: false,
            loading: true,
            server: true,
            token: this.$route.query.token
          }).then(data => {
            save(3, data)
            this.$features.back(-2)
          })
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
