<template>
  <div
    class="o-cell--panel has-hairline--b"
    @click="onClick"
  >
    <div class="o-cell--panel__left">
      <template v-if="value">
        <div class="o-cell--panel__title is-ellipsis u-font--black">{{ title }}</div>
        <div
          class="is-ellipsis u-font--black"
          :class="{ 'u-color': disable }"
        >
          {{ value }}
        </div>
      </template>
      <template v-else>
        {{ placeholder }}
      </template>
    </div>
    <img
      v-if="!disable"
      class="o-cell--panel__icon"
      src="@/assets/icon-arrow--right.png"
    />
  </div>
</template>

<script>
export default {
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    disable: {
      type: [Boolean, String],
      default: false
    }
  },
  methods: {
    onClick () {
      if (!this.disable) {
        this.$emit('click')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.o-cell--panel {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  height: 120px;
  @include u-font--bold;

  &__left {
    flex: 1;
    font-size: 32px;
    color: $color--dark;
    line-height: 40px;
  }

  &__title {
    font-size: 24px;
    color: $color;
  }

  &__icon {
    flex: none;
    width: 32px;
    height: 32px;
  }
}
</style>
