<template>
  <div class="l-page">
    <v-nav :title="$t('common.order.title')"/>
    <div class="l-page__content">
      <template v-if="order">
        <order :order="order"/>
        <div class="is-tip--dark has-padding--around has-separate--t">
          <div class="c-vlist-item--nearer is-dflex--top">
            <div>{{ $t('common.order.use') }}</div>
            <div class="u-font u-separate--nearest">{{ $t('common.order.usec') }}</div>
          </div>
          <div class="u-separate--near">
            <div>{{ $t('common.order.open') }}</div>
            <div class="u-font u-pre-wrap u-separate--nearest">{{ $t('common.order.openc') }}</div>
          </div>
          <div class="u-separate--near">
            <div>{{ $t('common.order.extra') }}</div>
            <div class="u-font u-separate--nearest">{{ $t('common.order.extrac') }}</div>
          </div>
        </div>
        <div class="is-tip--dark has-padding--around has-separate--t u-font">
          <div class="c-vlist-item--nearer l-flex--row is-top">
            <div class="l-flex__none">{{ $t('common.order.state') }}</div>
            <div class="l-flex__fill u-align-right u-h-separate--near" :class="{ 'u-color--orange': isRefund }">{{ statusMsg }}</div>
          </div>
          <template v-if="payInfo">
            <div class="c-vlist-item--nearer l-flex--row is-top">
              <div class="l-flex__none">{{ $t('common.order.ptime') }}</div>
              <div class="l-flex__fill u-align-right u-h-separate--near">{{ payInfo.time }}</div>
            </div>
            <div class="c-vlist-item--nearer l-flex--row is-top">
              <div class="l-flex__none">{{ $t('common.order.spname') }}</div>
              <div class="l-flex__fill u-align-right u-h-separate--near">{{ $t('home.title') }}</div>
            </div>
            <div class="c-vlist-item--nearer l-flex--row is-top">
              <div class="l-flex__none">{{ $t('common.order.spno') }}</div>
              <div class="l-flex__fill u-align-right u-h-separate--near">{{ payInfo.transactionId }}</div>
            </div>
          </template>
          <div class="c-vlist-item--nearer l-flex--row is-top">
            <div class="l-flex__none">{{ $t('common.order.no') }}</div>
            <div class="l-flex__fill u-align-right u-h-separate--near">{{ order.info.id }}</div>
          </div>
          <div v-if="order.coupon" class="c-vlist-item--nearer l-flex--row is-top">
            <div class="l-flex__none">{{ $t('common.order.coupon') }}</div>
            <div class="l-flex__fill u-align-right u-h-separate--near">{{ order.coupon.id }}</div>
          </div>
          <div v-if="isRefund" class="c-vlist-item--nearer l-flex--row is-top">
            <div class="l-flex__none">{{ $t('common.order.refund') }}</div>
            <div class="l-flex__fill u-align-right u-h-separate--near">{{ refundTime }}</div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { dateFormat, transformTicketValidTime } from '@/utils/format.js'
import Order from '@/components/ticket/Order.vue'

export default {
  components: {
    Order
  },
  data () {
    return {
      order: null,
      payInfo: null,
      statusMsg: '',
      isRefund: false,
      refundTime: ''
    }
  },
  computed: {
    ...mapGetters(['orderTimeout'])
  },
  methods: {
    transform (order) {
      let { orderId, addTime, status, spOrderList, discountPrice, coupon, totalPrice, payInfo, refund } = order

      this.order = {
        info: {
          id: orderId,
          date: dateFormat(addTime, 'yyyy.MM.dd')
        },
        list: spOrderList[0].productList.map(ticket => {
          return {
            name: ticket.productName,
            count: ticket.productCount,
            total: ticket.price,
            date: transformTicketValidTime(ticket.extInfo)
          }
        }),
        subtotal: coupon ? totalPrice : null,
        discount: coupon ? coupon.discount ? `- ${ coupon.discount }` : coupon.name : '',
        total: coupon ? discountPrice : totalPrice
      }

      switch (status) {
        case 1:
        case 2:
        case 6:
        case 36:
          this.statusMsg = this.$t(`status.order.${ status }`)
          break
        default:
          this.statusMsg = status >= 41 ? this.$t('status.order.41') : this.$t('status.order.0')
          break
      }

      this.isRefund = status === 6
      if (this.isRefund && refund && refund.refundTime) {
        this.refundTime = dateFormat(refund.refundTime, 'yyyy.MM.dd hh:mm:ss')
      }

      if (payInfo) {
        this.payInfo = {
          time: payInfo.payTime ? dateFormat(payInfo.payTime, 'yyyy.MM.dd') : '',
          transactionId: payInfo.transactionId
        }
      }
    }
  },
  created () {
    if (this.$route.params.id) {
      this.$http.post('/api/common/user/order/detail', {
        orderId: this.$route.params.id
      }, {
        login: true,
        server: true
      }).then(data => {
        this.transform(data)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
