<template>
  <div class="l-page">
    <v-nav :title="$t('common.feedback.title')"/>
    <div class="l-page__content">
      <div class="has-padding--around">
        <div class="is-tip--dark">{{ $t('common.feedback.offering') }}</div>
        <div class="c-options u-separate--gap">
          <div
            v-for="(option, oindex) in options"
            :key="oindex"
            class="c-options__item o-option"
            :class="{ 'is-selected': oindex === index }"
            @click="toggle(oindex)"
          >
            {{ option.name }}
          </div>
        </div>
      </div>
      <div class="has-padding--around has-separate--t">
        <div class="l-flex--row">
          <div class="is-tip--dark l-flex__fill">{{ $t('common.feedback.message') }}</div>
          <div class="is-tip u-font l-flex__none">{{ message.length }}/200</div>
        </div>
        <field
          class="u-separate--nearer"
          v-model="message"
          :placeholder="$t('common.feedback.placeholder')"
          maxlength="200"
        />
      </div>
      <div class="has-padding--around has-separate--t">
        <div class="is-tip--dark">{{ $t('common.feedback.photos') }}</div>
        <div class="l-flex--row u-separate">
          <div
            v-for="(img, index) in imgs"
            :key="index"
            class="o-photo l-flex__fill">
            <v-resource class="o-photo__img" :info="img"/>
            <i class="o-photo__close">
              <van-icon name="cross"/>
            </i>
          </div>
          <div
            v-if="imgs.length < 5"
            class="o-photo is-add l-flex__fill"
            @click="toAdd"
          >
            <van-icon name="plus"/>
          </div>
        </div>
      </div>
      <div
        v-if="isLogined"
        class="has-padding--around has-separate--t"
      >
        <field
          v-model="email"
          :title="$t('sign.info.email')"
          :placeholder="$t('sign.info.emailholder')"
          maxlength="200"
          :error="emailError"
          @input="emailError = ''"
        />
      </div>
    </div>
    <div class="c-fixed-footer has-hairline--tb">
      <v-button
        class="l-flex__fill"
        :text="$t('btn.submit')"
        :disable="disable"
        small
        @click="toSubmit"
      />
    </div>
    <input ref="fileUpload" type="file" accept="image/*" style="display: none;" @change="handleFile"/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { checkEmail } from '@/utils/patt.js'
import { Icon } from 'vant'
import Field from '@/components/list/Field.vue'
import VButton from '@/components/VButton.vue'

export default {
  components: {
    [ Icon.name ]: Icon,
    Field,
    VButton
  },
  data () {
    return {
      os: '',
      index: -1,
      options: [],
      imgs: [],
      message: '',
      email: '',
      emailError: ''
    }
  },
  computed: {
    ...mapGetters(['isLogined']),
    isAndroid () {
      return this.os === 'android'
    },
    isIos () {
      return this.os === 'ios'
    },
    disable () {
      return this.index === -1 || !this.message
    }
  },
  methods: {
    toggle (index) {
      if (this.index === index) {
        this.index = -1
      } else {
        this.index = index
      }
    },
    upload (file, type) {
      this.$http.upload('/api/common/uploadPic', { file, type }, {
        loading: true,
        server: true
      }).then(data => this.imgs.push(data))
    },
    handleFile (e) {
      let target = e.target || e.srcElement
      let file = target.files[0]
      if (!file) {
        return
      }
      this.$refs.fileUpload.value = null
      this.upload(file, 'file')
    },
    toAdd () {
      if (this.isAndroid) {
        if (this.$refs.fileUpload) {
          this.$refs.fileUpload.click()
        }
      } else if (this.isIos) {
        this.$native.pickimage().then(result => {
          let file = result && result.image
          if (file) {
            let bytes = window.atob(file)
            let array = []
            for (let i = 0; i < bytes.length; i++) {
              array.push(bytes.charCodeAt(i))
            }
            this.upload(new Blob([new Uint8Array(array)], { type: 'image/jpeg' }), 'blob')
          }
        })
      }
    },
    toSubmit () {
      if (this.email) {
        let result = checkEmail(this.email)
        this.emailError = result.msg
        if (!result.pass) {
          return
        }
      }

      this.$http.post('/api/common/user/feedback', {
        typeId: this.options[this.index].id,
        content: this.message,
        email: this.email,
        picIds: this.imgs.map(img => img.id)
      }, {
        login: this.isLogined,
        loading: true,
        server: true
      }).then(() => {
        this.$toastInst = this.$toast.success(this.$t('common.feedback.success'))

        this.$timer = setTimeout(() => {
          this.$features.back()
        }, 3000)
      })
    }
  },
  created () {
    this.$http.post('/api/common/dic/values', {
      typeId: 'feedbackType'
    }, {
      server: true
    }).then(data => {
      this.options = data
    })

    this.$native.os().then(os => {
      this.os = os
    })
  },
  beforeDestroy () {
    if (this.$toastInst) {
      clearTimeout(this.$timer)
      this.$toastInst.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.o-photo {
  box-sizing: border-box;
  display: inline-block;
  position: relative;
  max-width: 20%;
  font-size: 36px;
  color: $color;

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 16px;
    overflow: hidden;
  }

  &__close {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    font-size: 28px;
    color: $color-white;
    border-radius: 50%;
    overflow: hidden;
    transform: translate(50%, -50%);
    background-color: $color-blue;
  }

  &::after {
    content: '';
    display: inline-block;
    padding-bottom: 100%;
  }

  &.is-add {
    border: 2px solid $color;
    border-radius: 16px;

    .van-icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  & + & {
    margin-left: 32px;
  }
}
</style>
