<template>
  <div
    class="o-phone"
    :class="{ 'is-error': isError }"
  >
    <div class="o-phone__wrapper has-hairline--b">
      <div class="o-phone__left l-flex--row has-hairline--r" @click.stop="onShowPicker">
        <div
          class="is-content--dark l-flex__fill"
          :class="{ 'u-color': !areaCode }"
        >
          {{ code }}
        </div>
        <van-loading
          v-if="loading"
          type="spinner"
          class="o-phone__loading"
        />
        <van-icon
          v-else
          class="o-phone__icon l-flex__none"
          name="arrow-down"
        />
      </div>
      <input
        class="o-phone__input"
        type="number"
        :placeholder="$t('common.phoneNumber')"
        :value="value"
        @input="onInput"
      />
    </div>
    <div class="o-phone__tip">{{ msg }}</div>
    <van-popup
      v-if="showCodePicker"
      v-model="showPicker"
      position="bottom"
      @closed="onHidePicker"
    >
      <van-picker
        :columns="columns"
        :show-toolbar="true"
        :confirm-button-text="$t('btn.done')"
        :cancel-button-text="$t('btn.cancel')"
        @confirm="onConfirmCode"
        @cancel="showPicker = false"
      />
    </van-popup>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Icon, Loading, Popup, Picker } from 'vant'

export default {
  components: {
    [ Icon.name ]: Icon,
    [ Loading.name ]: Loading,
    [ Popup.name ]: Popup,
    [ Picker.name ]: Picker
  },
  props: {
    areaCode: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    },
    error: {
      type: String,
      default: ''
    },
    maxlength: {
      type: [Number, String],
      default: 16
    }
  },
  data () {
    return {
      showCodePicker: false,
      showPicker: false,
      columns: null
    }
  },
  computed: {
    ...mapState(['countryList']),
    isError () {
      return !!this.error
    },
    msg () {
      return this.error || this.tip
    },
    loading () {
      return this.countryList.length === 0
    },
    code () {
      let code = this.areaCode || !this.loading && this.countryList[0].areaCode
      return code ? `+${ code }` : ''
    },
    index () {
      return this.areaCode ? this.countryList.findIndex(item => item.areaCode === this.areaCode) : -1
    }
  },
  methods: {
    onShowPicker () {
      if (!this.loading) {
        this.columns = [
          {
            values: this.countryList.map(country => `${ country.name } +${ country.areaCode }`),
            defaultIndex: Math.max(this.index, 0)
          }
        ]
        this.showCodePicker = true
        this.$nextTick(() => this.showPicker = true)
      }
    },
    onHidePicker () {
      this.showCodePicker = false
    },
    onConfirmCode (values, indexs) {
      this.showPicker = false
      this.$emit('change', this.countryList[indexs[0]])
    },
    onInput (e) {
      let val = e.target.value.slice(0, this.maxlength)
      if (val === this.value) {
        e.target.value = val
      } else {
        this.$emit('input', val)
      }
    },
    check () {
      if (!this.loading) {
        if (this.index > -1) {
          this.$emit('change', this.countryList[this.index])
        } else if (!this.areaCode) {
          this.$emit('change', this.countryList[0])
        }
      }
    }
  },
  watch: {
    countryList: {
      handler () {
        this.check()
      },
      immediate: true
    }
  }
}
</script>

<style lang="scss" scoped>
.o-phone {
  &.is-error {
    .o-phone__wrapper::after {
      border-bottom-color: $color--error;
    }

    .o-phone__tip {
      color: $color--error;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    height: 80px;
  }

  &__left {
    flex: none;
    align-self: stretch;
    box-sizing: border-box;
    position: relative;
    width: 210px;
    padding-right: 30px;
    margin-right: 30px;
  }

  &__icon {
    font-size: 32px;
    color: $color;
  }

  &__loading {
    color: $color;
    transform: scale(.6);
  }

  &__input {
    flex: 1 1 auto;
    min-width: 0;
    height: 40px;
    border: none;
    outline: none;
    @include u-font--bold;
    @include is-content($color--dark);

    &::-webkit-input-placeholder {
      color: $color;
    }
  }

  &__tip {
    min-height: 40px;
    font-size: 24px;
    color: $color;
    line-height: 40px;
    @include u-font;
  }
}
</style>
