<template>
  <list-page
    :title="$t('common.verify.code.title')"
    :tip="$t('common.verify.code.sms')"
  >
    <div class="c-code" @click="showKeyboard = true">
      <div
        v-for="(msg, index) in codeMsg"
        :key="index"
        class="c-hlist-item--near c-code__item"
        :class="{ 'is-selected': index === codeIndex }"
      >
        {{ msg }}
      </div>
    </div>
    <van-number-keyboard
      :show="showKeyboard"
      @input="onInput"
      @delete="onDelete"
      @blur="showKeyboard = false"
    />
    <div class="is-sub u-pre-wrap u-align-center u-separate--farther">
      {{ $t('common.verify.code.retry') }}
      <span
        :class="{ 'u-color': isSended, 'u-color--blue': !isSended }"
        @click="resend"
      >
        {{ $t('common.verify.code.send') }}{{ countMsg }}
      </span>
    </div>
  </list-page>
</template>

<script>
import { NumberKeyboard } from 'vant'
import ListPage from '@/components/list/ListPage.vue'

export default {
  components: {
    [ NumberKeyboard.name ]: NumberKeyboard,
    ListPage
  },
  props: {
    verify: {
      type: Object,
      default () {
        return null
      }
    }
  },
  data () {
    return {
      showKeyboard: false,
      code: '',
      count: 0
    }
  },
  computed: {
    isSended () {
      return this.count > 0
    },
    countMsg () {
      if (this.count > 0) {
        let minutes = Math.floor(this.count / 60)
        if (minutes < 10) {
          minutes = `0${ minutes }`
        }
        let seconds = this.count % 60
        if (seconds < 10) {
          seconds = `0${ seconds }`
        }
        return `(${ minutes }:${ seconds })`
      }
      return ''
    },
    codeIndex () {
      return this.showKeyboard ? this.code.length : -1
    },
    codeMsg () {
      let arr = []
      for (let i = 0; i < 4; i++) {
        arr[i] = i === this.codeIndex ? '' : this.code[i] || '-'
      }
      return arr
    }
  },
  methods: {
    resend () {
      if (this.verify && this.count === 0) {
        this.$http.post('/api/common/user/phone/smscode', this.verify.data, {
          login: true,
          loading: true,
          server: true
        }).then(({ expireTime }) => {
          this.verify.expireTime = expireTime
          this.toCount()
        })
      }
    },
    onInput (key) {
      if (this.showKeyboard && this.code.length < 4) {
        this.code += key
        if (this.code.length === 4) {
          this.showKeyboard = false
          this.toVerify()
        }
      }
    },
    onDelete () {
      if (this.showKeyboard) {
        this.code = this.code.slice(0, this.code.length - 1)
      }
    },
    toCount () {
      this.count = Math.floor(Math.max(this.verify.expireTime - Date.now(), 0) / 1000)
      if (this.count > 0) {
        this.$timer = setTimeout(this.toCount, 1000)
      }
    },
    toVerify () {
      if (this.verify) {
        let url
        switch (this.verify.type) {
          case 'bind':
            url = '/api/app/user/bindPhone'
            break
          default:
            return
        }

        this.$http.post(url, {
          areaCode: this.verify.data.areaCode,
          phone: this.verify.data.phone,
          code: this.code
        }, {
          login: true,
          loading: true,
          server: true
        }).then(() => {
          this.$store.dispatch('userInfo', {
            areaCode: this.verify.data.areaCode,
            phone: this.verify.data.phone
          })
          this.$router.replace({
            name: 'verify-phone-done'
          })
        }, () => this.code = '')
      }
    }
  },
  created () {
    this.$timer = -1
    if (this.verify) {
      this.toCount()
    }
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.c-code {
  display: flex;
  justify-content: center;
  height: 138px;

  &__item {
    flex: 1;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 138px;
    font-size: 72px;
    color: $color--dark;
    border-radius: 8px;
    border: 2px solid $color-separate;

    &.is-selected {
      border: 4px solid $color-blue;
    }
  }
}
</style>
