<template>
  <div class="c-vlist-item--nearer">
    <template v-if="isInput">
      <phone
        v-if="isPhone"
        v-model="info.value"
        :area-code="info.areaCode"
        :maxlength="info.from.maxLen"
        :error="error"
        @change="onAreaCodeChange"
      />
      <field
        v-else
        v-model="info.value"
        :title="originInfo.name"
        :placeholder="originInfo.name"
        :maxlength="info.from.maxLen"
        :error="error"
      />
    </template>
    <field
      v-if="isSelect"
      class="c-vlist-item--nearer"
      :value="msg"
      :title="originInfo.name"
      :placeholder="originInfo.name"
      readonly
      clickable
      @click="onClick"
    />
    <div
      v-if="isRadio"
      class="c-radio-group"
    >
      <div class="c-radio-group__title">{{ originInfo.name }}</div>
      <van-radio-group
        v-model="info.value"
        class="u-wrapper"
      >
        <van-radio
          v-for="item in originInfo.value"
          :key="item.id"
          class="c-radio-group__item"
          :name="item.id"
          checked-color="#00A9E0"
        >
          {{ item.name }}
        </van-radio>
      </van-radio-group>
    </div>
    <div
      v-if="hasRemark"
      class="o-reamrk is-tip--dark u-font--bold u-inline-block"
      v-html="originInfo.remark"
      @click.prevent="toLink"
    ></div>
    <required-item
      v-if="info.hasExtInfo"
      :info="info.extInfo"
    />
    <van-popup
      v-if="showPicker"
      v-model="show"
      position="bottom"
      @closed="onHide"
    >
      <van-picker
        :columns="columns"
        :show-toolbar="true"
        :confirm-button-text="$t('btn.done')"
        :cancel-button-text="$t('btn.cancel')"
        @confirm="onConfirm"
        @cancel="show = false"
      />
    </van-popup>
  </div>
</template>

<script>
import { getLength } from '@/utils/format.js'
import { RadioGroup, Radio, Popup, Picker } from 'vant'
import Field from '@/components/list/Field.vue'
import Phone from '@/components/list/Phone.vue'

export default {
  name: 'RequiredItem',
  components: {
    [ RadioGroup.name ]: RadioGroup,
    [ Radio.name ]: Radio,
    [ Popup.name ]: Popup,
    [ Picker.name ]: Picker,
    Field,
    Phone
  },
  props: {
    info: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      error: '',
      show: false,
      showPicker: false
    }
  },
  computed: {
    originInfo () {
      return this.info.from
    },
    isRadio () {
      return this.originInfo.valueType === 'radio'
    },
    isInput () {
      return this.originInfo.valueType === 'input'
    },
    isPhone () {
      return this.originInfo.isPhone
    },
    isSelect () {
      return this.originInfo.valueType === 'select'
    },
    selectIndex () {
      return this.isSelect ? this.originInfo.value.findIndex(val => val.id === this.info.value) : -1
    },
    columns () {
      return this.isSelect ? [
        {
          values: this.originInfo.value.map(val => val.name),
          defaultIndex: Math.max(this.selectIndex, 0)
        }
      ] : null
    },
    msg () {
      return this.selectIndex >= 0 ? this.originInfo.value[this.selectIndex].name : ''
    },
    hasRemark () {
      return this.originInfo.remark !== void 0 && this.originInfo.remark !== ''
    }
  },
  methods: {
    isValid (item) {
      let target = item.from
      let val = item.value

      if (item.required && !val) {
        return target.valueType === 'input' ? -1 : -2
      }

      if (val && target.valueType === 'input') {
        if (target.isPhone && !item.areaCode) {
          return -3
        }

        let length = target.isPhone ? val.length : getLength(val)
        if (length < target.minLen) {
          return -4
        }

        if (length > target.maxLen) {
          return -5
        }

        let regExp = target.regex || item.$regExp
        if (regExp && !(new RegExp(regExp).test(val))) {
          return -6
        }
      }

      return 1
    },
    checkValid () {
      let code = this.isValid(this.info)
      this.error = code < -2 ? ' ' : ''
      this.info.code = code
    },
    onAreaCodeChange ({ areaCode, regExp }) {
      this.info.areaCode = areaCode
      this.info.$regExp = regExp
      if (this.info.value) {
        this.checkValid()
      }
    },
    onClick () {
      this.showPicker = true
      this.$nextTick(() => this.show = true)
    },
    onHide () {
      this.showPicker = false
    },
    onConfirm (values, indexs) {
      this.$value = null
      this.info.value = this.originInfo.value[indexs[0]].id
      this.show = false
    },
    toLink () {
      if (this.originInfo.link && this.originInfo.link.length > 0 && this.originInfo.link[0].content) {
        this.$emit('link', this.originInfo.link[0].content)
      }
    }
  },
  watch: {
    'info.value' () {
      this.checkValid()
    }
  }
}
</script>

<style lang="scss" scoped>
.o-reamrk {
  margin: 16px 0 40px;
}

.c-radio-group {
  padding-bottom: 40px;

  &__title {
    margin-bottom: 24px;
    font-size: 28px;
    color: $color--dark;
    line-height: 40px;
  }

  &__item {
    display: inline-flex;
    margin-right: 48px;

    /deep/ .van-radio__label {
      @include u-font--bold;
      @include is-content($color--dark);
    }
  }
}
</style>
