import router from '@/router'
import store from '@/store'
import { log } from '@/utils/log.js'
import EventBus from '@/utils/event-bus.js'
import http from './http.js'
import { Toast } from 'vant'

function info () {
  return store.state.authInfo
}

export function save (type, data) {
  clearTimeout(timer)
  let authInfo = info()
  if (authInfo && type < authInfo.type) {
    return false
  }
  let { userId, userIdType, token, tokenExpire, ...user } = data
  store.commit('authInfo', {
    type, userId, userIdType, token, tokenExpire, user
  })
  if (store.getters.isLogined) {
    EventBus.$emit('login')
  }
  return true
}

let fetching = false
let timer = -1
function retry () {
  let authInfo = info()
  let time = authInfo.tokenExpire - Date.now()
  if (time < 3600000) {
    time = Math.floor(time / 3 * 2)
  } else {
    time -= 3600000
  }
  timer = setTimeout(authNomral, time)
}

export function authNomral (cb) {
  if (fetching) {
    return
  }

  clearTimeout(timer)

  if (store.getters.isLogined) {
    return
  }

  fetching = true
  http.post('/api/common/user/token', {
    deviceId: 'app-device'
  }, {
    globals: true,
    auth: false
  }).then(data => {
    fetching = false
    save(0, data) && retry()
    cb && cb()
  }, () => {
    fetching = false
    authNomral(cb)
  })
}

export function isExpired () {
  let authInfo = info()
  if (!authInfo) {
    log('Unauthorized')
    return true
  }

  let expire = authInfo.tokenExpire
  if (expire <= Date.now()) {
    log('Token is expired')
    logout()
    return true
  }

  // 提前10分钟获取
  if (expire <= Date.now() + 600000) {
    log('Token will expire in 10 minutes')
    authNomral()
  }

  return false
}

export function auth () {
  return new Promise(resolve => {
    let authInfo = window.localStorage.getItem('authInfo') || null
    if (authInfo) {
      authInfo = JSON.parse(authInfo)
      if (!authInfo.tokenExpire || authInfo.tokenExpire <= Date.now()) {
        log('Clear authorization')
        authInfo = null
        window.localStorage.removeItem('authInfo')
      }
    }
    if (authInfo) {
      store.commit('authInfo', authInfo)
      if (store.getters.isLogined) {
        EventBus.$emit('login')
      } else {
        retry()
      }
      resolve()
    } else {
      authNomral(resolve)
    }
  })
}

export function logout () {
  router.logout = true

  let toast = Toast.loading({
    className: 'u-higher',
    overlayClass: 'u-higher',
    overlay: true
  })

  store.commit('authInfo', null)
  EventBus.$emit('logout')

  authNomral(() => {
    toast.clear()
    toast = null
    router.replace({
      name: 'home'
    }, () => router.logout = false, () => router.logout = false)
  })
}
