<template>
  <div v-if="qr" class="c-qr">
    <template>
      <div class="c-qr__img">
        <v-resource
          class="is-cover"
          :class="{ 'is-abnormal': isAbnormal }"
          :info="qr"
        />
        <div
          v-if="isAbnormal"
          class="c-qr__status"
          :class="{ 'is-expired': isExpired }"
        >
          <div
            class="c-qr__text is-content--dark is-ellipsis"
          >
            {{ msg }}
          </div>
        </div>
      </div>
      <div
        v-if="qr.code"
        class="is-mini--dark u-font u-separate--nearest"
        :class="{ 'is-abnormal': isAbnormal }"
      >
        {{ qr.code }}
      </div>
    </template>
    <div v-if="qr.id" class="is-sub u-separate l-inline-flex" @click="toTicketDetail">
      {{ $t('mine.detail') }}<img class="o-icon--small u-h-separate--gap" src="@/assets/icon-info.png"/>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: {
    qr: {
      type: Object,
      default: null
    },
    expired: {
      type: [Boolean, String],
      default: false
    },
    status: {
      type: String,
      default: ''
    }
  },
  computed: {
    ...mapGetters(['baseUrl']),
    url () {
      return this.qr ? this.qr.cosUrl || (this.baseUrl && this.qr.url ? `${ this.baseUrl }${ this.qr.url }` : '') : ''
    },
    isAbnormal () {
      return this.status !== ''
    },
    isExpired () {
      return this.status === 'expired'
    },
    msg () {
      return this.status ? this.$t(`mine.${ this.status }`) : ''
    }
  },
  methods: {
    toTicketDetail () {
      this.$features.ticket(this.qr.id)
    }
  }
}
</script>

<style lang="scss" scoped>
.c-qr {
  padding: 18px 0 8px;
  text-align: center;

  &__img {
    position: relative;
    display: inline-block;
    width: 230px;
    height: 230px;

  }

  .is-abnormal {
    opacity: .2;
  }

  &__status {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 220px;
    height: 197px;
    background: url(~@/assets/status-abnormal.png) 0 0 / 100% 100% no-repeat;
    transform: translate(-50%, -50%);

    &.is-expired {
      background-image: url(~@/assets/status-expired.png);

      .c-qr__text {
        color: $color--error;
      }
    }
  }

  &__text {
    transform: rotate(-24deg);
  }
}
</style>
