<template>
  <div class="l-page">
    <v-nav :title="$t('ticket.buy.title')"/>
    <template v-if="tabs.length > 0">
      <div class="o-title is-tip--dark l-flex__none">{{ $t('ticket.buy.cticket') }}</div>
      <van-tabs
        class="c-nav-tabs l-flex__fill"
        type='none'
        v-model="tabIndex"
        :swipe-threshold="5"
        :ellipsis="false"
        swipeable
        animated
        @change="switchTab"
      >
        <van-tab
          v-for="(tab, index) in tabs"
          :key="index"
        >
          <template #title>
            <div
              class="o-option"
              :class="{ 'is-selected': tabIndex === index }"
            >
              {{ tab.name }}
            </div>
          </template>
          <div :ref="tab.tabId">
            <div
              v-if="tab.loading && tab.total === 0"
              class="has-padding--around u-align-center"
            >
              <van-loading type="spinner"/>
            </div>
            <div
              v-if="!tab.loading && tab.total === 0 && tab.timestamp !== 0"
              class="has-padding--around is-tip--dark u-align-center"
            >
              {{ $t('common.empty') }}
            </div>
            <div
              v-if="tab.total > 0"
              class="c-list"
            >
              <ticket
                v-for="(ticket, index) in tab.tickets"
                :key="index"
                class="has-hairline--b"
                :ticket="ticket"
                :free="free"
              />
            </div>
          </div>
        </van-tab>
      </van-tabs>
      <div class="c-fixed-footer c-footer has-hairline--tb l-flex--row is-top">
        <div class="l-flex__fill u-font--bold">
          <div class="is-title--block--orange">{{ totalInfo.money }}</div>
          <div class="is-mini">{{ totalInfo.count }}{{ $t('ticket.buy.count') }}</div>
        </div>
        <v-button
          class="o-footer-button l-flex__none"
          :text="$t('ticket.buy.check')"
          :disable="disable"
          small
          @click="toPreConfirm"
        />
      </div>
    </template>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :message="dialogMsg.content"
      :confirm-button-text="dialogMsg.btn"
    >
      <template v-if="!dialogMsg.content">
        <div class="c-dialog__message">{{ $t('ticket.buy.ask') }}</div>
        <div class="c-dialog__sheet">
          <div class="c-dialog__button" @click="toOrder">{{ $t('ticket.buy.tocont') }}</div>
          <div class="c-dialog__button" @click="toBuyAdmissionTicket">{{ $t('ticket.buy.tobuy') }}</div>
        </div>
      </template>
    </van-dialog>
    <transition name="slide-wrapper">
      <router-view :tickets="orderTickets" style="z-index: 9;"/>
    </transition>
  </div>
</template>

<script>
import { transformTicketValidTime } from '@/utils/format.js'
import { sortBySortIndex } from '@/utils/sort.js'
import LocaleMixin from '@/components/mixins/locale.js'
import DialogMixin from '@/components/mixins/dialog.js'
import { Loading, Tab, Tabs } from 'vant'
import VButton from '@/components/VButton.vue'
import Ticket from '@/components/ticket/Ticket.vue'

export default {
  mixins: [ LocaleMixin, DialogMixin ],
  components: {
    [ Loading.name ]: Loading,
    [ Tabs.name ]: Tabs,
    [ Tab.name ]: Tab,
    VButton,
    Ticket
  },
  data () {
    return {
      minBuyCount: 0,
      maxBuyCount: 0,
      tabs: [],
      timestamp: -1,
      tabIndex: 0,
      orderTickets: []
    }
  },
  computed: {
    totalInfo () {
      let money = ''
      let count = 0
      if (this.tabs.length > 0) {
        let currency = ''
        let val = this.tabs.reduce((money, tab) => money + tab.tickets.reduce((total, ticket) => {
          if (!currency) {
            currency = ticket.priceList[0].unit
          }
          count += ticket.count
          return total + ticket.count * ticket.priceList[0].realPrice
        }, 0), 0).toFixed(2)
        money = `${ currency } ${ val }`
      }
      return { count, money }
    },
    free () {
      return this.maxBuyCount - this.totalInfo.count
    },
    disable () {
      return this.totalInfo.count === 0 || this.totalInfo.count < this.minBuyCount
    }
  },
  methods: {
    switchTab (index) {
      this.tabIndex = index
      this.getList()
    },
    transformType (data) {
      return {
        tabId: `tab${ data.id }`,
        id: data.id,
        typeId: data.typeId,
        name: data.name,
        count: 0,
        money: 0,
        loading: false,
        timestamp: 0,
        total: 0,
        tickets: []
      }
    },
    transformTicket (ticket) {
      ticket.count = 0
      return ticket
    },
    getList () {
      let tab = this.tabs[this.tabIndex]
      if (!tab || tab.loading) {
        return
      }

      tab.loading = true

      this.$http.post('/api/common/ticket/list', {
        sortId: tab.id,
        length: 1000
      }, {
        login: true
      }).then(data => {
        if (tab.timestamp !== data.updateTime) {
          tab.timestamp = data.updateTime
          tab.count = 0
          tab.money = 0
          tab.tickets = (data.data || []).sort(sortBySortIndex).map(this.transformTicket)
          tab.total = tab.tickets.length
        }
      }).finally(() => tab.loading = false)
    },
    getData (type = -1) {
      this.$http.post('/api/common/ticket/sort/list', null, {
        login: true,
        server: this.tabs.length === 0
      }).then(data => {
        if (this.$timestamp !== data.updateTime) {
          this.$timestamp = data.updateTime
          this.minBuyCount = data.minBuyCount || 0
          this.maxBuyCount = data.maxBuyCount || 0

          let curr = this.tabs[this.tabIndex]
          let typeId = curr ? curr.typeId : type
          this.tabs = (data.data || []).sort(sortBySortIndex).map(this.transformType)
          if (this.tabs.length > 0 && typeId > -1) {
            this.tabIndex = Math.max(0, this.tabs.findIndex(item => item.typeId === typeId))
          } else {
            this.tabIndex = 0
          }

          if (data.msgInfo && data.msgInfo.content) {
            this.dialogMsg.content = data.msgInfo.content
            this.dialogMsg.btn = data.msgInfo.btn || this.$t('btn.ok')
            this.dialogMsg.show = true
          }

          this.getList()
        }
      })
    },
    getOrderTickets () {
      let arr = []
      this.tabs.forEach(tab => {
        tab.tickets.forEach(ticket => {
          if (ticket.count > 0) {
            arr.push({
              currency: ticket.priceList[0].id,
              typeId: ticket.typeId,
              ticketId: ticket.ticketId,
              name: ticket.name,
              count: ticket.count,
              total: `${ ticket.priceList[0].unit }${ (ticket.count * ticket.priceList[0].realPrice).toFixed(2) }`,
              date: transformTicketValidTime(ticket)
            })
          }
        })
      })
      return arr
    },
    toBuyAdmissionTicket () {
      let index = this.tabs.findIndex(tab => tab.typeId === 1)
      if (index !== this.tabIndex) {
        this.tabIndex = index
      }
      this.dialogMsg.show = false
    },
    toOrder () {
      this.dialogMsg.show = false
      this.$router.push({
        name: 'ticket-pre-order'
      })
    },
    toPreConfirm () {
      this.orderTickets = this.getOrderTickets()
      if (this.orderTickets.findIndex(ticket => ticket.typeId === 1) === -1 && this.tabs.findIndex(tab => tab.typeId === 1) > -1) {
        this.dialogMsg.content = ''
        this.dialogMsg.btn = this.$t('btn.cancelupper')
        this.dialogMsg.show = true
      } else {
        this.toOrder()
      }
    },
    onLocaleUpdated () {
      this.$timestamp = 0
      this.tabs = []
    }
  },
  created () {
    this.$timestamp = 0
  },
  activated () {
    this.tabs.forEach(tab => {
      tab.tickets.forEach(ticket => ticket.count = 0)
    })

    let type = this.$route.query.type ? Number(this.$route.query.type) : -1
    if (type > -1) {
      let index = this.tabs.findIndex(item => item.typeId === type)
      this.tabIndex = Math.max(0, index)
    } else {
      this.tabIndex = 0
    }

    this.getData(type)
    this.getList()
  },
  deactivated () {
    this.orderTickets = []
  }
}
</script>

<style lang="scss" scoped>
.o-title {
  padding: 40px 40px 20px;
}

.c-nav-tabs {
  display: flex;
  flex-direction: column;

  /deep/ .van-tabs__wrap {
    box-shadow: 0 4px 8px 0 rgba($color-black, .1);
    z-index: 9;
  }

  /deep/ .van-tabs__wrap::after {
    display: none;
  }

  /deep/ .van-tab {
    align-items: stretch;
    padding: 0;
    margin-left: 16px;
    margin-bottom: 40px;

    &:first-child {
      margin-left: 40px;
    }

    &:last-child {
      padding-right: 40px;
    }
  }

  /deep/ .van-tabs__nav {
    background-color: transparent;
  }

  /deep/ .van-tabs__content {
    flex: 1;
  }

  /deep/ .van-tab__pane-wrapper {
    overflow-y: auto;
  }
}

.c-list {
  padding-left: 40px;
}

.o-footer-button {
  min-width: 300px;
}
</style>
