<template>
  <div class="c-calendar">
    <div class="l-flex--row has-padding--around has-hairline--b">
      <i
        class="o-icon c-calendar__toggle is-rotate l-flex__none"
        :class="{ 'is-valid': canToPresent }"
        @click="toPresentMonth"
      ></i>
      <div class="is-title u-text-center l-flex__fill">{{ date }}</div>
      <i
        class="o-icon c-calendar__toggle l-flex__none"
        :class="{ 'is-valid': canToNext }"
        @click="toNextMonth"
      ></i>
    </div>
    <div class="c-calendar__week l-flex--row is-centered">
      <div class="c-calendar__day is-header">{{ $t('calendar.sun') }}</div>
      <div class="c-calendar__day is-header">{{ $t('calendar.mon') }}</div>
      <div class="c-calendar__day is-header">{{ $t('calendar.tues') }}</div>
      <div class="c-calendar__day is-header">{{ $t('calendar.wed') }}</div>
      <div class="c-calendar__day is-header">{{ $t('calendar.thur') }}</div>
      <div class="c-calendar__day is-header">{{ $t('calendar.fri') }}</div>
      <div class="c-calendar__day is-header">{{ $t('calendar.sat') }}</div>
    </div>
    <div
      v-for="(week, weekIndex) in weeks"
      :key="weekIndex"
      class="c-calendar__week l-flex--row is-centered"
    >
      <div
        v-for="(day, dayIndex) in week"
        :key="dayIndex"
        class="c-calendar__day"
        :class="{ 'is-today': day.today, 'is-disable': day.disable, 'is-selected': !day.disable && day.selected }"
        @click="toChoose(day)"
      >
        {{ day.day }}
      </div>
    </div>
  </div>
</template>

<script>
import { dateFormat } from '@/utils/format.js'

export default {
  props: {
    ranges: {
      type: Array,
      default () {
        return []
      }
    },
    day: {
      type: String,
      default: ''
    }
  },
  data () {
    let date = new Date()
    return {
      limits: [],
      limitsOfYM: [],
      year: date.getFullYear(),
      month: date.getMonth() + 1,
      weeks: [],
      canToPresent: false,
      canToNext: false,
      min: null,
      max: null,
      current: null,
      choose: null,
      today: null,
      tomorrow: null
    }
  },
  computed: {
    date () {
      return this.current ? this.$t('calendar.tip', {
        year: this.current.year,
        month: this.$t(`calendar.month.${ this.current.month }`)
      }) : ''
    }
  },
  methods: {
    transform (timestamp) {
      let date = new Date(timestamp)
      return {
        year: date.getFullYear(),
        month: date.getMonth(),
        day: date.getDate()
      }
    },
    isSame (a, b) {
      return a && b ? a.year === b.year && a.month === b.month && a.day === b.day : false
    },
    compare (a, b, full = false) {
      if (a.year === b.year) {
        if (a.month === b.month) {
          if (full) {
            if (a.day === b.day) {
              return 0
            }
            return a.day === b.day ? 0 : a.day - b.day
          }
          return 0
        }
        return a.month - b.month
      }
      return a.year - b.year
    },
    createItem (date, disable = false) {
      let item = {
        year: date.getFullYear(),
        month: date.getMonth(),
        day: date.getDate(),
        week: date.getDay(),
        weekend: ((date.getDay() + 6) % 7) >= 5,
        disable
      }

      item.today = disable || this.isSame(item, this.today)
      item.selected = disable || this.isSame(item, this.choose)

      return item
    },
    isDisableDay (day) {
      if (this.compare(day, this.min, true) < 0) {
        return true
      }

      if (this.max !== null && this.compare(day, this.max, true) > 0) {
        return true
      }

      if (this.limits.length > 0) {
        return this.limits.findIndex(limit => {
          return this.compare(day, limit.startTime, true) >= 0 && this.compare(day, limit.endTime, true) <= 0
        }) === -1
      }

      return false
    },
    _createFillDays (count) {
      let arr = []
      for (let i = 0; i < count; i++) {
        arr.push(this.createEmptyItem())
      }
      return arr
    },
    _currentMonthDays (year, month) {
      let dateArr = []
      let lastDay = new Date(year, month + 1, 0).getDate()

      for (let i = 1; i <= lastDay; i++) {
        let day = this.createItem(new Date(year, month, i))
        day.disable = this.isDisableDay(day)
        dateArr.push(day)
      }

      return dateArr
    },
    _getPreentMonthDays (year, month) {
      let dateArr = []

      for (let i = new Date(year, month, 1).getDay(); i > 0; i--) {
        dateArr.push(this.createItem(new Date(year, month, 1 - i), true))
      }

      return dateArr
    },
    _getNextMonthDays (year, month, count) {
      let dateArr = []

      for (let i = 0; i < count; i++) {
        dateArr.push(this.createItem(new Date(year, month + 1, 1 + i), true))
      }

      return dateArr
    },
    calculate () {
      let { year, month } = this.current
      let monthDays = this._getPreentMonthDays(year, month).concat(this._currentMonthDays(year, month))
      let dates = monthDays.concat(this._getNextMonthDays(year, month, 42 - monthDays.length))
      let weeks = []
      let row
      for (let i = 0; i < dates.length; i++) {
        if (i % 7 === 0) {
          row = weeks.length
          weeks[row] = []
        }
        weeks[row][i % 7] = dates[i]
      }

      this.weeks = weeks

      this.canToPresent = this.compare(this.current, this.min) > 0
      this.canToNext = this.max === null || this.compare(this.current, this.max) < 0
    },
    _offsetMonth (offset) {
      if (offset < 0) {
        if (this.current.month === 0) {
          this.current.year -= 1
          this.current.month = 11
        } else {
          this.current.month -= 1
        }
      } else if (offset > 0) {
        if (this.current.month === 11) {
          this.current.year += 1
          this.current.month = 0
        } else {
          this.current.month += 1
        }
      }
    },
    toPresentMonth () {
      if (this.canToPresent) {
        if (this.limits.length > 0) {
          let index = -1
          for (let i = this.limitsOfYM.length - 1; i >= 0; i--) {
            if (this.compare(this.limitsOfYM[i], this.current) < 0) {
              index = i
              break
            }
          }
          if (index === -1) {
            return
          }
          this.current = {
            ...this.limitsOfYM[index]
          }
        } else {
          this._offsetMonth(-1)
        }
        this.calculate()
      }
    },
    toNextMonth () {
      if (this.canToNext) {
        if (this.limits.length > 0) {
          let index = this.limitsOfYM.findIndex(item => this.compare(item, this.current) > 0)
          if (index === -1) {
            return
          }
          this.current = {
            ...this.limitsOfYM[index]
          }
        } else {
          this._offsetMonth(1)
        }
        this.calculate()
      }
    },
    _fillZero (num) {
      return num >= 10 ? '' + num : '0' + num
    },
    toChoose (sday) {
      if (sday.disable) {
        return
      }

      if (this.choose && this.isSame(this.choose, sday)) {
        return
      }

      if (this.choose && this.compare(this.choose, this.current) === 0) {
        let index = this.choose.day - 1 + new Date(this.choose.year, this.choose.month, 1).getDay()
        this.weeks[Math.floor(index / 7)][index % 7].selected = false
      }

      sday.selected = true

      let { year, month, day } = sday
      this.choose = {
        year, month, day
      }
      this.$emit('change', `${ year }-${ this._fillZero(month + 1) }-${ this._fillZero(day) }`)
    },
    init () {
      let today = new Date(dateFormat(Date.now(), 'yyyy/MM/dd')).getTime()
      if (this.ranges.length > 0) {
        this.limits = this.ranges.filter(item => item.endTime >= today).map(item => {
          return { startTime: this.transform(item.startTime && item.startTime > today ? item.startTime : today), endTime: this.transform(item.endTime) }
        })
      } else {
        this.limits = []
      }

      if (this.limits.length > 0) {
        let ym = []
        let cache = {}
        this.limits.forEach(item => {
          let { year: a, month: b } = item.startTime
          let { year: d, month: e } = item.endTime
          if (!cache[a]) {
            cache[a] = {
              [ b ]: true
            }
            ym.push({ year: a, month: b })
          } else if (!cache[a][b]) {
            cache[a][b] = true
            ym.push({ year: a, month: b })
          }

          if (!cache[d]) {
            cache[d] = {
              [ e ]: true
            }
            ym.push({ year: d, month: e })
          } else if (!cache[d][e]) {
            cache[d][e] = true
            ym.push({ year: d, month: e })
          }
        })
        this.limitsOfYM = ym
        this.min = this.limits[0].startTime
        this.max = this.limits[this.limits.length - 1].endTime
      } else {
        this.min = this.transform(today)
        this.limitsOfYM = []
      }

      this.current = {
        ...this.min
      }

      if (this.day) {
        let date = this.transform(new Date(this.day.replace(/-/g, '/')))
        if (!this.isDisableDay(date)) {
          this.current = {
            ...date
          }
          this.choose = date
        }
      }

      this.today = this.transform(today)

      this.calculate()
    }
  },
  watch: {
    ranges: {
      handler () {
        this.init()
      },
      immediate: true
    }
  }
}
</script>

<style lang="scss" scoped>
.c-calendar {
  padding-bottom: 40px;

  &__toggle {
    background: url(~@/assets/icon-arrow--right.png) 0 0 / 100% 100% no-repeat;

    &.is-rotate {
      transform: rotate(180deg);
    }

    &.is-valid {
      background-image: url(~@/assets/icon-arrow--right--blue.png);
    }
  }

  &__week {
    display: flex;
    justify-content: center;
    align-items: center;

    & + & {
      margin-top: 32px;
    }
  }

  &__day {
    flex: none;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 80px;
    border-radius: 50%;
    @include u-font--bold;
    @include is-content($color--dark);

    & + & {
      margin-left: 20px;
    }

    &.is-header {
      color: $color;
    }

    &.is-today {
      color: $color-blue;
    }

    &.is-disable {
      color: $color-separate;
    }

    &.is-selected {
      color: $color-white;
      background-color: $color-blue;
    }
  }
}
</style>
