<template>
  <div class="c-extras">
    <div v-for="(activity, index) in activityList" :key="index" class="has-padding--around has-hairline--b">
      <div class="is-tip--dark">{{ activity.name }}</div>
      <div class="is-content--dark u-font--bold u-flat u-separate--nearer">
        <div v-if="activity.valid">
          <span v-for="(item, index) in activity.list" :key="index" class="u-h-separate-reverse--list" :class="{ 'u-color--orange': item.state === 0, 'u-line-through': item.state === 2 }">{{ item.span }}</span>
        </div>
        <div v-else>{{ activity.span }}</div>
      </div>
      <row
        v-if="activity.remark"
        class="u-separate--nearest"
        icon="time"
        dark
      >
        {{ activity.remark }}
      </row>
    </div>
    <div v-if="hasEntityList" class="has-padding--around has-hairline--b">
      <div class="is-tip--dark">{{ $t('explore.detail.entity') }}</div>
      <div>
        <div v-for="(info, index) in entityList" :key="index" class="c-entity has-hairline--t" @click="toEntity(info)">
          <v-resource class="c-entity__img" :info="info"/>
          <div class="c-entity__content">
            <div class="is-content--dark is-ellipsis">{{ info.name }}</div>
            <row
              v-if="info.location"
              class="u-separate--nearest"
              icon="location"
              dark
            >
              {{ info.location }}
            </row>
            <row
              v-if="info.valid"
              class="u-separate--nearest"
              icon="time"
              dark
            >
              <div class="is-ellipsis" :class="{ 'u-color--orange': info.opening }">{{ info.span }}</div>
            </row>
          </div>
        </div>
      </div>
    </div>
    <dining v-if="hasMenu" :entity="entity"/>
    <div v-if="hasShopType" class="has-padding--panel has-hairline--b">
      <row
        icon="tag"
        large
      >
        <div class="is-content--dark">{{ shopType }}</div>
      </row>
    </div>
    <div v-if="hasCondition" class="has-padding--around has-hairline--b">
      <div class="is-tip--dark">{{ $t('explore.detail.condition') }}</div>
      <div class="is-content--dark u-font u-separate--nearer">
        <div v-for="(condition, index) in conditionList" :key="index">{{ condition }}</div>
      </div>
    </div>
    <toggle v-if="hasShopExtras" class="has-hairline--b" :title="$t('explore.detail.extra')">
      <div class="is-content u-separate--nearer">
        <div v-for="(extra, index) in shopExtras" :key="index">{{ extra }}</div>
      </div>
    </toggle>
    <toggle v-if="hasRemark" class="has-hairline--b" :title="$t('explore.detail.remark')">
      <div class="is-content u-separate--nearer">
        <div v-for="(remark, index) in remarkList" :key="index">{{ remark }}</div>
      </div>
    </toggle>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { transformTime } from '@/utils/format.js'
import Toggle from '@/components/Toggle.vue'
import Row from '@/components/RowIcon.vue'
import Dining from './Dining.vue'

export default {
  components: {
    Toggle,
    Row,
    Dining
  },
  props: {
    entity: {
      type: Object,
      default () {
        return null
      }
    },
    status: {
      type: Object,
      default () {
        return {
          isOpeningNow: false,
          isSuspend: false,
          isClosed: false
        }
      }
    }
  },
  data () {
    return {
      now: Date.now()
    }
  },
  computed: {
    ...mapGetters(['baseUrl']),
    activityList () {
      // 非表演分类的表演关联需在非suspend与非close下显示
      return (this.entity.typeId !== 4 && !this.status.isSuspend && !this.status.isClosed && this.entity.activityList || []).map(this.transformActivity)
    },
    hasActivityList () {
      return this.activityList.length > 0
    },
    entityList () {
      if (this.entity.relateList) {
        let entity = this.entity.relateList.find(item => item.type === 'entity')
        return entity ? entity.list.map(this.transformEntity) : []
      }
      return []
    },
    hasEntityList () {
      return this.entityList.length > 0
    },
    conditionList () {
      return this.entity.conditionList && this.entity.conditionList.filter(this.filterEmpty) || []
    },
    hasCondition () {
      return this.conditionList.length > 0
    },
    hasMenu () {
      return this.entity.menu !== void 0
    },
    hasShopAttr () {
      return this.entity.shopAttr !== void 0
    },
    shopType () {
      if (this.hasShopAttr) {
        let attr = this.entity.shopAttr
        let info = []
        if (attr.productType) {
          info.push(attr.productType)
        }
        if (attr.themeType) {
          info = info.concat(attr.themeType.filter(this.filterEmpty))
        }
        return info.join('  •  ')
      }
      return ''
    },
    hasShopType () {
      return this.shopType !== ''
    },
    shopExtras () {
      if (this.hasShopAttr && this.entity.shopAttr.extList) {
        return this.entity.shopAttr.extList.filter(this.filterEmpty)
      }
      return []
    },
    hasShopExtras () {
      return this.shopExtras.length > 0
    },
    remarkList () {
      return this.entity.remark && this.entity.remark.filter(this.filterEmpty) || []
    },
    hasRemark () {
      return this.remarkList.length > 0
    }
  },
  methods: {
    filterEmpty (obj) {
      return obj !== ''
    },
    transformActivity (activity) {
      let has = activity.timeList ? activity.timeList.length > 0 : false
      let finished = has && activity.timeList.findIndex(item => item.status !== 'cancel' && item.endTime > this.now) === -1
      let valid = has && !finished
      return {
        name: activity.name,
        valid: valid,
        span: has ? finished ? this.$t('explore.detail.finished') : '' : this.$t('explore.detail.noshows'),
        list: valid ? activity.timeList.map(item => {
          return {
            state: item.status === 'cancel' ? 2 : item.endTime <= this.now ? 1 : 0,
            span: transformTime(item.startTime)
          }
        }) : [],
        remark: activity.remark
      }
    },
    transformEntity (entity) {
      let valid
      let opening
      let span
      if (entity.operatingHourList && entity.operatingHourList.length > 0) {
        let info = entity.operatingHourList[0]
        let now = Date.now()
        opening = info.openTime <= now && now < info.closeTime
        span = `${ transformTime(info.openTime) } - ${ transformTime(info.closeTime) }`
        valid = true
      } else {
        opening = false
        span = entity.operatorInfo
        valid = !!span
      }
      return {
        id: entity.id,
        name: entity.name,
        location: entity.location,
        cosUrl: entity.picInfo && entity.picInfo.cosUrl || '',
        url: entity.picInfo && entity.picInfo.url || '',
        valid, span, opening
      }
    },
    toEntity (entity) {
      this.$features.entity(entity.id)
    }
  }
}
</script>

<style lang="scss" scoped>
.c-entity {
  display: flex;
  padding: 32px 0;

  &:first-child::after {
    display: none;
  }

  &:last-child {
    padding-bottom: 0;
  }

  &__img {
    flex: none;
    width: 180px;
    height: 180px;
    border-radius: 16px;
    overflow: hidden;
  }

  &__content {
    flex: 1;
    padding: 20px 0 0 20px;
  }
}
</style>
