<template>
  <div v-if="hasBanners" class="o-banner" :class="{ 'is-primary': primary }">
    <van-swipe
      class="o-banner__swiper"
      :autoplay="autoplayDuration"
      :duration="loopDuration"
      indicator-color="white"
      @change="onBannerChange"
    >
      <van-swipe-item
        v-for="(item, index) in banners"
        :key="index"
        @click="onClick(index)"
      >
        <v-resource
          class="is-cover"
          :info="item"
          @play="stopAuto"
          @pause="startAuto"
          @error="startAuto"
          @ended="startAuto"
        />
      </van-swipe-item>
    </van-swipe>
    <slot/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { Swipe, SwipeItem } from 'vant'

export default {
  components: {
    [ Swipe.name ]: Swipe,
    [ SwipeItem.name ]: SwipeItem
  },
  props: {
    list: {
      type: Array,
      default () {
        return []
      }
    },
    primary: {
      type: [Boolean, String],
      default: false
    },
    inst: {
      type: [Boolean, String],
      default: false
    },
    auto: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      autoplay: true
    }
  },
  computed: {
    ...mapGetters(['loopInterval', 'loopDuration']),
    banners () {
      let list = this.list || []
      if (this.inst) {
        return list.map(item => item.picInfo)
      }
      return list
    },
    hasBanners () {
      return this.banners.length > 0
    },
    autoplayDuration () {
      return this.auto && this.autoplay ? this.loopInterval : 0
    }
  },
  methods: {
    onBannerChange () {
      if (!this.autoplay) {
        this.startAuto()
      }
    },
    onClick (index) {
      this.$emit('emit', index)
    },
    stopAuto () {
      this.autoplay = false
    },
    startAuto () {
      this.autoplay = true
    }
  }
}
</script>

<style lang="scss" scoped>
.o-banner {
  position: relative;
  height: 564px;
  z-index: 3;

  &.is-primary {
    height: 498px;
  }

  &__swiper {
    height: 100%;
  }
}
</style>
