import '@/scss/index.scss'
import 'amfe-flexible'

import Vue from 'vue'

import router from '@/router'
import store from '@/store'
import i18n from '@/i18n'

import cfg from 'config'

import '@/globals.js'

import { log, setEnable } from '@/utils/log.js'
import { inject } from '@/utils/js-native-api.js'
import { auth } from '@/utils/auth.js'
import { constant } from '@/utils/common.js'

import App from '@/App.vue'

Vue.config.productionTip = cfg.isDebug
Vue.config.devtools = cfg.isDebug

if (cfg.isDebug) {
  Vue.config.warnHandler = function (msg, vm, trace) {
    console.log(msg, trace)
  }
  Vue.config.errorHandler = function (err, vm, info) {
    console.log(err, info)
  }
  setEnable(false, true)
} else {
  setEnable(true, false)
}

log(process.env.APP_VERSION)

inject()
  .then(api => {
    if (i18n.locale === 'none') {
      return api.language().then(lang => {
        lang = lang.toLocaleUpperCase()
        switch (true) {
          case /^zh.*hk$/.test(lang):
          case /^zh.*tw$/.test(lang):
            i18n.locale = 'tc'
            break
          case /^zh/.test(lang):
            i18n.locale = 'sc'
            break
          default:
            i18n.locale = 'en'
            break
        }
      }, () => i18n.locale = 'en')
    }
  })
  .then(auth)
  .then(() => {
    log(`language is ${ i18n.locale }`)

    constant()

    if (!window.localStorage.getItem('GUIDE')) {
      window.localStorage.setItem('GUIDE', 'GUIDE')
      return import('@/components/guide').then(module => module.default(i18n))
    }
  }).then(() => {
    new Vue({
      store,
      router,
      i18n,
      render: h => h(App)
    }).$mount('#app')
  })
