<template>
  <div class="l-page">
    <v-nav :title="$t('common.weather.title')"/>
    <div class="l-page__content">
      <div v-if="weather" class="c-weather has-padding--around has-separate--b">
        <div class="is-tip--dark">{{ $t('common.weather.current') }}</div>
        <div class="l-flex--row u-separate--near">
          <v-resource class="c-weather__img" :info="weather.picInfo"/>
          <div class="c-weather__value u-font u-h-separate--far">{{ weather.temperature }}°C</div>
        </div>
      </div>
      <div class="u-sticky u-background--white">
        <div class="c-weather-tabs__title is-tip--dark">{{ $t('common.weather.other') }}</div>
        <div class="l-flex--row has-shadow--short">
          <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 0 }" @click="switchTab(0)">{{ $t('common.weather.rain') }}</div>
          <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 1 }" @click="switchTab(1)">{{ $t('common.weather.wind') }}</div>
        </div>
      </div>
      <van-swipe
        :initial-swipe="tabIndex"
        :show-indicators="false"
        :loop="false"
        @change="switchTab"
      >
        <van-swipe-item>
          <div ref="rain" class="c-weather-tabs__content">
            <div class="c-vlist-item--farther">
              <img class="o-weather-img" src="https://sop-1257079150.cos.ap-hongkong.myqcloud.com/weather/wether1.png"/>
              <div class="is-title u-separate">{{ $t('common.weather.yrain') }}</div>
              <div class="is-content--dark u-font u-separate--nearer">{{ $t('common.weather.yrainc') }}</div>
            </div>
            <div class="c-vlist-item--farther">
              <img class="o-weather-img" src="https://sop-1257079150.cos.ap-hongkong.myqcloud.com/weather/wether3.png"/>
              <div class="is-title u-separate">{{ $t('common.weather.rrain') }}</div>
              <div class="is-content--dark u-font u-separate--nearer">{{ $t('common.weather.rrainc') }}</div>
            </div>
            <div class="c-vlist-item--farther">
              <img class="o-weather-img" src="https://sop-1257079150.cos.ap-hongkong.myqcloud.com/weather/wether5.png"/>
              <div class="is-title u-separate">{{ $t('common.weather.brain') }}</div>
              <div class="is-content--dark u-font u-pre-wrap u-separate--nearer">{{ $t('common.weather.brainc') }}</div>
            </div>
          </div>
        </van-swipe-item>
        <van-swipe-item>
          <div ref="wind" class="c-weather-tabs__content">
            <div class="c-vlist-item--farther">
              <img class="o-weather-img" src="https://sop-1257079150.cos.ap-hongkong.myqcloud.com/weather/wether2.png"/>
              <div class="is-title u-separate">{{ $t('common.weather.wind1') }}</div>
              <div class="is-content--dark u-font u-separate--nearer">{{ $t('common.weather.wind1c') }}</div>
            </div>
            <div class="c-vlist-item--farther">
              <img class="o-weather-img" src="https://sop-1257079150.cos.ap-hongkong.myqcloud.com/weather/wether4.png"/>
              <div class="is-title u-separate">{{ $t('common.weather.wind3') }}</div>
              <div class="is-content--dark u-font u-separate--nearer">{{ $t('common.weather.wind3c') }}</div>
            </div>
            <div class="c-vlist-item--farther">
              <img class="o-weather-img" src="https://sop-1257079150.cos.ap-hongkong.myqcloud.com/weather/wether6.png"/>
              <div class="is-title u-separate">{{ $t('common.weather.wind8') }}</div>
              <div class="is-content--dark u-font u-pre-wrap u-separate--nearer">{{ $t('common.weather.wind8c') }}</div>
            </div>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vant'

export default {
  components: {
    [ Swipe.name ]: Swipe,
    [ SwipeItem.name ]: SwipeItem
  },
  data () {
    return {
      weather: null,
      tabIndex: 0
    }
  },
  computed: {
    swiperHeight () {
      return this.tabHeight[this.tabIndex] || 0
    }
  },
  methods: {
    getWeather () {
      this.$http.post('/api/common/weather/detail').then(data => {
        this.weather = data
      })
    },
    switchTab (index) {
      this.tabIndex = index
    }
  },
  created () {
    this.getWeather()
    this.$timer = setInterval(this.getWeather, 60000)
  },
  beforeDestroy () {
    clearInterval(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.c-weather {
  padding: 40px 40px 42px;

  &__img {
    width: 134px;
    height: 134px;
  }

  &__value {
    font-size: 96px;
    color: $color--dark;
    line-height: 115px;
  }
}

.c-weather-tabs {
  &__title {
    padding: 40px 40px 24px;
  }

  &__content {
    padding: 48px 40px;
  }
}

.o-tab {
  padding-top: 35px;
  color: $color;
  border-bottom-width: 4px;

  &.is-selected {
    color: $color-blue;
    border-color: $color-blue;
  }
}

.o-weather-img {
  width: 100%;
  height: 368px;
  border-radius: 16px;
}
</style>
