    <div
      class="is-content--dark has-padding"
      :class="{ 'is-large': large }"
    >
      <div :class="{ 'is-ellipsis--multi': large, 'is-ellipsis': !large }">{{ entity.name }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    entity: {
      type: Object,
      default: null
    },
    large: {
      type: [Boolean, String],
      default: false
    }
  },
  methods: {
    toDetail () {
      this.$features.entity(this.entity.id)
    }
  }
}
</script>

