<template>
  <div
    class="l-page"
    :class="{ 'u-background--blue': isBinding }"
  >
    <v-nav
      :title="$t('ticket.bind.title')"
      custom
      :icon="navIcon"
      @back="toBack"
    />
    <template v-if="step === 1">
      <div class="l-page__content">
        <div class="o-ticket-demo">
          <img style="width: 100%;" src="@/assets/ticket.png"/>
        </div>
        <div class="c-bind-ticket">
          <div class="is-title--page">{{ $t('ticket.bind.code') }}</div>
          <div class="is-mini--dark u-font u-separate--far">{{ $t('ticket.bind.tip') }}</div>
          <field
            class="u-separate--farther"
            v-model="code"
            :title="$t('ticket.bind.tid')"
            :placeholder="$t('ticket.bind.tid')"
            maxlength="32"
            :error="error"
            @input="error = ''"
          >
            <van-icon
              class="c-bind-ticket__scan u-color--blue u-h-separate--near"
              name="scan"
              @click="toScan"
            />
          </field>
          <v-button
            class="u-separate--farther"
            :text="$t('btn.confirmupper')"
            :disable="disable"
            @click="toCheck"
          />
        </div>
      </div>
    </template>
    <template v-if="step === 2">
      <div class="l-page__content l-flex--column is-centered has-padding--around">
        <div class="c-result u-text-center">
          <div class="c-result__header">{{ $t('ticket.bind.confirm') }}</div>
          <div class="c-result__content">
            <div class="is-title--page">{{ ticket.name }}</div>
            <div class="is-content--orange u-separate--nearer">{{ ticket.valid }}</div>
            <div class="u-separate--far l-flex--row">
              <div
                class="o-button is-trigger is-hollow is-auto"
                @click="toBack"
              >
                {{ $t('btn.cancelupper') }}
              </div>
              <div
                class="o-button is-trigger is-auto"
                @click="toBind"
              >
                {{ $t('btn.confirmupper') }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <template v-if="step === 3">
      <div class="l-page__content l-flex--column is-centered has-padding--around">
        <div class="c-result has-status u-text-center">
          <div class="c-result__header">
            <div class="c-result__status"></div>
            {{ $t('ticket.bind.success') }}
          </div>
          <div class="c-result__content">
            <div class="is-title--page">{{ ticket.name }}</div>
            <div class="is-content--orange u-separate--nearer">{{ ticket.valid }}</div>
            <div class="u-separate--far">
              <div
                v-if="ticket.canEscheduler"
                class="c-vlist-item--near"
              >
                <router-link
                  tag="div"
                  class="o-button is-trigger is-auto"
                  :to="{ name: 'escheduler' }"
                  replace
                >
                  {{ $t('common.payment.result.escheduler') }}
                </router-link>
              </div>
              <div class="c-vlist-item--near">
                <div
                  class="o-button is-trigger is-hollow is-auto"
                  @click="toReset"
                >
                  {{ $t('ticket.bind.another') }}
                </div>
              </div>
              <div class="u-separate--far u-wrapper">
                <router-link
                  tag="div"
                  class="l-inline-flex"
                  :to="{ name: 'mine' }"
                  replace
                >
                  <div class="is-sub--blue">{{ $t('common.payment.result.view') }}</div>
                  <img class="o-icon" src="@/assets/icon-arrow--right--blue.png"/>
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="dialogMsg.title"
      :message="dialogMsg.content"
      :confirm-button-text="$t('btn.ok')"
    />
  </div>
</template>

<script>
import { transformTicketValidTime } from '@/utils/format.js'
import DialogMixin from '@/components/mixins/dialog.js'
import { Icon } from 'vant'
import VButton from '@/components/VButton.vue'
import Field from '@/components/list/Field.vue'

export default {
  mixins: [ DialogMixin ],
  components: {
    [ Icon.name ]: Icon,
    VButton,
    Field
  },
  data () {
    return {
      code: '',
      error: '',
      step: 1,
      ticket: null
    }
  },
  computed: {
    disable () {
      return this.code === '' || this.error !== ''
    },
    navIcon () {
      return this.step === 3 ? 'cross' : 'down'
    },
    isBinding () {
      return this.step > 1
    }
  },
  methods: {
    check (code) {
      return /^[0-9a-zA-Z]{1,32}$/.test(code)
    },
    toScan () {
      this.$features.scan().then(message => {
        if (this.check(message)) {
          this.code = message
          this.error = ''
        } else {
          this.dialogMsg.title = this.$t('ticket.bind.t1')
          this.dialogMsg.content = this.$t('ticket.bind.msg1')
          this.dialogMsg.show = true
        }
      })
    },
    toCheck () {
      if (this.check(this.code)) {
        this.$http.post('/api/common/user/ticket/checkMediaCode', {
          mediaCode: this.code
        }, {
          login: true,
          loading: true
        }).then(data => {
          switch (data.status) {
            case 0:
              this.ticket = {
                name: data.ticketName,
                valid: transformTicketValidTime(data)
              }
              this.$features.showModal(this.$t('ticket.bind.title'), `${ this.$ticketInfo.name }\n${ this.$ticketInfo.time }`, this.$t('btn.confirm'), this.bindTicket, null, this.resumeScan)
              break
            default:
              this.error = this.$t('ticket.bind.err2')
              break
          }
        }, err => {
          if (err.system) {
            if (!err.abort) {
              this.dialogMsg.title = this.$t('ticket.bind.t2')
              this.dialogMsg.content = this.$t('ticket.bind.msg2')
              this.dialogMsg.show = true
            }
          } else {
            this.error = this.$t('ticket.bind.err1')
          }
        })
      } else {
        this.error = this.$t('ticket.bind.err1')
      }
    },
    toBind () {
      this.$http.post('/api/common/user/ticket/bind', {
        mediaCode: this.code
      }, {
        login: true,
        server: true,
      }).then(data => {
        this.ticket = {
          name: data.ticketName,
          valid: transformTicketValidTime(data),
          canEscheduler: data.canEscheduler
        }
        this.step = 3
      })
    },
    toReset () {
      this.code = ''
      this.ticket = null
      this.step = 1
    },
    toBack () {
      if (this.step === 2) {
        this.step = 1
      } else {
        this.$features.back()
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.step !== 2 || this.$router.logout) {
      next()
    } else {
      this.step = 1
      next(false)
    }
  }
}
</script>

<style lang="scss" scoped>
.o-ticket-demo {
  margin: 70px auto 0;
  width: 718px;
}

.c-bind-ticket {
  padding: 90px 75px 40px;

  &__scan {
    font-size: 48px;
  }
}

.c-result {
  flex: none;
  position: relative;
  border-radius: 16px;
  background-color: $color-white;

  &.has-status {
    .c-result__header {
      padding: 124px 35px 48px;
    }

    .c-result__content {
      padding-bottom: 48px;
    }
  }

  &__header {
    position: relative;
    padding: 46px 35px 48px;

    &::after {
      @include hairline();
      border-bottom-width: 1px;
      border-style: dashed;
    }
  }

  &__status {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 50%;
    width: 160px;
    height: 160px;
    border: 10px solid $color-white;
    border-radius: 50%;
    background-color: $color-white;
    transform: translate(-50%, -40%);
    overflow: hidden;

    &::after {
      content: '';
      display: inline-block;
      width: 100%;
      height: 100%;
      background-image: url(~@/assets/state-success.png);
      @include bg-img-attrs;
    }
  }

  &__content {
    padding: 56px 35px;
  }
}
</style>
