import Vue from 'vue'

import i18n from '@/i18n'
import store from '@/store'
import router from '@/router'

import * as SystemEvent from '@/code/system-event.js'
import { log } from '@/utils/log.js'
import EventBus from '@/utils/event-bus.js'
import JsNativeApi from '@/utils/js-native-api.js'
import http, { clear } from '@/utils/http.js'
import { isExpired } from '@/utils/auth'
import { language, initMails, resetMails } from '@/utils/common.js'
import crypto from '@/utils/crypto.js'
import features from '@/utils/features.js'

import { Toast, Dialog, Tabs, Slider } from 'vant'
import NavBar from '@/components/NavBar.vue'

import Resource from '@/components/Resource.vue'

Vue.use(Toast)
Vue.use(Dialog)
Vue.component('VNav', NavBar)
Vue.component('VResource', Resource)

Vue.prototype.$log = log
Vue.prototype.$eventBus = EventBus
Vue.prototype.$http = http
Vue.prototype.$native = JsNativeApi
Vue.prototype.$crypto = crypto
Vue.prototype.$features = features

Dialog.setDefaultOptions({ className: 'c-dialog' })

Toast.allowMultiple()
Toast.setDefaultOptions({ duration: 3000 })
Toast.setDefaultOptions('loading', { forbidClick: true, duration: 0 })
Toast.setDefaultOptions('success', { forbidClick: true })

let upgrading = false
let upgradeForce = false
let upgradeLoading = null
EventBus.$on(SystemEvent.VERSION_UPGRADE_FORCE, () => {
  if (!upgradeForce) {
    Dialog.close()
    upgradeForce = true
    upgrading = true
    if (!upgradeLoading) {
      upgradeLoading = Toast.loading({
        className: 'u-highest',
        overlayClass: 'u-highest',
        message: i18n.t('upgrade.upgrading'),
        overlay: true
      })
    }
  }
})

EventBus.$on(SystemEvent.VERSION_UPGRADE, () => {
  if (upgradeForce) {
    upgradeLoading.clear()
    upgradeLoading = null
    Dialog.alert({
      className: 'c-dialog u-highest',
      overlayClass: 'u-highest',
      title: false,
      message: i18n.t('upgrade.tip'),
      confirmButtonText: i18n.t('btn.ok')
    }).then(() => JsNativeApi.shutdown())
  } else {
    upgrading = true
    Dialog.confirm({
      className: 'c-dialog u-highest',
      overlayClass: 'u-highest',
      title: false,
      message: i18n.t('upgrade.tip'),
      confirmButtonText: i18n.t('btn.confirm'),
      cancelButtonText: i18n.t('btn.cancel'),
    }).then(() => JsNativeApi.shutdown(), () => upgrading = false)
  }
})

EventBus.$on(SystemEvent.VERSION_UPGRADE_FAIL, code => {
  if (upgradeForce) {
    upgradeLoading.clear()
    upgradeLoading = null
    Dialog.alert({
      className: 'c-dialog u-highest',
      title: false,
      message: i18n.t(`upgrade.error.${ code }`),
      confirmButtonText: i18n.t('btn.ok')
    }).then(() => JsNativeApi.shutdown())
  }
})

let routerType = null

let routerPush = router.push
router.push = function (...args) {
  routerType = 'push'
  routerPush.call(this, ...args)
}

let routerReplace = router.replace
router.replace = function (...args) {
  routerType = 'replace'
  routerReplace.call(this, ...args)
}

let routerGo = router.go
router.go = function (...args) {
  routerType = 'go'
  routerGo.call(this, ...args)
}

let cachedRoute = null

function saveRoute (route) {
  if (route) {
    let { name, params = {}, query = {} } = route
    cachedRoute = { name, params, query, replace: true }
  } else {
    cachedRoute = null
  }
}

function popRoute () {
  let route = cachedRoute
  cachedRoute = null
  return route || {
    name: 'home',
    replace: true
  }
}

router.beforeEach((to, from, next) => {
  log(`${ routerType || 'web' } ${ from.name } -> ${ to.name }`)
  let currRouterType = routerType
  routerType = null

  switch (true) {
    case from.name !== null && upgrading:
    case from.meta.tabbar === true && !currRouterType:
    case !store.getters.isAuth || isExpired():
      next(false)
      return
    default:
      break
  }

  if (to.matched.length ===0) {
    routerType = 'replace'
    next({ name: 'home', replace: true })
    return
  }

  clear()

  if (to.meta.login === true && !store.getters.isLogined) {
    if (from.name === 'sign') {
      routerType = 'replace'
      next({ name: 'home', replace: true })
    } else {
      routerType = currRouterType
      saveRoute(to)
      next({ name: 'sign' })
    }
  } else if (to.meta.login === false && store.getters.isLogined) {
    routerType = currRouterType
    next(popRoute())
  } else {
    if (to.meta.tabbar) {
      saveRoute(null)
    } else if (from.meta.back && !currRouterType) {
      next(false)
      Vue.nextTick(() => router.go(from.meta.back))
      return
    }
    next()
  }
})

let source = null
function userInfo () {
  if (source && !store.getters.isLogined) {
    source.cancel('abort')
  } else if (!source && store.getters.isLogined) {
    source = http.createSource()
    http.post('/api/common/user/info', null, {
      globals: true,
      login: true,
      cancelToken: source.token
    }).then(data => {
      store.dispatch('userInfo', data)
    }, err => {
      if (!err.abort) {
        setTimeout(userInfo, 2000)
      }
    }).finally(() => source = null)
  }
}

EventBus.$on('login', () => {
  language()
  initMails()
  userInfo()
})

EventBus.$on('logout', () => {
  clear()
  resetMails()
  userInfo()
})

function isHidden (element) {
  return window.getComputedStyle(element).display === 'none' || element.offsetParent === null
}

Tabs.methods.setLine = function () {
  let shouldAnimate = this.inited

  this.$nextTick(function () {
    let titles = this.$refs.titles

    if (!titles || !titles[this.currentIndex] || this.type !== 'line' || isHidden(this.$el)) {
      return
    }

    let title = titles[this.currentIndex].$el
    let width = title.offsetWidth
    let left = title.offsetLeft
    let lineStyle = {
      width: width + 'px',
      backgroundColor: this.color,
      transform: 'translateX(' + left + 'px)'
    }

    if (shouldAnimate) {
      lineStyle.transitionDuration = this.duration + 's'
    }

    this.lineStyle = lineStyle
  })
}

Slider.methods.format = function (value) {
  if (this.value !== value) {
    let diff = Math.round((value - this.value) / this.step) * this.step
    value = this.value + diff
  }
  return Math.max(this.min, Math.min(value, this.max))
}
