<template>
  <list-page :title="$t('settings.account.title')">
    <cell-panel
      :placeholder="$t('settings.account.email.title')"
      :title="$t('settings.account.iemail')"
      :value="email"
      :disable="loginByEmail"
      @click="toAddEmail"
    />
    <cell-panel
      :placeholder="$t('settings.account.phone.title')"
      :title="$t('settings.account.iphone')"
      :value="phone"
      @click="toAddPhone"
    />
    <cell
      v-if="loginByEmail"
      :title="$t('settings.account.pwd.title')"
      arrow
      @click="toChangePassword"
    />
    <cell
      :title="$t('settings.account.signOut')"
      @click="dialogMsg.show = true"
    />
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="$t('settings.account.signOut')"
      :message="$t('settings.account.signOutTip')"
      :confirm-button-text="$t('btn.confirm')"
      :cancel-button-text="$t('btn.cancel')"
      show-cancel-button
      @confirm="clearCache"
    />
  </list-page>
</template>

<script>
import { mapGetters } from 'vuex'
import { logout } from '@/utils/auth.js'
import DialogMixin from '@/components/mixins/dialog.js'
import ListPage from '@/components/list/ListPage.vue'
import Cell from '@/components/list/Cell.vue'
import CellPanel from '@/components/list/CellPanel.vue'

export default {
  mixins: [ DialogMixin ],
  components: {
    ListPage,
    Cell,
    CellPanel
  },
  computed: {
    ...mapGetters(['loginByEmail', 'userInfo']),
    email () {
      return this.userInfo && this.userInfo.email || ''
    },
    phone () {
      if (this.userInfo) {
        let { areaCode = '', phone = '' } = this.userInfo
        if (areaCode && phone) {
          return `+${ areaCode }${ phone }`
        }
      }
      return ''
    }
  },
  methods: {
    toAddEmail () {
      this.$router.push({
        name: 'add-email'
      })
    },
    toAddPhone () {
      this.$router.push({
        name: 'verify-phone'
      })
    },
    toChangePassword () {
      this.$router.push({
        name: 'change-password'
      })
    },
    signOut () {
      this.$http.post('/api/common/user/logout', null, {
        globals: true,
        login: true
      })
      logout()
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
