<template>
  <div class="l-page">
    <v-nav
      :title="title"
      fixed
      alone
    />
    <div
      class="l-page__content o-photo u-background--black"
      :style="imageStyle"
    >
      <template v-if="isTakingPhoto">
        <input ref="fileUpload" type="file" accept="image/*" style="display: none;" @change="handleFile"/>
        <div class="c-tool">
          <van-icon class="c-tool__item" name="photo-o" @click="toChoose"/>
          <van-icon class="c-tool__item" name="upgrade" @click="toRecognize"/>
        </div>
      </template>
      <template v-else>
        <div v-if="animal" class="c-detail has-padding--block l-flex--column is-centered">
          <v-resource class="c-detail__img" :info="animal.picInfo"/>
          <div class="is-title--block u-separate--near">{{ animal.name }}</div>
          <div class="o-button is-trigger is-fixed--larger u-separate" @click="toDetail">{{ $t('ai.more') }}</div>
          <div class="is-sub u-separate--near">
            {{ $t('ai.unmatch') }}<span class="u-color--blue" @click="toFeedback">{{ $t('ai.feedback') }}</span>
          </div>
        </div>
        <div v-else class="c-failure">
          <div class="o-sorry">{{ $t('ai.sorry') }}</div>
          <div
            v-if="animals.length > 0"
            class="c-animals"
          >
            <div class="is-title has-padding--v has-hairline--b u-sticky u-align-center u-background--white">{{ $t('ai.maybe') }}</div>
            <div class="c-list--2c has-padding--around">
              <animal
                v-for="(item, index) in animals"
                :key="index"
                class="c-list--2c__item"
                :entity="item"
                large
              />
            </div>
          </div>
        </div>
      </template>
    </div>
    <div v-if="step > -1" class="c-loading is-fill l-flex--row is-centered">
      <div class="c-loading__item" :class="stepClass"></div>
    </div>
    <van-popup
      v-model="feedback.show"
      position="bottom"
      round
    >
      <div class="is-dflex--column">
        <div class="c-close-header has-hairline--b">
          {{ $t('common.feedback.title') }}
          <van-icon
            class="c-close-header__close"
            name="cross"
            @click="feedback.show = false"
          />
        </div>
        <div class="has-padding--h u-separate--far">
          <div class="is-tip--dark">{{ $t('ai.ask') }}</div>
          <div class="l-flex--row u-separate">
            <div class="is-tip--dark l-flex__fill">{{ $t('common.feedback.message') }}</div>
            <div class="is-tip u-font l-flex__none">{{ feedback.msg.length }}/200</div>
          </div>
          <field
            class="u-separate--nearer"
            v-model="feedback.msg"
            :placeholder="$t('common.feedback.placeholder')"
            maxlength="200"
          />
        </div>
        <div class="c-fixed-footer has-hairline--tb u-separate--nearer">
          <div class="o-button is-trigger is-hollow is-auto" @click="feedback.show = false">{{ $t('btn.cancelupper') }}</div>
          <div class="o-button is-trigger is-auto" @click="submit">{{ $t('btn.confirmupper') }}</div>
        </div>
      </div>
    </van-popup>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="$t('ai.thank')"
      :message="$t('ai.thankt')"
      :confirm-button-text="$t('btn.ok')"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import DialogMixin from '@/components/mixins/dialog.js'
import { Popup, Icon } from 'vant'
import Animal from '@/components/map/Animal.vue'
import Field from '@/components/list/Field.vue'

export default {
  mixins: [ DialogMixin ],
  components: {
    [ Popup.name ]: Popup,
    [ Icon.name ]: Icon,
    Animal,
    Field
  },
  data () {
    return {
      os: '',
      img: null,
      isTakingPhoto: true,
      step: -1,
      animal: null,
      animals: [],
      feedback: {
        show: false,
        success: false,
        msg: ''
      }
    }
  },
  computed: {
    ...mapGetters(['isLogined']),
    isAndroid () {
      return this.os === 'android'
    },
    isIos () {
      return this.os === 'ios'
    },
    title () {
      return this.isTakingPhoto ? this.$t('ai.what') : ''
    },
    imageStyle () {
      if (this.img && this.img.path) {
        return `backgroundImage: url("${ this.img.path }")`
      }
      return ''
    },
    stepClass () {
      return this.step >= 0 ? `is-step${ this.step }` : ''
    }
  },
  methods: {
    choose (file, type, path = '') {
      this.img = { file, type, path }
      if (type === 'file') {
        let reader = new FileReader()
        reader.onload = e => {
          this.img.path = e.target.result
        }
        reader.readAsDataURL(file)
      }
    },
    handleFile (e) {
      let target = e.target || e.srcElement
      let file = target.files[0]
      if (!file) {
        return
      }
      this.$refs.fileUpload.value = null
      this.choose(file, 'file')
    },
    toChoose () {
      if (this.isAndroid) {
        if (this.$refs.fileUpload) {
          this.$refs.fileUpload.click()
        }
      } else if (this.isIos) {
        this.$native.pickimage().then(result => {
          let file = result && result.image
          if (file) {
            let bytes = window.atob(file)
            let array = []
            for (let i = 0; i < bytes.length; i++) {
              array.push(bytes.charCodeAt(i))
            }
            this.choose(new Blob([new Uint8Array(array)], { type: 'image/jpeg' }), 'blob', `data:image/jpeg;base64,${ file }`)
          }
        })
      }
    },
    toRecognize () {
      if (this.img) {
        this.step = 0
        this.$timer = setInterval(() => this.step = (this.step + 1) % 3, 1000)
        this.$http.upload('/api/common/ai/animal/recognition', this.img).then(data => {
          this.isTakingPhoto = false
          this.animal = data
        }, err => {
          // -1010 未识别到动物
          if (err.code === -1010) {
            this.isTakingPhoto = false
            this.$http.post('/api/common/entity/list/animal').then(data => {
              this.animals = data.data
            })
          } else if (!err.system) {
            this.$toast(err.msg)
          }
        }).finally(() => {
          this.step = -1
          clearInterval(this.$timer)
        })
      }
    },
    toDetail () {
      if (this.animal && this.animal.entityId) {
        this.$features.entity(this.animal.entityId)
      }
    },
    toFeedback () {
      this.feedback.msg = ''
      this.feedback.show = true
    },
    submit () {
      this.$http.post('/api/common/ai/animal/feedback', {
        recognitionId: this.animal.id,
        remark: this.feedback
      }, {
        login: this.isLogined,
        loading: true,
        server: true
      }).then(() => {
        this.feedback.show = false
        this.dialogMsg.show = true
      })
    }
  },
  created () {
    this.$timer = -1
    this.$native.os().then(os => {
      this.os = os
    })
  },
  beforeDestroy () {
    clearInterval(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.o-photo {
  background-position: 50% 50%;
  background-size: contain;
  background-repeat: no-repeat;
}

.c-tool {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 100px;
  font-size: 72px;
  color: $color-white;
  text-align: center;

  &__item + &__item {
    margin-left: 128px;
  }
}

.c-loading {
  background-color: rgba($color-black, .65);
  z-index: 19;

  &__item {
    display: inline-block;
    width: 160px;
    height: 160px;
    @include bg-img-attrs;

    &.is-step0 {
      background-image: url(~@/assets/waiting-0.png);
    }

    &.is-step1 {
      background-image: url(~@/assets/waiting-1.png);
    }

    &.is-step2 {
      background-image: url(~@/assets/waiting-2.png);
    }
  }
}

.c-detail {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 16px 16px 0 0;
  background-color: $color-white;

  &__img {
    display: inline-block;
    width: 160px;
    height: 160px;
    border-radius: 50%;
    overflow: hidden;
  }
}

.c-failure {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 80px;
  left: 0;
  right: 0;
  bottom: 0;
  margin-top: $save-header;
  overflow-y: auto;
}

.o-sorry {
  flex: none;
  padding: 13px 0 15px;
  margin: 40vh 40px 32px;
  font-size: 26px;
  color: $color-white;
  line-height: 36px;
  text-align: center;
  border-radius: 16px;
  background-color: rgba($color-black, .5);
}

.c-animals {
  flex: 1 0 0;
  border-radius: 16px 16px 0 0;
  background-color: $color-white;
}
</style>
