<template>
  <div class="l-page">
    <v-nav :title="$t('common.payment.title')"/>
    <div class="l-page__content has-padding--around">
      <div class="is-tip--dark">{{ $t('common.payment.tip') }}</div>
      <div
        v-for="type in types"
        :key="type.value"
        class="o-payment has-padding--around has-shadow u-separate l-flex--row is-centered"
        :class="{ 'is-selected': payMethod === type.value }"
        @click="payMethod = type.value"
      >
        <div class="o-payment__icon l-flex__none">
          <v-resource
            class="o-payment__payment"
            :info="type.picInfo"
            custom
          />
        </div>
        <div class="is-content--dark u-font u-h-separate--farthest l-flex__fill">{{ type.name }}</div>
        <van-icon
          class="o-payment__selected u-color--blue u-h-separate--near"
          name="checked"
        />
      </div>
    </div>
    <div class="c-fixed-footer has-hairline--tb">
      <v-button
        class="l-flex__fill"
        :text="$t('btn.confirmupper')"
        :disable="disable"
        small
        @click="toPay"
      />
    </div>
    <div
      v-if="paying"
      class="c-processing is-fill l-flex--column is-centered"
    >
      <van-loading class="u-color--white" size="36"/>
      <div class="is-tip u-color--white u-font--bold u-separate--near">{{ $t('common.payment.processing') }}</div>
    </div>
  </div>
</template>

<script>
import { Icon, Loading } from 'vant'
import VButton from '@/components/VButton.vue'

export default {
  components: {
    [ Icon.name ]: Icon,
    [ Loading.name ]: Loading,
    VButton
  },
  data () {
    return {
      selectedIndex: -1,
      types: [],
      payMethod: '',
      paying: false
    }
  },
  computed: {
    disable () {
      return this.payMethod === ''
    },
  },
  methods: {
    toPay () {
      this.$payInfo = `${ this.$payInfo }&payMethod=${ this.payMethod }`
      if (this.$payUrl) {
        this.paying = true
        window.location.replace(`${ this.$payUrl }?${ this.$payInfo }`)
      }
    }
  },
  created () {
    this.$http.post('/api/app/order/preAsiaPay', {
      orderId: this.$route.params.id
    }, {
      login: true,
      server: true
    }).then(data => {
      if (data.orderInfo && data.payInfo) {
        this.types = data.payInfo && data.payInfo.payMethodList || []
        let { orderId, payPrice } = data.orderInfo
        let { payUrl, merchantId, currCode, successUrl, failUrl, cancelUrl, secureHash, mpsModeList, payTypeList, payMethodList = [] } = data.payInfo
        let lang
        switch (this.$i18n.locale) {
          case 'tc':
            lang = 'C'
            break
          case 'sc':
            lang = 'X'
            break
          default:
            lang = 'E'
            break
        }
        let mpsMode = mpsModeList && mpsModeList[0] && mpsModeList[0].value
        let payType = payTypeList && payTypeList[0] && payTypeList[0].value
        this.types = payMethodList
        this.$payInfo = `merchantId=${ merchantId }&amount=${ payPrice }&orderRef=${ orderId }&currCode=${ currCode }&mpsMode=${ mpsMode }&successUrl=${ encodeURIComponent(successUrl) }&failUrl=${ encodeURIComponent(failUrl) }&cancelUrl=${ encodeURIComponent(cancelUrl) }$secureHash=${ secureHash }&payType=${ payType }&lang=${ lang }`
        this.$payUrl = payUrl
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.o-payment {
  position: relative;
  border-radius: 16px;

  &.is-selected {
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 4px solid $color-blue;
      border-radius: 16px;
    }

    .o-payment__selected {
      visibility: visible;
    }
  }

  &__icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 186px;
  }

  &__payment {
    max-width: 50%;
  }

  &__selected {
    visibility: hidden;
  }
}

.c-processing {
  font-size: 60px;
  background-color: rgba($color-black, .67);
  z-index: 99;
}
</style>
