<template>
  <div
    class="c-collect-ticket l-page u-background--blue"
  >
    <v-nav
      :title="$t('ticket.collect.title')"
      custom
      :icon="navIcon"
      @back="toBack"
    />
    <template v-if="step === 1">
      <div class="l-page__content l-flex--column is-centered has-padding--around">
        <div class="c-result u-text-center">
          <div class="c-result__header">{{ $t('ticket.collect.tip') }}</div>
          <div class="c-result__content">
            <div class="is-title--page">{{ ticket.name }}</div>
            <div class="is-content--orange u-separate--nearer">{{ ticket.valid }}</div>
            <div class="u-separate--far l-flex--row">
              <div
                class="o-button is-trigger is-hollow is-auto"
                @click="dialogMsg.show = true"
              >
                {{ $t('btn.cancelupper') }}
              </div>
              <div
                class="o-button is-trigger is-auto"
                @click="toReslove(2)"
              >
                {{ $t('btn.confirmupper') }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <template v-if="step === 2">
      <div class="l-page__content l-flex--column is-centered has-padding--around">
        <div class="c-result has-status u-text-center">
          <div class="c-result__header">
            <div class="c-result__status">
              <i
                class="c-result__avatar"
                :style="avatar"
              ></i>
            </div>
            {{ $t('ticket.collect.success') }}
          </div>
          <div class="c-result__content">
            <div class="is-title--page">{{ ticket.name }}</div>
            <div class="is-content--orange u-separate--nearer">{{ ticket.valid }}</div>
            <div class="u-separate--far">
              <div class="c-vlist-item--near">
                <router-link
                  tag="div"
                  class="o-button is-trigger is-auto"
                  :to="{ name: 'escheduler' }"
                  replace
                >
                  {{ $t('common.payment.result.escheduler') }}
                </router-link>
              </div>
              <div class="u-separate--far u-wrapper">
                <router-link
                  tag="div"
                  class="l-inline-flex"
                  :to="{ name: 'mine' }"
                  replace
                >
                  <div class="is-sub--blue">{{ $t('common.payment.result.view') }}</div>
                  <img class="o-icon" src="@/assets/icon-arrow--right--blue.png"/>
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <template v-if="step === 3">
      <div class="l-page__content l-flex--column is-centered has-padding--around">
        <div class="c-result u-text-center">
          <div
            v-if="message"
            class="c-result__header"
          >
            {{ message }}
          </div>
          <div class="c-result__content">
            <div class="is-title--page">{{ ticket.name }}</div>
            <div class="is-content--orange u-separate--nearer">{{ ticket.valid }}</div>
          </div>
        </div>
      </div>
    </template>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="$t('ticket.collect.askt')"
      :message="$t('ticket.collect.ask')"
      :confirm-button-text="$t('btn.confirm')"
      :cancel-button-text="$t('btn.cancel')"
      show-cancel-button
      @confirm="toReslove(3)"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { transformTicketValidTime } from '@/utils/format.js'
import DialogMixin from '@/components/mixins/dialog.js'
import { Icon } from 'vant'

export default {
  mixins: [ DialogMixin ],
  components: {
    [ Icon.name ]: Icon
  },
  data () {
    return {
      step: 0,
      message: '',
      ticket: null
    }
  },
  computed: {
    ...mapGetters(['avatarUrl']),
    navIcon () {
      return this.step > 1 ? 'cross' : 'down'
    },
    avatar () {
      return this.avatarUrl ? { backgroundImage: `url("${ this.avatarUrl }")` } : null
    }
  },
  methods: {
    toReslove (status) {
      this.$http.post('/api/common/user/ticket/share/process', {
        shareId: this.$id,
        status
      }, {
        login: true,
        loading: true,
        server: true
      }).then(() => {
        if (status === 2) {
          this.step = 2
        } else {
          this.toBack()
        }
      })
    },
    toBack () {
      this.$features.tabbar('home')
    }
  },
  created () {
    this.$id = Number(this.$route.params.id)
    this.$http.post('/api/common/user/ticket/share/detail', {
      shareId: this.$route.params.id
    }, {
      login: true
    }).then(data => {
      if (data.canAccept && !data.isAccept) {
        this.ticket = {
          name: data.ticketName,
          valid: transformTicketValidTime(data)
        }
        this.step = 1
      } else {
        this.step = 3
        this.message = data.isAccept ? data.isSelfAccept ? this.$t('ticket.collect.accepts') : this.$t('ticket.collect.accepto') : ''
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.c-result {
  flex: none;
  position: relative;
  border-radius: 16px;
  background-color: $color-white;

  &.has-status {
    .c-result__header {
      padding: 124px 35px 48px;
    }

    .c-result__content {
      padding-bottom: 48px;
    }
  }

  &__header {
    position: relative;
    padding: 46px 35px 48px;

    &::after {
      @include hairline();
      border-bottom-width: 1px;
      border-style: dashed;
    }
  }

  &__status {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 50%;
    width: 160px;
    height: 160px;
    border: 10px solid $color-white;
    border-radius: 50%;
    background-color: $color-white;
    transform: translate(-50%, -40%);
    overflow: hidden;
  }

  &__avatar {
    display: inline-block;
    width: 100%;
    height: 100%;
    @include bg-img-attrs;
  }

  &__content {
    padding: 56px 35px;
  }
}

.c-collect-ticket {
  /deep/ .van-dialog.c-dialog {
    .van-dialog__header, .van-dialog__message {
      color: $color-black;
    }
  }
}
</style>
