<template>
  <div class="l-page">
    <v-nav
      :title="$t('explore.filter.title')"
      icon="cross"
    >
      <span class="is-content u-color--white" @click="toReset">{{ $t('explore.filter.clear') }}</span>
    </v-nav>
    <div class="l-page__content l-flex--column">
      <van-tabs
        class="l-flex__fill c-nav-tabs c-nav-tabs--explore"
        type='none'
        v-model="tabIndex"
        :ellipsis="false"
        swipeable
        animated
        @change="getFilters"
      >
        <van-tab
          v-for="(tab, index) in tabs"
          :key="index"
        >
          <template #title>
            <div
              class="c-nav-tabs--explore__item"
              :class="{ 'is-selected': tabIndex === index }"
            >
              <div
                v-if="tabIndex === index"
                class="c-nav-tabs--explore__icon"
                :style="{
                  'background-image': tab.info.picInfo.selectedCosUrl ? `url(${ tab.info.picInfo.selectedCosUrl })` : `url(${ baseUrl}${ tab.info.picInfo.selectedUrl })`
                }"
              ></div>
              <div
                v-else
                class="c-nav-tabs--explore__icon"
                :style="{
                  'background-image': tab.info.picInfo.unselectedCosUrl ? `url(${ tab.info.picInfo.unselectedCosUrl })` : `url(${ baseUrl}${ tab.info.picInfo.unselectedUrl })`
                }"
              ></div>
              <div class="c-nav-tabs--explore__name" :class="{ 'u-color--blue': tabIndex === index, 'u-font--bold': tabIndex === index }">{{ tab.info.name }}</div>
            </div>
          </template>
          <div
            v-if="selectedFilterInfo.status === 1"
            class="has-padding--around u-align-center"
          >
            <van-loading type="spinner"/>
          </div>
          <div
            v-if="selectedFilterInfo.status === -1"
            class="is-tip--dark has-padding--around"
          >
            {{ $t('common.empty') }}
          </div>
          <template v-if="selectedFilterInfo.status === 2">
            <div v-for="(block, index) in selectedBlocks" :key="index" class="has-padding--around has-separate">
              <div v-for="(item, oindex) in block.list" :key="oindex" class="c-vlist-item--farther">
                <template v-if="item.valueType === 'multiSelect'">
                  <div class="l-flex--row">
                    <div class="is-tip--dark l-flex__fill">{{ item.name }}</div>
                    <div
                      v-if="item.canToggle"
                      class="o-toggle l-flex__none"
                      @click="toggleBlock(item)"
                    >
                      {{ item.toggle ? $t('explore.filter.less') : $t('explore.filter.more') }}
                      <i class="o-toggle__icon" :class="{ 'is-toggle': item.toggle }"></i>
                    </div>
                  </div>
                  <div class="c-options u-separate--gap">
                    <div v-for="option in item.pre" :key="option.id" class="c-options__item o-option" :class="{ 'is-selected': option.selected }" @click="selectChange(option)">{{ option.name }}</div>
                    <template v-if="item.toggle">
                      <div v-for="option in item.next" :key="option.id" class="c-options__item o-option" :class="{ 'is-selected': option.selected }" @click="selectChange(option)">{{ option.name }}</div>
                    </template>
                  </div>
                </template>
                <template v-if="item.valueType === 'range'">
                  <div class="l-flex--row">
                    <div class="is-tip--dark l-flex__fill">{{ item.name }}</div>
                    <div class="is-tip--blue u-font--bold l-flex__nonw">{{ item.value }}</div>
                    <div class="is-tip--blue u-font--bold l-flex__nonw">{{ item.unit }}</div>
                  </div>
                  <van-slider
                    class="u-separate"
                    v-model="item.value"
                    :min="item.min"
                    :max="item.max"
                    :step="item.step"
                    active-color="#00A9E0"
                    inactive-color="#DDDDDD"
                  />
                </template>
                <div v-if="item.valueType === 'checkBox'" class="is-tip--dark u-relative">
                  {{ item.name }}
                  <van-switch
                    class="is-fixed-r"
                    :data-key="item.id"
                    v-model="item.value"
                    active-color="#00A9E0"
                    inactive-color="#DDDDDD"
                  />
                </div>
              </div>
            </div>
          </template>
        </van-tab>
      </van-tabs>
    </div>
    <div class="c-fixed-footer has-hairline--tb">
      <div class="o-button is-trigger is-auto" @click="toBack">{{ $t('explore.filter.check') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { Tabs, Tab, Loading, Slider, Switch } from 'vant'
import { sortByIndex } from '@/utils/sort.js'

export default {
  components: {
    [ Tabs.name ]: Tabs,
    [ Tab.name ]: Tab,
    [ Loading.name ]: Loading,
    [ Slider.name ]: Slider,
    [ Switch.name ]: Switch
  },
  data () {
    return {
      blocks: [],
      filterInfo: null,
      scrollInto: '',
      tabIndex: 0
    }
  },
  computed: {
    ...mapState(['entityTypes']),
    ...mapGetters(['baseUrl']),
    tabs () {
      return this.entityTypes.list
    },
    selectedTab () {
      return this.tabs.length > 0 ? this.tabs[this.tabIndex] : null
    },
    selectedFilterInfo () {
      return this.selectedTab ? this.filterInfo[this.tabIndex] : null
    },
    selectedFilters () {
      return this.selectedFilterInfo && this.selectedFilterInfo.list || []
    },
    selectedBlocks () {
      if (this.selectedFilters.length > 0) {
        let blocks = []
        let type = ''
        let list
        this.selectedFilters.forEach(item => {
          if (type !== item.valueType) {
            list = []
            type = item.valueType
            blocks.push({
              type, list
            })
          }
          list.push(item)
        })
        return blocks
      }
      return []
    }
  },
  methods: {
    isValid (item) {
      switch (item.valueType) {
        case 'multiSelect':
          return item.value !== void 0 && item.value.length > 0
        case 'range':
          return typeof item.defaultValue === 'number'
        case 'checkBox':
          return typeof item.defaultValue === 'boolean'
        default:
          return false
      }
    },
    transform (item) {
      let value
      switch (item.valueType) {
        case 'multiSelect':
          value = (item.value || []).map(info => {
            return {
              id: info.id,
              name: info.name,
              selected: false
            }
          })
          return {
            id: item.id,
            valueType: item.valueType,
            name: item.name,
            canToggle: value.length > 8,
            toggle: false,
            value: value,
            pre: value.slice(0, 8),
            next: value.slice(8)
          }
        case 'range':
        case 'checkBox':
          item.value = item.defaultValue
          break
        default:
          break
      }
      return item
    },
    initFilters (filterList, tab) {
      if (tab.filter.length > 0) {
        filterList.forEach(filter => {
          let temp = tab.filter.find(item => item.id === filter.id)
          if (temp) {
            switch (filter.valueType) {
              case 'multiSelect':
                filter.value.forEach(block => {
                  if (temp.value.findIndex(id => id === block.id) >= 0) {
                    block.selected = true
                  }
                })
                break
              case 'range':
              case 'checkBox':
                filter.value = temp.value
                break
              default:
                break
            }
          }
        })
      }

      return filterList
    },
    getFilters () {
      let info = this.selectedFilterInfo
      if (info) {
        if (info.status === 0) {
          info.status = 1
          let tab = this.selectedTab
          this.$http.post('/api/common/entity/sort/detail', {
            sortId: tab.sortId
          }).then(data => {
            let filterList = (data.filterList || []).filter(item => this.isValid(item)).sort(sortByIndex).map(this.transform)
            this.initFilters(filterList, tab)
            info.list = filterList
            info.status = filterList.length > 0 ? 2 : -1
          }, () => info.status = 0)
        } else if (info.status === 2) {
          this.initFilters(this.selectedFilters, this.selectedTab)
        }
      }
    },
    toggleBlock (item) {
      item.toggle = !item.toggle
    },
    selectChange (option) {
      option.selected = !option.selected
    },
    filter (item) {
      switch (item.valueType) {
        case 'multiSelect':
          return item.value.findIndex(option => option.selected) >= 0
        case 'range':
        case 'checkBox':
          return item.value !== item.defaultValue
        default:
          return true
      }
    },
    transformFilter (item) {
      switch (item.valueType) {
        case 'multiSelect':
          return {
            id: item.id,
            value: item.value.filter(option => option.selected).map(option => option.id)
          }
        case 'range':
          return {
            id: item.id,
            value: item.value
          }
        case 'checkBox':
          return {
            id: item.id,
            value: item.value
          }
        default:
          break
      }
      return item
    },
    toReset () {
      this.selectedFilters.map(item => {
        switch (item.valueType) {
          case 'multiSelect':
            item.value.forEach(option => option.selected = false)
            break
          case 'range':
          case 'checkBox':
            item.value = item.defaultValue
            break
          default:
            break
        }
      })
    },
    toBack () {
      let tab = this.selectedTab
      if (tab) {
        this.entityTypes.index = this.tabIndex
        tab.filter = this.selectedFilters.filter(item => this.filter(item)).map(this.transformFilter)
        tab.loading = false
        tab.timestamp = 0
        tab.total = 0
        tab.list = []
      }
      this.$features.tabbar('map')
    }
  },
  created () {
    if (this.entityTypes && this.entityTypes.list.length > 0) {
      this.tabIndex = this.entityTypes.index
      this.filterInfo = this.entityTypes.list.map(() => {
        return {
          status: 0,
          list: null
        }
      })
      this.getFilters()
    }
  }
}
</script>

<style lang="scss" scoped>
.c-nav-tabs {
  /deep/ .van-tabs__wrap {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0 , .1);
  }
}
</style>
