<template>
  <div class="l-page">
    <div class="l-page__header--primary">
      <div class="l-page__title">{{ $t('tabbar.map') }}</div>
      <div class="l-flex__none l-flex--row">
        <img class="o-nav-icon" src="@/assets/icon-scan.png"/>
        <!-- <router-link
          tag="i"
          class="o-nav-icon"
          :to="{ name: 'settings' }">
          <van-icon name="setting-o" />
        </router-link> -->
      </div>
    </div>
    <div class="l-page__content l-flex--column">
      <div class="c-header has-padding--h has-hairline--b">
        <div class="c-header__wrapper" :style="{ transform: `translateY(${ translateY })` }">
          <div class="c-header__item">
            <div class="c-header__left">
              <div>{{ date }}</div>
              <div class="o-sp"></div>
              <div class="u-font">{{ openTime }}</div>
            </div>
            <div
              v-if="weather"
              class="c-header__right"
              @click="toWeather"
            >
              <v-resource class="c-header__icon" :info="weather" icon/>
              <div class="u-font--bold u-h-separate--list">{{ weather.temperature }}°C</div>
            </div>
          </div>
          <div
            v-for="(item, index) in scrolls"
            :key="index"
            class="c-header__item"
          >
            <div class="c-header__left">
              <template v-if="item.leftInfo">
                <v-resource class="c-header__icon" :info="item.leftInfo.icon"/>
                <div v-if="item.leftInfo.text" class="u-font--bold u-h-separate--list">{{ item.leftInfo.text }}</div>
              </template>
            </div>
            <div v-if="item.rightInfo" class="c-header__right">{{ item.rightInfo }}</div>
          </div>
        </div>
      </div>
      <div class="l-flex--column l-flex__fill u-relative">
        <div class="o-filter" :class="{ 'is-filter': isFilter }" @click="toFilter"></div>
        <van-tabs
          class="l-flex__fill c-nav-tabs c-nav-tabs--explore"
          type='none'
          v-model="tabIndex"
          :ellipsis="false"
          swipeable
          animated
          @change="switchTab"
        >
          <van-tab
            v-for="(tab, index) in tabs"
            :key="index"
          >
            <template #title>
              <div
                class="c-nav-tabs--explore__item"
                :class="{ 'is-selected': tabIndex === index }"
              >
                <div
                  v-if="tabIndex === index"
                  class="c-nav-tabs--explore__icon"
                  :style="{
                    'background-image': tab.info.picInfo.selectedCosUrl ? `url(${ tab.info.picInfo.selectedCosUrl })` : `url(${ baseUrl}${ tab.info.picInfo.selectedUrl })`
                  }"
                ></div>
                <div
                  v-else
                  class="c-nav-tabs--explore__icon"
                  :style="{
                    'background-image': tab.info.picInfo.unselectedCosUrl ? `url(${ tab.info.picInfo.unselectedCosUrl })` : `url(${ baseUrl}${ tab.info.picInfo.unselectedUrl })`
                  }"
                ></div>
                <div class="c-nav-tabs--explore__name" :class="{ 'u-color--blue': tabIndex === index, 'u-font--bold': tabIndex === index }">{{ tab.info.name }}</div>
              </div>
            </template>
            <div :ref="tab.id" class="has-padding--around">
              <div
                v-if="tab.loading && tab.total === 0"
                class="u-align-center u-separate--farther"
              >
                <van-loading type="spinner"/>
              </div>
              <div
                v-if="!tab.loading && tab.total === 0 && tab.timestamp !== 0"
                class="is-tip--dark"
              >
                {{ $t('common.empty') }}
              </div>
              <div
                v-if="tab.total > 0"
                class="c-list"
                :class="{ 'has-filter': tab.filter.length > 0 }"
              >
                <div
                  v-for="(item, index) in tab.list"
                  :key="index"
                  class="c-list__entity"
                  :class="{ 'is-animal': item.typeId === 3 }"
                >
                  <animal v-if="item.typeId === 3" :entity="item"/>
                  <facilities v-if="item.typeId === 8" :entity="item"/>
                  <activity v-if="item.typeId !== 3 && item.typeId !== 8" :entity="item" :now="now"/>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
    <van-popup
      v-if="close2animal.showList"
      v-model="close2animal.show"
      position="bottom"
      round
      @closed="clearClose2Animal"
    >
      <div class="c-close-header has-hairline--b">
        {{ $t('explore.detail.close2animals') }}
        <van-icon
          class="c-close-header__close"
          name="cross"
          @click="closeClose2Animal"
        />
      </div>
      <div class="c-animal-list">
        <v-animal
          v-for="(item, index) in close2animal.list"
          :key="index"
          class="c-vlist-item--far"
          :entity="item"
        />
      </div>
    </van-popup>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import { dateFormat, transformTime } from '@/utils/format.js'
import { sortBySortIndex } from '@/utils/sort.js'
import LocaleMixin from '@/components/mixins/locale.js'
import { Icon, Loading, Tab, Tabs, Popup } from 'vant'
import VAnimal from '@/components/map/VAnimal.vue'
import Animal from '@/components/map/Animal.vue'
import Activity from '@/components/map/Activity.vue'
import Facilities from '@/components/map/Facilities.vue'

export default {
  mixins: [ LocaleMixin ],
  components: {
    [ Icon.name ]: Icon,
    [ Loading.name ]: Loading,
    [ Tabs.name ]: Tabs,
    [ Tab.name ]: Tab,
    [ Popup.name ]: Popup,
    VAnimal,
    Animal,
    Activity,
    Facilities
  },
  data () {
    return {
      close2animal: {
        show: false,
        showList: false,
        list: null
      },
      loading: false,
      now: Date.now(),
      tabIndex: 0,
      translateY: '0',
      date: '',
      openTime: '',
      weather: null,
      scrolls: [],
      duration: 0,
      scrollIndex: 0
    }
  },
  computed: {
    ...mapState(['entityTypes']),
    ...mapGetters(['baseUrl']),
    totalScroll () {
      let total = this.scrolls.length
      if (this.date !== '' || this.weather !== null) {
        total += 1
      }
      return total
    },
    tabs () {
      return this.entityTypes.list
    },
    isFilter () {
      let tab = this.tabs[this.tabIndex]
      if (tab) {
        return tab.filter.length > 0
      }
      return false
    }
  },
  methods: {
    ...mapActions(['saveEntityTypes']),
    toWeather () {
      this.$router.push({ name: 'weather' })
    },
    toFilter () {
      this.$router.push({ name: 'filter' })
    },
    startScroll () {
      clearTimeout(this.$timer)
      this.translateY = this.scrollIndex > 0 ? `-${ this.scrollIndex }00%` : '0'
      if (this.duration > 0 && this.totalScroll > 1) {
        this.$timer = setTimeout(() => {
          this.scrollIndex = (this.scrollIndex + 1) % this.totalScroll
          this.startScroll()
        }, this.duration)
      }
    },
    checkScroll () {
      if (this.scrollIndex >= this.totalScroll) {
        this.scrollIndex = 0
      }
      this.startScroll()
    },
    getWeather () {
      this.$http.post('/api/common/weather/detail').then(data => {
        this.weather = {
          icon: data.picInfo.url,
          iconCos: data.picInfo.cosUrl,
          temperature: data.temperature
        }
        this.checkScroll()
      })
    },
    refreshHeader () {
      this.$http.post('/api/common/park/detail').then(data => {
        if (data.parkOperatingHourList.length > 0) {
          let obj = data.parkOperatingHourList[0]
          this.date = dateFormat(obj.parkOpenTime, 'yyyy.MM.dd (w)')
          this.openTime = `${ transformTime(obj.parkOpenTime) }-${ transformTime(obj.parkCloseTime) }`
        }
        this.checkScroll()
      })

      this.getWeather()

      this.$http.post('/api/common/information/list').then(data => {
        this.scrolls = data.data.map(item => {
          let { leftInfo, rightInfo } = item
          if (leftInfo) {
            let { icon, text } = leftInfo
            if (icon || text && text.value) {
              leftInfo = {
                icon,
                text: text && text.value || ''
              }
            } else {
              leftInfo = null
            }
          }

          if (rightInfo) {
            rightInfo = rightInfo.text && rightInfo.text.value || ''
          }

          if (leftInfo || rightInfo) {
            return { leftInfo, rightInfo }
          }

          return null
        }).filter(item => item !== null)
        this.duration = data.duration * 1000
        this.checkScroll()
      })

      this.startScroll()
    },
    getTabs () {
      if (this.loading) {
        return
      }
      this.loading = true
      this.$http.post('/api/common/entity/sort/list').then(data => {
        if (this.entityTypes.timestamp !== data.updateTime) {
          let tabs = (data.data || []).sort(sortBySortIndex).map(item => {
            return {
              id: `tab_${ item.id }_${ item.typeId }`,
              sortId: item.id,
              typeId: item.typeId,
              info: item,
              loading: false,
              timestamp: 0,
              filter: [],
              total: 0,
              list: []
            }
          })
          this.tabIndex = Math.max(0, Math.min(this.tabIndex, tabs.length - 1))
          this.saveEntityTypes({
            list: tabs,
            timestamp: data.updateTime,
            index: this.tabIndex
          })
          this.switchTab(this.tabIndex)
        }
      }).finally(() => this.loading = false)
    },
    getData () {
      let tab = this.tabs[this.tabIndex]
      if (!tab.loading) {
        tab.loading = true
        this.$http.post('/api/common/entity/list', {
          sortId: tab.sortId,
          filterList: tab.filter
        }).then(data => {
          if (tab.timestamp !== data.updateTime) {
            tab.timestamp = data.updateTime
            let list = data.data || []
            tab.total = list.length
            tab.list = list.sort(sortBySortIndex)
          }
        }).finally(() => tab.loading = false)
      }
    },
    toReset () {
      let tab = this.tabs[this.tabIndex]
      if (!tab.loading) {
        tab.timestamp = 0
        tab.filter = []
        tab.total = 0
        tab.list = []
        this.getData()
      }
    },
    switchTab (index) {
      this.tabIndex = index
      let tab = this.tabs[this.tabIndex]
      if (tab) {
        this.getData()
      }
    },
    showClose2Animal (list) {
      this.close2animal.list = list
      this.close2animal.showList = true
      this.$nextTick(() => this.close2animal.show = true)
    },
    clearClose2Animal () {
      this.close2animal.showList = false
      this.close2animal.show = false
      this.close2animal.list = null
    },
    closeClose2Animal () {
      this.close2animal.show = false
    },
    refreshContent () {
      this.now = Date.now()
      this.getWeather()
      this.switchTab(this.tabIndex)
    },
    onLocaleUpdated () {
      this.tabs = []
      this.tabIndex = 0
      this.saveEntityTypes({
        timestamp: 0,
        index: 0,
        list: []
      })
    }
  },
  created () {
    this.$timer = -1
    this.$rtimer = -1
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  },
  activated () {
    this.$eventBus.$on('close2animal', this.showClose2Animal)

    if (this.tabs.length > 0) {
      this.tabIndex = this.entityTypes.index
      this.tabs.forEach(tab => {
        if (tab.scrollTop) {
          let dom = this.$refs[tab.tabId]
          if (dom) {
            dom[0].parentNode.parentNode.scrollTop = tab.scrollTop
          }
          tab.scrollTop = 0
        }
      })
    }

    this.refreshHeader()
    this.getTabs()
    this.refreshContent()
    this.$rtimer = setInterval(this.refreshContent, 60000)
  },
  deactivated () {
    this.tabs.forEach(tab => {
      let dom = this.$refs[tab.id]
      if (dom) {
        tab.scrollTop = dom[0].parentNode.parentNode.scrollTop
      }
    })
    this.$eventBus.$off('close2animal', this.showClose2Animal)
    clearTimeout(this.$timer)
    clearInterval(this.$rtimer)
    this.entityTypes.index = this.tabIndex
    this.clearClose2Animal()
  }
}
</script>

<style lang="scss" scoped>
.c-header {
  position: relative;
  height: 96px;
  overflow: hidden;

  &__wrapper {
    height: 100%;
    transition: transform .2s;
    overflow: visible;
  }

  &__item {
    display: flex;
    align-items: center;
    height: 96px;
    font-size: 28px;
    color: $color--dark;
    line-height: 32px;

    & > * {
      flex: none;
    }
  }

  &__left {
    flex: 1;
    display: flex;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
  }

  &__right {
    flex: none;
    display: flex;
    align-items: center;
    padding-left: 12px;
  }

  &__icon {
    width: 40px;
    height: 40px;
  }
}

.c-nav-tabs {
  /deep/ .van-tabs__wrap::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 254px;
    background: url(~@/assets/tabs.png) 0 0 / 100% 100% no-repeat;
  }
}

.o-filter {
  position: absolute;
  top: 180px;
  left: 50%;
  width: 58px;
  height: 44px;
  transform: translateX(-50%);
  background-image: url(~@/assets/icon-filters-normal.png);
  z-index: 99;
  @include bg-img-attrs;

  &.is-filter {
    background-image: url(~@/assets/icon-filters.png);
  }
}

.c-list {
  display: grid;
  grid-template-columns: 315px 315px;
  grid-row-gap: 40px;
  justify-content: space-between;

  &__entity {
    grid-column-start: span 2;

    &.is-animal {
      grid-column-start: span 1;
    }
  }
}

.c-animal-list {
  box-sizing: border-box;
  max-height: 584px;
  padding: 40px 40px 24px;
  overflow-y: auto;
}
</style>
