<template>
  <div class="l-page">
    <v-nav
      :title="title"
      :scroll="scrollTop"
      fixed
    />
    <template v-if="hasTicket">
      <div
        class="l-page__content"
        @scroll="scroll"
      >
        <banner :list="bannerPicList"/>
        <div class="has-padding--block has-hairline--b">
          <div class="is-title--page">{{ title }}</div>
          <intro
            v-if="hasContent"
            class="u-separate--near"
            :text="ticket.content"
          />
          <div
            v-if="animalFeeding"
            class="l-flex--row is-content u-separate--near"
          >
            <img class="o-img" src="@/assets/icon-feed.png"/>
            <div class="is-tip--dark u-font u-h-separate">{{ $t('ticket.detail.feeding') }}</div>
          </div>
          <div
            v-if="!isClose2Animal && hasPrice"
            class="is-title--page--orange u-font u-separate--near"
          >
            {{ priceInfo.price }}
          </div>
        </div>
        <div
          v-if="isClose2Animal && hasPrice"
          class="has-padding--around has-hairline--b"
        >
          <div class="is-title--page--orange u-font">{{ priceInfo.price }}</div>
          <div
            v-if="priceInfo.remark"
            class="is-tip--dark u-font u-separate--nearest"
          >
            {{ priceInfo.remark }}
          </div>
        </div>
        <div
          v-if="hasRaList"
          class="has-padding--v has-hairline--b"
        >
          <div class="is-tip--dark has-padding--h">{{ raList.title }}</div>
          <div class="l-hlist u-separate--near">
            <div class="l-hlist__wrapper">
              <div
                v-for="(entity, index) in raList.list"
                :key="index"
                class="c-hlist-item o-exhibit"
                @click="toEntity(entity)"
              >
                <v-resource class="o-exhibit__resource" :info="entity.picInfo"/>
                <div class="o-exhibit__name">{{ entity.name }}</div>
              </div>
            </div>
          </div>
        </div>
        <div
          v-if="hasTicketingPointList"
          class="has-padding--around has-hairline--b"
        >
          <div class="is-tip--dark">{{ $t('ticket.detail.points') }}</div>
          <div class="u-separate--nearer">
            <div
              v-for="(desc, index) in ticketingPointList"
              :key="index"
              class="c-vlist-item--nearest is-content--dark u-font"
            >
              {{ desc }}
            </div>
          </div>
        </div>
        <div
          v-if="hasRequiredRemarkInfo"
          class="has-padding--v has-hairline--b"
        >
          <div class="has-padding--h is-tip--dark">{{ requiredRemark.title }}</div>
          <div class="u-separate--near">
            <div
              v-if="hasRequiredRemarkPicList"
              class="c-vlist-item l-hlist"
            >
              <div class="l-hlist__wrapper">
                <v-resource
                  v-for="(item, index) in requiredRemarkPicList"
                  :key="index"
                  class="c-hlist-item--near o-img"
                  :info="item"
                />
              </div>
            </div>
            <div
              v-if="hasRequiredRemarkTextList"
              class="c-vlist-item has-padding--h"
            >
              <div
                v-for="(desc, index) in requiredRemarkTextList"
                :key="index"
                class="c-vlist-item--nearest is-content--dark u-font"
              >
                {{ desc }}
              </div>
            </div>
          </div>
        </div>
        <div
          v-if="hasMeetingTimeAndPlace"
          class="has-padding--around has-hairline--b"
        >
          <div class="is-tip--dark">{{ meetingTimeAndPlace.title }}</div>
          <div class="u-separate--near">
            <row
              v-if="meetingTimeAndPlace.time"
              class="c-vlist-item--nearest"
              icon="time"
              primary
            >
              <div class="is-content--dark">{{ meetingTimeAndPlace.time }}</div>
            </row>
            <row
              v-if="meetingTimeAndPlace.place"
              class="c-vlist-item--nearest"
              icon="location--primary"
              primary
            >
              <div class="is-content--blue u-font--bold">{{ meetingTimeAndPlace.place }}</div>
            </row>
          </div>
        </div>
        <toggle
          v-if="hasTermsAndConditions"
          class="has-padding--around has-hairline--t"
          :title="tcOrRemark"
          :fixed="!isClose2Animal"
        >
          <div class="u-separate--nearer">
            <div
              v-for="(desc, index) in termsAndConditions"
              :key="index"
              class="c-vlist-item--nearest is-content--dark u-font l-flex--row is-top"
            >
              <div class="l-flex__none">•</div>
              <div class="l-flex__fill u-h-separate--nearest" v-html="desc"></div>
            </div>
          </div>
        </toggle>
      </div>
      <div class="c-fixed-footer has-hairline--tb">
        <div class="o-button is-trigger is-auto" @click="toBuy">{{ btnMsg }}</div>
      </div>
    </template>
  </div>
</template>

<script>
import Banner from '@/components/Banner.vue'
import Intro from '@/components/Intro.vue'
import Row from '@/components/RowIcon.vue'
import Toggle from '@/components/Toggle.vue'

export default {
  components: {
    Banner,
    Intro,
    Row,
    Toggle
  },
  data () {
    return {
      ticket: null,
      scrollTop: 0
    }
  },
  computed: {
    hasTicket () {
      return this.ticket !== null
    },
    title () {
      return this.hasTicket && this.ticket.name || ''
    },
    isClose2Animal () {
      return this.hasTicket && this.ticket.typeId === 5
    },
    tcOrRemark () {
      return this.isClose2Animal ? this.$t('ticket.detail.remark') : this.$t('ticket.detail.tc')
    },
    btnMsg () {
      switch (true) {
        case this.isClose2Animal:
          return this.$t('ticket.detail.c2a')
        case this.hasTicket && this.ticket.typeId === 4:
          return this.$t('ticket.detail.oft')
        default:
          return this.$t('btn.buyticket')
      }
    },
    bannerPicList () {
      return this.hasTicket && this.ticket.bannerPicList || []
    },
    hasContent () {
      return this.hasTicket && this.ticket.content || false
    },
    animalFeeding () {
      return this.hasTicket && this.ticket.animalFeeding === true
    },
    priceInfo () {
      if (this.hasTicket && this.ticket.price && this.ticket.price.price) {
        return this.ticket.price
      }
      return null
    },
    hasPrice () {
      return this.priceInfo !== null
    },
    raList () {
      if (this.hasTicket && this.ticket.raList && this.ticket.raList.list && this.ticket.raList.list.length > 0) {
        return this.ticket.raList
      }
      return null
    },
    hasRaList () {
      return this.raList !== null
    },
    ticketingPointList () {
      if (this.hasTicket && this.ticket.ticketingPointList) {
        let ticketingPointList = this.ticket.ticketingPointList
        let arr = this.normalize(ticketingPointList.list)
        if (ticketingPointList.remark) {
          arr.push(`*${ ticketingPointList.remark }`)
        }
        return arr
      }
      return []
    },
    hasTicketingPointList () {
      return this.ticketingPointList.length > 0
    },
    requiredRemark () {
      return this.hasTicket && this.ticket.requiredRemark || null
    },
    hasRequiredRemark () {
      return this.requiredRemark !== null
    },
    requiredRemarkPicList () {
      return this.hasRequiredRemark && this.requiredRemark.picList || []
    },
    hasRequiredRemarkPicList () {
      return this.requiredRemarkPicList.length > 0
    },
    requiredRemarkTextList () {
      return this.hasRequiredRemark ? this.normalize(this.requiredRemark.textList) : []
    },
    hasRequiredRemarkTextList () {
      return this.requiredRemarkTextList.length > 0
    },
    hasRequiredRemarkInfo () {
      return this.hasRequiredRemarkPicList || this.hasRequiredRemarkTextList
    },
    meetingTimeAndPlace () {
      if (this.hasTicket && this.ticket.meetingTimeAndPlace) {
        let meetingTimeAndPlace = this.ticket.meetingTimeAndPlace
        if (meetingTimeAndPlace.list && meetingTimeAndPlace.list.length > 0) {
          let { time = [], place = '' } = meetingTimeAndPlace.list[0]
          time = time.length > 0 ? time.join(' ') : ''
          if (time || place) {
            return {
              title: meetingTimeAndPlace.title,
              time,
              place
            }
          }
        }
      }
      return null
    },
    hasMeetingTimeAndPlace () {
      return this.meetingTimeAndPlace !== null
    },
    termsAndConditions () {
      return this.hasTicket ? this.filter(this.ticket.termsAndConditions) : []
    },
    hasTermsAndConditions () {
      return this.termsAndConditions.length > 0
    }
  },
  methods: {
    scroll (e) {
      this.scrollTop = e.currentTarget.scrollTop
    },
    filter (list) {
      return list ? list.filter(item => !!item) : []
    },
    normalize (list) {
      return this.filter(list).map(item => `• ${ item }`)
    },
    toEntity (entity) {
      this.$features.entity(entity.entityId)
    },
    toBuy () {
      if (this.$route.name === 'ticket-detail') {
        this.$features.back()
      } else {
        this.$features.toBuyTicket(this.ticket.typeId)
      }
    }
  },
  created () {
    this.$http.post('/api/common/ticket/detail', {
      ticketId: this.$route.params.id
    }, {
      login: true,
      server: true
    }).then(data => {
      this.ticket = data
    })
  }
}
</script>

<style lang="scss" scoped>
.o-img {
  display: inline-block;
  width: 72px;
  height: 72px;
}
</style>
