<template>
  <div
    class="c-escheduler-ticket u-text-center"
    :class="{ 'is-choosed': ticket.choose }"
  >
    <van-icon
      class="c-escheduler-ticket__info"
      name="info-o"
      @click.stop="showInfo"
    />
    <div class="c-escheduler-ticket__text is-content--dark is-ellipsis">{{ ticket.origin.name }}</div>
    <div class="c-escheduler-ticket__text is-mini u-separate--gap">{{ ticket.msg }}</div>
    <van-icon
      class="c-escheduler-ticket__icon"
      name="success"
    />
  </div>
</template>

<script>
import { Icon } from 'vant'

export default {
  components: {
    [ Icon.name ]: Icon
  },
  props: {
    ticket: {
      type: Object,
      default: null
    }
  },
  methods: {
    showInfo () {
      this.$emit('info', this.ticket.origin)
    }
  }
}
</script>

<style lang="scss" scoped>
.c-escheduler-ticket {
  position: relative;
  padding: 50px 16px;
  border-radius: 16px;
  border: 4px solid $color-separate;

  &.is-choosed {
    border-color: $color-blue;
    background-color: $color-blue--light;

    .c-escheduler-ticket__text {
      color: $color-blue;
    }

    .c-escheduler-ticket__icon {
      display: inline-block;
    }
  }

  &__info {
    position: absolute;
    top: 12px;
    right: 12px;
    font-size: 36px;
    color: $color;
  }

  &__icon {
    display: none;
    position: absolute;
    bottom: -2px;
    right: -2px;
    padding: 10px;
    font-size: 24px;
    color: $color-white;
    border-radius: 12px 0 16px 0;
    background-color: $color-blue;
  }
}
</style>
