<template>
  <div class="c-toggle has-padding--large" :class="{ 'is-toggle': isOpen }">
    <div class="l-flex--row" @click="toggle = !toggle">
      <div class="l-flex__auto">
        <div class="is-tip--dark">{{ title }}</div>
        <div v-if="note" class="is-tip u-font u-separate--gap">{{ note }}</div>
      </div>
      <van-icon
        v-if="!fixed"
        class="c-require-block__icon c-toggle__icon"
        name="arrow-down"
      />
    </div>
    <div v-if="isOpen">
      <slot/>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant'

export default {
  components: {
    [ Icon.name ]: Icon
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    note: {
      type: String,
      default: ''
    },
    fixed: {
      type: [Boolean, String],
      default: false
    }
  },
  data () {
    return {
      toggle: false
    }
  },
  computed: {
    isOpen () {
      return this.fixed || this.toggle
    }
  }
}
</script>

<style lang="scss" scoped>
.c-toggle {
  &.is-toggle {
    padding-bottom: 40px;

    .c-toggle__icon {
      transform: rotate(-180deg);
    }
  }

  &__icon {
    font-size: 32px;
    color: $color;
    transition: transform .3s;
  }
}
</style>
