<template>
  <div id="app">
    <transition :name="transition">
      <keep-alive>
        <router-view v-if="$route.meta.cache" />
      </keep-alive>
    </transition>
    <transition :name="transition">
      <router-view v-if="!$route.meta.cache" />
    </transition>
  </div>
</template>

<script>
import { Loading } from 'vant'

export default {
  components: {
    [ Loading.name ]: Loading
  },
  data () {
    return {
      transition: 'slide-r2l',
      isForceUpgrade: false
    }
  },
  computed: {
    key () {
      return this.$route.fullPath
    }
  },
  methods: {
    checkDirection (to) {
      if (this.$back || to.meta.tabbar) {
        this.transition = 'slide-l2r'
      } else {
        this.transition = 'slide-r2l'
      }
    },
    back (depth) {
      this.$back = true
      this.$router.go(depth)
    }
  },
  beforeMount () {
    let element = document.getElementById('loading')
    if (element) {
      document.body.removeChild(element)
    }
  },
  mounted () {
    this.$router.beforeEach((to, from, next) => {
      this.checkDirection(to)
      this.$nextTick(next)
      this.$back = false
    })

    this.$eventBus.$on('back', this.back)

    this.$native.updateH5({
      token: this.$store.state.authInfo.token
    })
  }
}
</script>

<style lang="scss">
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  @include u-font--black;
}
</style>
