<template>
  <div class="has-hairline--b">
    <div class="has-padding--block">
      <div class="is-title--page">{{ entity.name }}</div>
      <row
        v-if="location"
        class="u-separate--nearer"
        icon="location--primary"
        primary
      >
        <div class="is-content--blue u-font--bold">{{ location }}</div>
      </row>
      <div
        v-if="entity.taglineTitle"
        class="is-title u-separate--nearer"
      >
        {{ entity.taglineTitle }}
      </div>
      <intro
        v-if="entity.animalFun"
        class="u-separate--near"
        :text="entity.animalFun"
      />
      <intro
        v-if="hasIntrodction"
        class="u-separate--near"
        :text="entity.introduce"
      />
      <row
        v-if="hasOFT"
        class="u-separate--near"
        icon="fastrack"
        large
        dark
      >
        {{ $t('explore.detail.oft') }}
      </row>
    </div>
    <div
      v-if="hasProtection"
      class="has-padding--around has-hairline--t"
    >
      <div class="is-tip--dark">{{ $t('explore.detail.protection') }}</div>
      <intro
        class="u-separate--near"
        :text="entity.animalProtection"
      />
    </div>
  </div>
</template>

<script>
import Row from '@/components/RowIcon.vue'
import Intro from '@/components/Intro.vue'

export default {
  components: {
    Row,
    Intro
  },
  props: {
    entity: {
      type: Object,
      default () {
        return null
      }
    }
  },
  computed: {
    location () {
      return this.entity.location
    },
    hasProtection () {
      return this.entity.animalProtection !== void 0 && this.entity.animalProtection !== ''
    },
    hasIntrodction () {
      return this.entity.introduce !== void 0 && this.entity.introduce !== ''
    },
    hasOFT () {
      return this.entity.relateList && this.entity.relateList.findIndex(item => item.type === 'oft') >= 0
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
