export default {
  computed: {
    isBaseRoute () {
      return this.$route.name === this.route
    }
  },
  methods: {
    refresh () {},
    clear () {}
  },
  created () {
    if (this.isBaseRoute) {
      this.refresh()
      this.$base = true
    } else {
      this.$base = false
    }
  },
  beforeRouteUpdate (to, from, next) {
    if (to.name === this.route) {
      if (to.name === from.name) {
        this.clear()
        this.$nextTick(() => {
          this.refresh()
        })
      } else if (!this.$base) {
        this.$base = true
        this.$nextTick(() => {
          this.refresh()
        })
      }
    }
    next()
  }
}
